import claimprize from '../api/claimPrize';

const state = {
  claimBtnDisabled: false
};

const actions = {
  ClaimPrizeOnEgm({
    state,
    commit,
    rootGetters,
    rootState
  }, ticketId) {
    claimprize.ClaimPrizeOnEgm(rootState.CardID, rootState.EgmLocation, ticketId, rootGetters.guid).then((data) => {
      rootState.isFetching = false;
      if (data.ErrorCode != 0) {
        rootGetters.rootAnalytics.trackException("Error", {Error:data});
        dispatch("addInfo", {
          message: data.ErrorMessage,
          errorCode: data.ErrorCode
        }, {
          root: true
        });
        commit("setClaimBtnDisabled", false);
      }
    }).catch((err) => {
      rootGetters.rootAnalytics.trackException("Error", {Error:err.message});
      dispatch("addInfo", {
        'message': err.message
      }, {
        root: true
      });
      commit("setClaimBtnDisabled", false);
    });
  }
};

const mutations = {
  setClaimBtnDisabled(state, data) {
    state.claimBtnDisabled = data;
  }
};

export default {
  'namespaced': true,
  state,
  actions,
  mutations
};