import {genModel, declareModel} from "m5-content-model"

function modelConstructor(el, modules) {
    var store = {
        state: {},
        mutations: {},
        getters: {},
        actions: {},
        modules: {}
    }
    // create model
    const model = genModel(store, modules, el, arguments);
    // when model is ready, please call start
    model.start();

    return model;
}

export default declareModel('com.igt.banned', modelConstructor)