import { genModel, declareModel } from "m5-content-model";

function modelConstructor(el, modules) {

  var store = {
    state: {},
    mutations: {},
    getters: {},
    actions: {
      buttonPressed() {
        this.analytics.willTriggerBusiness("attendantButtonPressed");
        IGTMediaElements.buttonPress('OK');
      }
    },
    modules: {}
  }

  // create model
  const model = genModel(store, modules, el);

  // when model is ready, please call start
  model.start();
  return model;
}

export default declareModel("com.igt.attendant_welcome_screen", modelConstructor);
