import { getURLParameterByName } from "../supporting/utils"

Date.prototype.toCustomTimeString = function(){
    var h = this.getHours();
    var m = this.getMinutes();
    var s = this.getSeconds();
    var suffix = h > 12 ? "PM": "AM";
    h = h % 12;
    m = `00${m}`.substr(-2, 2);
    s = `00${s}`.substr(-2, 2);
    return `${h}:${m}:${s} ${suffix}`
}

var serialNumber = 0;
var ignoreAnalytics = !(Number(getURLParameterByName("analytics")) == 1);
if (!('toJSON' in Error.prototype)) {
    Object.defineProperty(Error.prototype, 'toJSON', {
        value: function () {
            var alt = {};

            Object.getOwnPropertyNames(this).forEach(function (key) {
                alt[key] = this[key];
            }, this);

            return alt;
        },
        configurable: true,
        writable: true
    });

}


function safeGuard(userInfo){
    if (typeof userInfo == "object") {
        var str = JSON.stringify(userInfo)
        if (str == undefined) {
            userInfo == undefined
        } else {
            userInfo = JSON.parse(str)
        }
    }
    return userInfo;
}

export default {
    apis: {
        trackView(screenName, userInfo) {
            this.$store.dispatch("trackView", { target: screenName, userInfo });
        },
        trackPress(target, userInfo) {
            this.$store.dispatch("trackPress", { target, userInfo })
        },
        trackNavigate(target, userInfo) {
            this.$store.dispatch("trackNavigate", { target, userInfo })
        },
        willTriggerBusiness(target, userInfo) {
            serialNumber++
            var startTime = (new Date())
            this.$store.dispatch("willTriggerBusiness", { target, startTime, serialNumber, userInfo });
            return (function (userInfo) {
                var endTime = (new Date())
                var timeCost = endTime - startTime;

                this.$store.dispatch("didTriggerBusiness", { target, startTime, endTime, serialNumber, timeCost, userInfo });
            }).bind(this)
        },
        trackException(target, userInfo) {
            var isFatal = false;
            this.$store.dispatch("trackException", { target, isFatal, userInfo });
        },
        trackFatalException(target, userInfo) {
            var isFatal = true;
            this.$store.dispatch("trackException", { target, isFatal, userInfo });
        }
    },

    getters: {
        isAnalyticsReady(state) {
            return state.floorLocation != "" && parseInt(state.cardId) != 0 && state.machineNumber != "";
        }
    },
    actions: {
        trackView(ctx, payload) {
            var viewName = payload ? payload.target : null;
            var userInfo = safeGuard(payload ? payload.userInfo : null);
            
            viewName = viewName ? ctx.getters.screenName + " - " + viewName : ctx.getters.screenName;
            var finalPayload = [
                { application: ctx.state.namespace },
                { location: ctx.state.floorLocation },
                { machineNumber: ctx.state.machineNumber },
                { cardId: ctx.state.cardId }
            ]
            if (userInfo) {
                finalPayload.push({ userInfo })
            }
            if (ignoreAnalytics) return;
            IGTMediaElements.analytics.trackView(viewName, document.location.href, finalPayload);
        },
        trackPress(ctx, payload) {
            var target = payload ? payload.target : "unkown target";
            var userInfo = safeGuard(payload ? payload.userInfo : null);
            var finalPayload = [
                { application: ctx.state.namespace },
                { location: ctx.state.floorLocation },
                { machineNumber: ctx.state.machineNumber },
                { cardId: ctx.state.cardId }
            ]
            if (userInfo) {
                finalPayload.push({ userInfo })
            }
            if (ignoreAnalytics) return;
            IGTMediaElements.analytics.trackEvent(target, "clickEvent", finalPayload);
        },
        trackNavigate(ctx, payload) {

            var target = payload ? payload.target : "unkown";
            var userInfo = safeGuard(payload ? payload.userInfo : null);
            var finalPayload = [
                { application: ctx.state.namespace },
                { location: ctx.state.floorLocation },
                { machineNumber: ctx.state.machineNumber },
                { cardId: ctx.state.cardId }
            ]
            if (userInfo) {
                finalPayload.push({ userInfo })
            }
            if (ignoreAnalytics) return;
            IGTMediaElements.analytics.trackEvent(target, "navigation", finalPayload);
        },
        trackException(ctx, payload) {

            var target = payload ? payload.target : "UnkownException";
            var isFatal = payload ? payload.isFatal : false;
            var userInfo = safeGuard(payload ? payload.userInfo : null);
            var finalPayload = [
                { application: ctx.state.namespace },
                { location: ctx.state.floorLocation },
                { machineNumber: ctx.state.machineNumber },
                { cardId: ctx.state.cardId }
            ]
            if (userInfo) {
                finalPayload.push({ userInfo })
            }
            var errorType = isFatal ? "Critical Error: " : "Error: ";
            var errorFormat = isFatal ? "!" : "?"
            trace(`${errorType} ${target}${errorFormat}\n${JSON.stringify(userInfo)}`);
            if (ignoreAnalytics) return;
            IGTMediaElements.analytics.trackException(target, isFatal, finalPayload);
        },
        willTriggerBusiness(ctx, payload) {

            var target = payload ? payload.target : "unkown";
            var userInfo = safeGuard(payload ? payload.userInfo : null);
            var finalPayload = [
                { application: ctx.state.namespace },
                { location: ctx.state.floorLocation },
                { machineNumber: ctx.state.machineNumber },
                { cardId: ctx.state.cardId },
                // { startTime: payload.startTime},
                { serialNumber: payload.serialNumber }
            ]
            if (userInfo) {
                finalPayload.push({ userInfo })
            }
            var startTime = payload.startTime.toCustomTimeString();
            trace(`Start ${target}\nstart=${startTime};`)
            if (ignoreAnalytics) return;
            IGTMediaElements.analytics.trackEvent(target, "triggerBusiness", finalPayload);

        },
        didTriggerBusiness(ctx, payload) {

            var target = payload ? payload.target : "unkown";
            var userInfo = safeGuard(payload ? payload.userInfo : null);
            var beginTime = payload.startTime.toISOString();
            var endTime = payload.endTime.toISOString();
            var timeCost = payload.timeCost;
            var finalPayload = [
                { application: ctx.state.namespace },
                { location: ctx.state.floorLocation },
                { machineNumber: ctx.state.machineNumber },
                { cardId: ctx.state.cardId },
                { beginTime: beginTime },
                { endTime: endTime },
                { timeCost: timeCost },
                { serialNumber: payload.serialNumber }
            ]
            var timeCostFormat = "";
            if (timeCost < 1000) {
                timeCostFormat = "."
            } else if (timeCost < 3000) {
                timeCostFormat = " "
            } else if (timeCost < 5000) {
                timeCostFormat = "?"
            } else {
                timeCostFormat = "!"
            }
            if (userInfo) {
                finalPayload.push({ userInfo })
                trace(`Stop ${target}\ntime=${timeCost}${timeCostFormat}\nuserInfo=${JSON.stringify(userInfo)};`)
            } else {
                trace(`Stop ${target}\ntime=${timeCost}${timeCostFormat}`)
            }
            if (ignoreAnalytics) return;
            IGTMediaElements.analytics.trackEvent(target, "getBusinessResponse", finalPayload);
        }
    }



}