import {
  genModel,
  declareModel,
  mapToken,
  retry
} from "m5-content-model"

function modelConstructor(el, modules) {
  var store = {
    state: {
      isAttendantCardInserted: false,
      amount: ''
    },
    actions: {
      buttonPressed() {
        this.analytics.willTriggerBusiness("attendantButtonPressed");
        IGTMediaElements.buttonPress('OK');
      }
    },
    modules: {}
  }
  const model = genModel(store, modules, el);
  mapToken('0x25', 'amount');


  model.watch("cardId", function (n, o) {
    trace("new CardID: ", n, "| old CardID: ", o);
    if (n != '0') {
      var trackerIsAtten = model.analytics.willTriggerBusiness("isAttendantCardInsertedStart");
      var checkAttendant = retry(
        function () {
          return new Promise((resolve, reject) => {
            IGTMediaElements.attendant.isAttendantCardInserted().then(function (enabledResponse) {
              trackerIsAtten("isAttendantCardInsertedEnd", enabledResponse);
              trace("Response", JSON.stringify(enabledResponse))
              if (enabledResponse.attendantInserted === true) {
                resolve(true);
              } else {
                reject(false);
              }

            });
          })
        }, Infinity, 1500);
        
      checkAttendant.then(inserted => {
          model.state.isAttendantCardInserted = inserted;
        })
        .catch(error => {
          console.log(`retry 3 times, not a attendant: ${error}!`)
          trace(`retry 3 times, not a attendant: ${error}!`)
        });
    } else {
      model.state.isAttendantCardInserted = false;
    }
  })

  model.start()
  return model;
}

export default declareModel('com.igt.jackpot_screen', modelConstructor)
