import ResizeObserver from 'resize-observer-polyfill';

if (!("ResizeObserver" in global)) {
    global.ResizeObserver = ResizeObserver;
}

try{
    (new Date()).toLocaleTimeString()
}catch(e){
    Date.prototype.toLocaleTimeString = function(){
        var h = this.getHours();
        var m = this.getMinutes();
        var s = this.getSeconds();
        var suffix = h > 12 ? "PM": "AM";
        h = h % 12;
        m = `00${m}`.substr(-2, 2);
        s = `00${s}`.substr(-2, 2);
        return `${h}:${m}:${s} ${suffix}`
	}
	Date.prototype.toLocaleString = function(){
		var months = ['unknown', 'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
		var h = this.getHours();
        var m = this.getMinutes();
		var s = this.getSeconds();
		var suffix = h > 12 ? "PM": "AM";
        h = h % 12;
        m = `00${m}`.substr(-2, 2);
        s = `00${s}`.substr(-2, 2);

		var mth = this.getMonth() <=11 ? this.getMonth() + 1 : 0;
		var dt = this.getDate();
		var yr = this.getFullYear();
		
		return `${months[mth]} ${dt} ${yr} ${h}:${m}:${s} ${suffix}`;
	}
}

(function() {
    // Get a handle on the global object
	var local;
	if (typeof global !== 'undefined') local = global;
	else if (typeof window !== 'undefined' && window.document) local = window;
	else local = self;

	// It's replaced unconditionally to preserve the expected behavior
    // in programs even if there's ever a native finally.
	local.Promise.prototype['finally'] = function finallyPolyfill(callback) {
		var constructor = this.constructor;

		return this.then(function(value) {
				return constructor.resolve(callback()).then(function() {
					return value;
				});
			}, function(reason) {
				return constructor.resolve(callback()).then(function() {
					throw reason;
				});
			});
	};
}());