import cm, {
  getURLParameterByName
} from 'm5-content-model'

import {
  mockData
} from './summary_mock-data.js'

import * as util from './model.util.js'

function modelConstructor (el, modules, beforeGen) {
  if (IGTMediaElements.isMock) {
    IGTMediaElements = Object.assign({}, IGTMediaElements, {
      tokenDic: {
        ...IGTMediaElements.tokenDic,
        "0x17": "10010"
      }
    })
  }

  let currentPromotionIndex = 0
  const thresholdMap = {}

  var store = {
    state: {
      demo: getURLParameterByName('demo'),
      serverMockData: getURLParameterByName('serverMockData'),
      serverIP: getURLParameterByName('serverIP'),
      protocal: getURLParameterByName('protocal'),
      domain: getURLParameterByName('domain'),

      // playerName: "", //"Player",
      // machineNumber: '',

      uniqueId: '', // Unique Id (of BE2)
      cardId: '', // "12350265000619667505",
      asset: '',
      egmLocation: '', // "25001",
      playerId: '',
      egmId () {
        const pad = `0000000000${this.uniqueId}`
        return `IGT_${pad.substr(-10)}`
      }, // todo getters
      promotionIds: [],
      promotions: [],
      currentPromotion: {},
      currentTheme: '',
      RR_config: {},
      errorMessage: '',
      randomRichesAvailable: false,
      highestProgressPromotion: {},
      status: {
        isWSConfigCorrect: false,
        xcAvailable: false,
        ptpAvailable: false,
        isWebSocketConnected: false,
        isIsmUpdateEnabled: false,
        isDuplicateCard: false
      },
      currentThresholdId: 0,
      currentThreshold: 0,
      allTiersHit: false
    },
    getters: {
      calculatedPercentage (state, getters) {
        if (state.currentPromotion && state.currentPromotion.pointBalance) {
          const ctid = thresholdMap[state.currentPromotion.promoId]
          const pb = ctid > 0 ? (state.currentPromotion.pointBalance - state.currentPromotion.thresholds[ctid - 1]) : state.currentPromotion.pointBalance
          const deltaTier = ctid > 0 ? (state.currentPromotion.thresholds[ctid] - state.currentPromotion.thresholds[ctid - 1]) : state.currentPromotion.thresholds[ctid]
          return Math.floor(pb / deltaTier * 100)
        } else {
          return 0
        }
      },
      highestPromotionProgress (state) {
        let highestProgress = 0
        for (let i = 0; i < state.promotions.length; i++) {
          const p = state.promotions[i]
          const lastThreshold = p.thresholds[p.thresholds.length - 1]
          const progress = parseInt(p.pointBalance) / lastThreshold
          if (progress >= 1) {
            continue
          }

          if (progress > highestProgress) {
            highestProgress = progress
            state.highestProgressPromotion = p
          }
        }
        return highestProgress
      },
      getPromotionByPromoId (state) {
        return promotionId => state.promotions.find(p => p.promoId == promotionId) // have to use == since one is string the other is number
      },
      promotionTheme (state) {
        IGTMediaElements.util.log.debug(`rr-summary-model promotionTheme ${JSON.stringify(state.RR_config.themePromotionMap)}`)

        return promotionName => {
          const result = state.RR_config.themePromotionMap.find((t) => {
            return t.promotionName === promotionName
          })
          return result ? result.theme : state.RR_config.themeList[0]
        }
      }
    },
    mutations: {
      UPDATE_PLAYER_PROGRESS (state, {
        p,
        progress
      }) {
        p.pointBalance = progress

        let tId = thresholdMap[p.promoId]
        let allHit = true
        for (let i = tId; i < p.thresholds.length; i++) {
          if (p.pointBalance < parseInt(p.thresholds[i])) {
            tId = i
            allHit = false
            break
          }
        }

        if (allHit) {
          // console.log(`hit all tiers for promotion ${p.promoId}`)
          if (p.promoId == state.currentPromotion.promoId) { // have to use == since one is string the other is number
            state.allTiersHit = true
          }
        } else {
          thresholdMap[p.promoId] = tId

          if (p.promoId == state.currentPromotion.promoId) { // have to use == since one is string the other is number
            state.currentThreshold = p.thresholds[tId]
            state.currentThresholdId = tId
          }
          // console.log(`thresholdMap after, ${p.promoId}, ${tId}`, thresholdMap)
          // this._vm.$set(state.thresholdMap, p.promoId, tId);
        }
      }
    },
    actions: {
      changePromotion: function (context, promoId) {
        const promotion = context.getters.getPromotionByPromoId(context.state.promotionIds[currentPromotionIndex])
        context.state.currentPromotion = promotion
        const tId = thresholdMap[promotion.promoId]
        context.state.currentThreshold = promotion.thresholds[tId]
        context.state.currentThresholdId = tId
        context.state.allTiersHit = promotion.pointBalance >= promotion.thresholds[promotion.thresholds.length - 1]
      },
      nextPromotion: function (context) {
        currentPromotionIndex += 1
        currentPromotionIndex %= context.state.promotions.length
        context.dispatch('changePromotion', currentPromotionIndex)
      },
      previousPromotion: function (context) {
        currentPromotionIndex -= 1
        if (currentPromotionIndex < 0) {
          currentPromotionIndex += context.state.promotions.length
        }
        context.dispatch('changePromotion', currentPromotionIndex)
      },
      betSimulator: function (context) {
        if (process.env.NODE_ENV === 'development' || context.state.demo) {
          const result = {}
          const randomPP = parseInt(Math.random(10) * 100, 10)

          context.state.promotions.forEach((p) => {
            if (parseInt(p.pointBalance) < parseInt(p.thresholds[p.thresholds.length - 1])) {
              result[p.promoId] = `${parseInt(p.pointBalance) + randomPP},${context.state.playerId}`
            } else {
              result[p.promoId] = `${parseInt(p.pointBalance)},${context.state.playerId}`
            }
          })

          IGTMediaElements.contentCache
            .setStateElement('rr-progressMeter', result, 0)
            .then(() => {})
        }
      }
    }
  }

  const model = cm.genModel(store, modules, el, beforeGen)

  function getServiceUrl ({
    cardId,
    asset,
    egmLocation,
    egmId
  }) {
    return new Promise((resolve, reject) => {
      if (model.state.serverMockData) {
        const protocal = model.state.protocal || 'http'
        const ip = model.state.serverIP
        const domain = model.state.domain
        if (protocal === 'http' && ip) {
          resolve(`http://${ip}:15360/randomRichesConfig?cardid=&asset=&location=&egmId=IGT_0049088826`)
        } else if (protocal === 'https' && domain) {
          resolve(`https://${domain}:15360/randomRichesConfig?cardid=&asset=&location=&egmId=IGT_0049088826`)
        } else {
          reject('specify serverIP=10.222.31.103 or protocal=https&domain=randomriches.igt.com as query string of request url in serverMockData mode')
        }
      } else {
        const addrNamespace = 'randomriches.igt.com'
        IGTMediaElements.getServerAddresses([addrNamespace]).then(function (serverAddresses) {
          const h = serverAddresses[addrNamespace]

          IGTMediaElements.util.log.info(`randomriches.igt.com is ${h}`)

          if (h && h !== 'UNKNOWN') {
            resolve(`${h}/randomRichesConfig?cardid=${cardId}&asset=${asset}&location=${egmLocation}&egmId=${egmId}`)
          } else {
            reject(util.customizeMessage('general.host_ip_configure_issue', 'incorrrect ip configured'))
          }
        }).catch(function (error) {
          IGTMediaElements.util.log.error(`rr-summary-model error happened while getting server address, error is ${error}`)
          reject(util.customizeMessage('unknown', error))
        })
      }
    })
  }

  function fetchData () {
    // todo add analytics track view api
    return new Promise((resolve, reject) => {
      getServiceUrl({
        cardId: model.state.cardId,
        asset: model.state.asset,
        egmLocation: model.state.egmLocation,
        egmId: model.state.egmId()
      }).then((url) => {
        IGTMediaElements.util.log.info(`rr-summary-model web request full url is ${url}`)

        return util.timeoutPromise(3000, fetch(url)).then((response) => {
          const contentType = response.headers.get('content-type')
          if (contentType && contentType.includes('application/json')) {
            resolve(response.json())
          } else {
            reject(util.customizeMessage('summary.invalid_response', 'incorrect response get from RR service'))
          }
        }).catch((error) => {
          reject(util.customizeMessage('general.service_timeout', error))
        })
      }).catch(error => {
        reject(error)
      })
    })
  }

  function getServerData () {
    return new Promise((resolve, reject) => {
      if (model.state.demo || process.env.NODE_ENV === 'development') {
        setTimeout(() => {
          resolve(mockData)
          // reject('time out while receiving data from service')
        }, 2000)
      } else {
        fetchData().then((json) => {
          resolve(json)
        }).catch((error) => {
          reject(error)
        })
      }
    })
  }

  function callSvc () {
    model.state.errorMessage = '' // set it to empty in case of content reconnects to RR service

    const cb = model.analytics.willTriggerBusiness('getServerData')
    getServerData().then((json) => {
      const promotions = json.randomRichesConfig.promotions
      if (promotions && promotions.length > 0) {
        promotions.forEach((p) => {
          if (p.activeThresholdIdx > -1 && parseInt(p.pointBalance) < parseInt(p.thresholds[p.thresholds.length - 1])) {
            model.state.promotionIds.push(p.promoId)
            model.state.promotions.push(p)
            thresholdMap[p.promoId] = parseInt(p.activeThresholdIdx)
            model.commit('UPDATE_PLAYER_PROGRESS', {
              p: p,
              progress: p.pointBalance
            })
          }
        })

        if (model.state.promotions.length > 0) {
          model.dispatch('changePromotion')

          currentPromotionIndex = model.state.promotionIds.indexOf(model.state.currentPromotion.promoId)

          model.state.randomRichesAvailable = true

          populateProgressMeter()
        }
      } else {
        model.state.errorMessage = util.customizeMessage('summary.no_active_promotion_with_non_hit_tiers', 'no active promotion existed')
      }

      cb() // todo test it
    }).catch((error) => {
      model.state.errorMessage = error
      IGTMediaElements.util.log.error(`rr-summary-model callSvc error is ${error}`)
    })
  }

  function populateProgressMeter () {
    setInterval(() => {
      IGTMediaElements.contentCache.getStateElement('rr-progressMeter').then(data => {
        if (!data) return
        try {
          Object.keys(data).forEach(pId => {
            if (pId) { // why pId is undefined which generated in worker?
              const curPromo = model.getters.getPromotionByPromoId(pId)
              if (curPromo) {
                const prev_pr = curPromo.pointBalance
                const [cur_pr, cur_pId] = data[pId].split(',')
                if (cur_pr != prev_pr && cur_pId === model.state.playerId) {
                  model.commit('UPDATE_PLAYER_PROGRESS', {
                    p: curPromo,
                    progress: cur_pr
                  })
                }
              } else {
                IGTMediaElements.util.log.debug(`rr-summary-model populateProgressMeter no promotion for ${pId}, current promotions are ${JSON.stringify(model.state.promotions)}`)
              }
            }
          })
        } catch (err) {
          IGTMediaElements.util.log.error(`rr-summary-model populateProgressMeter error is ${err}`)
          model.state.errorMessage = util.customizeMessage('unknown', err)
        }
      })
    }, 1000)
  }

  setInterval(() => {
    IGTMediaElements.contentCache.getStateElement('rr-isWebSocketConnected').then(data => {
      model.state.status.isWebSocketConnected = (data + '' === 'true')
    })

    // IGTMediaElements.contentCache.getStateElement('rr-xcAvailable').then(data => {
    //   model.state.status.xcAvailable = (data + '' === 'true')
    // })

    // IGTMediaElements.contentCache.getStateElement('rr-ptpAvailable').then(data => {
    //   model.state.status.ptpAvailable = (data + '' === 'true')
    // })

    // IGTMediaElements.contentCache.getStateElement('rr-isDuplicateCard').then(data => {
    //   model.state.status.isDuplicateCard = (data + '' === 'true')
    // })
  }, 5000)

  // setTimeout(() => {
  //   IGTMediaElements.contentCache.getStateElement('rr-isIsmUpdateEnabled').then(data => {
  //     model.state.status.isIsmUpdateEnabled = (data + '' === 'true')
  //   })
  // }, 10000)

  // setTimeout(() => {
  //   IGTMediaElements.contentCache.getStateElement('rr-isWSConfigCorrect').then(data => {
  //     model.state.status.isWSConfigCorrect = (data + '' === 'true')
  //   })
  // }, 5000)

  // // printLogOnServiceWindow
  // setInterval(() => {
  //   IGTMediaElements.contentCache.getState().then(data => {
  //     if (!data) return

  //     Object.keys(data).forEach(function (key) {
  //       if (/^worker-log-/.test(key)) {
  //         trace(`web worker: ${JSON.stringify(data[key])} for ${key}`)
  //         IGTMediaElements.contentCache.deleteStateElement(key)
  //       } else {}
  //     })
  //   })
  // }, 2000)

  if (model.state.serverMockData) {
    callSvc()
    model.start()
    model.state.currentTheme = 'build-wonder'
  } else {
    model.watch(() => [model.state.uniqueId, model.state.cardId, model.state.asset, model.state.egmLocation, model.state.playerId], function (newValue, oldValue) {
      if (newValue.includes('0') || newValue.includes('') || newValue.includes(null)) return

      callSvc()

      let times = 6
      const interval = setInterval(() => {
        if (times === 0) {
          clearInterval(interval)
        }
        if (model.state.status.isWebSocketConnected && !model.state.randomRichesAvailable) {
          callSvc()
          times -= 1
        }
      }, 5000)
    }, {
      immediate: true
    })

    // model.mapToken('0x30', 'playerName')
    model.mapToken('0x45', 'playerId')
    model.mapToken('0x17', 'uniqueId')
    model.mapToken('0x31', 'cardId')
    model.mapToken('0x10', 'asset')
    model.mapToken('0xAF', 'egmLocation')

    // model.dispatch('reloadConfig')
    model.start(true)

    model.watch('appConfig', (data) => {
      model.state.RR_config = data['com.igt.randomriches']
      IGTMediaElements.util.log.info('rr-summary-model load config')
    })

    model.watch(() => {
      return [model.state.currentPromotion, model.state.RR_config, model.state.errorMessage, model.state.isReady]
    }, ([currentPromotion, RR_config, errorMessage]) => {
      if (errorMessage) {
        model.state.isReady = true
      } else {
        if (!currentPromotion.promoName || !RR_config.themePromotionMap) {
          if (model.state.isReady) {
            model.state.isReady = false
          }

          return
        }

        model.state.currentTheme = model.getters.promotionTheme(currentPromotion.promoName)

        // IGTMediaElements.util.log.debug(`rr-summary-model promoName[${currentPromotion.promoName}] currentTheme[${model.state.currentTheme}]`)

        if (!model.state.isReady) {
          setTimeout(function () {
            model.state.isReady = true
          }, 50)
        }
      }
    }, {
      immediate: true
    })
  }

  return model
}

export default cm.declareModel('com.igt.RR.summary', modelConstructor)
