import cm from 'm5-content-model'

export default cm.declareModel('com.igt.cardlessStatus', function (el, options, beforeGen) {
    const model = cm.genModel({
        state: {
            interfaceType: "",
            connectStatus: "",
            versionCode:"",
            mobile:"",
            appLabel:"",
            disconnectedReasonCode:"",
            lastDisconnectReasonCode:"",
            historyStatusData:"",
            hasUpdated: null
        },
        actions: {
            onGetCardlessStatusHistory:function(){
                console.log("onGetCardlessStatusHistory")
                getCardlessStatusHistory();
            }
        }
    }, options, el, beforeGen);

    function getCardlessStatusHistory(){
        IGTMediaElements.cardless.getCardlessStatusHistory().then((data) => {
            console.log('history:', JSON.stringify(data));
            trace("cardlessHistory:", JSON.stringify(data))
            model.state.historyStatusData = data;
        });
    }

    function getAndSubCardlessStatus() {
        console.log("start to get cardless status");    
        IGTMediaElements.cardless.getCardlessStatus().then((data) => {  
            console.log('CardlessStatus:', JSON.stringify(data));
            trace("CardlessStatus:", JSON.stringify(data));      
            model.state.hasUpdated = false;
            setCardlessStatusData(data);
        });

        IGTMediaElements.cardless.subscribeCardlessStatus((dataSub) => {
            console.log('subCardlessStatus:', JSON.stringify(dataSub));
            trace("subCardlessStatus:", JSON.stringify(dataSub));    
            model.state.hasUpdated = true;
            setCardlessStatusData(dataSub);
        })
    }

    function setCardlessStatusData(data) {        
        IGTMediaElements.util.log.debug("setCardlessStatusData:" + JSON.stringify(data));
        if(data && data.interface && data.status) {
            model.state.interfaceType = data.interface;
            model.state.connectStatus = data.status
            model.state.versionCode = data.version || "";
            model.state.mobile = data.mobile || "";
            model.state.appLabel = data.app_label || "";
            model.state.disconnectedReasonCode = data.disconnectReason || "";
            model.state.lastDisconnectReasonCode = data.lastDisconnectReason || "";
        }
    }

    getAndSubCardlessStatus();
    model.start();
    return model;
})
