import Vuex from 'vuex'

export default function(defaultStore, store, customOptions, overwrite){
	customOptions = Object.assign({
		state: {},
		getters: {},
		mutations: {}, 
		actions: {},
		modules: {}
	}, customOptions);
	// defaultStore = defaultStore || {};
	// store = store || {};
	var ms = {};
	for (var key in customOptions.modules){
		ms[key] = customOptions.modules[key].raw;
		
		for(var i of ["didTriggerBusiness", "getAppConfig", "getURLMaps", "reloadConfig", "syncDisplayType", "trackException", "trackNavigate", "trackPress", "trackView", "willTriggerBusiness"]){
			delete ms[key].actions;
		}
		// ms[key].namespaced = true;
		
	}
	overwrite = Object.assign({ state: null, mutations: null, getters: null, actions: null, modules: null }, overwrite); 
	customOptions.modules = ms;
	var mergedStore = {
		state: Object.assign({}, defaultStore.state, store.state, customOptions.state, overwrite.state),
		mutations:Object.assign({}, defaultStore.mutations, store.mutations, customOptions.mutations, overwrite.mutations),
		getters: Object.assign({}, defaultStore.getters, store.getters, customOptions.getters, overwrite.getters),
		actions: Object.assign({}, defaultStore.actions, store.actions, customOptions.actions, overwrite.actions),
		modules: Object.assign({}, defaultStore.modules, store.modules, customOptions.modules, overwrite.modules)
	}
	// Object.values(mergedStore.modules).forEach(function(value){
	// 	value.state._isSubModule = true;
	// });
	return mergedStore;
}