import {genModel, declareModel,mapToken, start} from "m5-content-model"


function modelConstructor(el,modules){
  var store = {
    state: {
      AmountWonSoFar: null,
      CurrentMultiplier: null,
      RemainingTime: null
    }
  }
  const model = genModel(store, modules, el);

  model.mapToken("0x7C", "AmountWonSoFar");
  model.mapToken("0x18", "CurrentMultiplier");
  model.mapToken("0x44", "RemainingTime");

  model.start();

  return model;
}

export default declareModel("com.igt.MJT", modelConstructor)