import changeFrame from "../feature/FreeTransform"
import translate from "../feature/LocalizeFilter"
import changePlaceholder from "../feature/ReplacePlaceholderFilter"
import keepWhitespace from "../feature/KeepWhitespaceFilter"
import { getURLParameterByName, getURLParams,  updateGlobalState, originalIGTMediaElements, mockIGTMediaElements } from "../supporting/utils"
import Vue from 'vue'
import navi from "../feature/Navigation"
import analytics from "../feature/Analytics"
import formatMoney from "../feature/FormatMoneyFilter";
import debounce from "lodash/debounce"
import { setDocumentStyleProperty } from "../styles/DefineStyle";



var context = { resizeMethod: null };
var cachedGlobal = {
    language: "en"
};
var reactiveContext = new Proxy(context, {
    set(obj, key, value) {
        var result = Reflect.set(obj, key, value)
        if (key == "resizeMethod" && typeof obj["resizeMethod"] == "function") {
            obj["resizeMethod"]();
        }
        return result;
    },
    get(obj, key) {
        return Reflect.get(obj, key);
    }
})

var patchStyle = null;

export default function template(el, isPrimary) {
    var data, computed, watch, methods, mounted, beforeMount;
    var needsWriteToDB = true;


    data = {
        localization: {},
        nowarning: Number(getURLParameterByName("nowarning")) == 1,
        resize: "",
        isDebugging: Number(getURLParameterByName("debug")) == 1,
        resizeExcludes: [],
        isTransforming: false,
        isSyncingData: false
    };
    computed = {
        allowDebug() {
            return this.isDebugging || this.appConfig.isDebug;
        },
        originalSize() {
            if (this.isVertical) {
                return { width: 256, height: 956 }
            } else {
                return { width: 960, height: 224 }
            }
        },
        systemValidationErrorDescriptions() {
            return Object.values(this.systemValidationErrors).filter(i => !!i)
        },
        analytics() {
            var self = this;
            Object.keys(analytics.apis).forEach(function (key) {
                analytics.apis[key] = analytics.apis[key].bind(self)
            })
            return analytics.apis;
        },
        currencySymbol() {
            return this.appConfig.currencySymbol || this.$store.state.currencySymbol;
        },
        isProcessing() {
            
            return !this.isReady || this.isTransforming || this.isChangingDisplayType;
        },
        isWarningEnabled() {
            return !this.nowarning && !this.isChangingDisplayType && (this.appConfig ? this.appConfig.warning : false);
        },
        matrix: {
            cache: false,
            get() {
                var match = (/matrix\((\s?[-+]?[0-9]*\.?(?:[0-9]+))\,(\s?[-+]?[0-9]*\.?(?:[0-9]+))\,(\s?[-+]?[0-9]*\.?(?:[0-9]+))\,(\s?[-+]?[0-9]*\.?(?:[0-9]+))\,(\s?[-+]?[0-9]*\.?(?:[0-9]+))\,(\s?[-+]?[0-9]*\.?(?:[0-9]+))\)/.exec($(el).css("transform")));
                if (match && this.contentWidth && this.contentHeight) {
                    let arr = Array.from(match);
                    arr.shift();
                    return arr.map(i => Number(i));
                } else {
                    return [1, 0, 0, 1, 0, 0];
                }

            }
        }
    };
    watch = {
        $_global: {
            handler(value) {
                let oldValue = cachedGlobal;
                let syncState = {};
                let needsSync = false;
                for (var key in value) {
                    if (value[key] != oldValue[key]) {
                        needsSync = true;
                        syncState[key] = this.$store.state[key] = value[key];
                    }
                    if(value[key] != this.$store.state[key]){
                        this.$store.state[key] = value[key];
                    }
                }
                cachedGlobal = JSON.parse(JSON.stringify(value));
                if(needsSync){
                    updateGlobalState(syncState);
                }
            },
            deep: true

        },
        cardId(value) {
            this.refreshLanguage();
            // IGTMediaElements.analytics.trackView(this.screenId, document.location.href, { cardId: value });
        },
        isDemo: {
            immediate: true,
            handler(value) {
                mockIGTMediaElements(value);
            }
        },
        isAnalyticsReady(value) {
            if (value) {
                this.analytics.trackView();
            }
        },
        isProcessing: {
            immediate: true,
            handler(value) {
                $(el).setVisible(!value);
                if(isPrimary){
                    $("#loading").setVisible(value)
                }
            }
        },
        language(value) {
            $(":root").attr("lang", value);
            if(this.$store.state.$_global.language != value){
                this.$store.state.$_global.language = value;
                IGTMediaElements.getCurrentTokenValue('0x31').then((e) => {
                    var cardId = e.token.value;
                    IGTMediaElements.contentCache.setStateElement("language-" + cardId, value);
                });
            }
        },
        preferredDisplayType() {
            if (this.$store._actions["syncDisplayType"]) {
                this.$store.dispatch("syncDisplayType");
            }
        },
        currentContentSize: {
            handler(value) {
                this.$nextTick(function () {
                    this.resizeApp(true);

                })
            },
            deep: true
        },
        allowDebug(n) {
            if (n) {
                this.$nextTick(function () {
                    this.resizeApp();
                })

            }
        },
        resize(value, oldValue) {
            $(this.$el).attr("resize", value);
            this.resizeApp();
        }


    };
    methods = {
        navigate: navi,
        updateGlobalState, getURLParameterByName, getURLParams,
        switchDebug: function () {
            console.log("switch debug");
            if (this.$refs.log && this.$refs.log.switchDebug) {
                this.$refs.log.switchDebug();
            }
        }
    };
    beforeMount = function () {
        var self = this;
        // Vue.filter("translate", translate(this));

        if(isPrimary){
            if (this.$el.attributes['resize'] && this.$el.attributes['resize'].value) {
                this.resize = this.$el.attributes['resize'].value;;
            }
            if (this.$el.attributes['currencySymbol'] && this.$el.attributes['currencySymbol'].value) {
                this.$store.state.currencySymbol = this.$el.attributes['currencySymbol'].value;;
            }
            if (this.$el.attributes['resize-excludes'] && this.$el.attributes['resize-excludes'].value) {
                var arr = this.$el.attributes['resize-excludes'].value.split(",").map(e => e.trim());
                this.resizeExcludes = arr;
            }
    
            // var width = this.$el.clientWidth || $(this.$el).width();
            // var height = this.$el.clientHeight || $(this.$el).height();
            // mapResizeToMethod();
            Vue.filter("translate", translate(this));
            Vue.filter("changePlaceholder", changePlaceholder);
            Vue.filter("money", formatMoney);
            Vue.filter("keepWhitespace", keepWhitespace);
            patchStyle = $("style[patch]");
            this.resizeApp();
            function adjustContentSize() {
                self.$store.state.currentContentSize = Object.assign(self.$store.state.currentContentSize, { width: window.innerWidth, height: window.innerHeight })
                Vue.nextTick(function () {
                    self.isTransforming = false;
                })
            }
            
            var debounceDuration = parseInt(self.urlparams.transformDuration) || self.transformDuration || (["fullscreen", "normal"].includes(self.preferredDisplayType) ? 300: 0);
            var deboundedAdjustContentSize = debounce(adjustContentSize, debounceDuration);
            self.isTransforming = true;
            deboundedAdjustContentSize();
            window.addEventListener("resize", function (e) {
                self.isTransforming = true;
                if (debounceDuration <= 17) {
                    adjustContentSize();
                } else {
                    deboundedAdjustContentSize();
                }
            });
            if (this.$store._actions["syncDisplayType"] && ["normal", "fullscreen"].includes(this.$store.state.preferredDisplayType)) {
                this.$store.dispatch("syncDisplayType");
            }
        }else{
            this.$options.filters["translate"] = translate(this);

        }

        window.addEventListener("message", function (e) {
            /*property that should be expose in igt-media-elements*/
            const data = e.data;
            const cmd = data.cmd;
            if (cmd == "fullscreenState") {
                if ("fullscreenState" in data) {
                    self.$store.state.isFullScreen = data.fullscreenState;
                }
            }
        });
    };
    methods.resizeApp = function (ignoreReset) {
        var value = this.resize;
        reactiveContext.resizeMethod && reactiveContext.resizeMethod();
        this.resetScale();
        let ratio = window.innerWidth / window.innerHeight;
        if (this.resizeExcludes.indexOf(this.displayName) == -1) {
            if (value == "preset-1") {
                var threshold = 5 / 1;
                if (ratio > threshold) {
                    reactiveContext.resizeMethod = this.autoScaleFillTrailing;
                } else {
                    // var isUnkown = this.displayName.toLowerCase().indexOf("unkown") != -1;
                    // if (isUnkown) {
                    //     reactiveContext.resizeMethod = this.autoScale;
                    // } else {
                    //     reactiveContext.resizeMethod = this.smartScale;
                    // }
                    reactiveContext.resizeMethod = this.stretch;
                }
                patchStyle.remove();
            } else if (value == "preset-2") {
                var isUnkown = this.displayName.toLowerCase().indexOf("unkown") != -1;
                if (isUnkown) {
                    if (ratio >= 640 / 240 || (ratio >= .2 && ratio <= .4)) {
                        reactiveContext.resizeMethod = this.smartScale;
                    } else {
                        reactiveContext.resizeMethod = this.autoScale;
                    }
                } else {
                    reactiveContext.resizeMethod = this.smartScale;
                }
            } else if (/auto\s?stretch/.test(value)) {
                reactiveContext.resizeMethod = this.stretch;
                patchStyle.remove();
            } else if (/auto\s?scale\s?fill\s?trailing/.test(value)) {
                reactiveContext.resizeMethod = this.autoScaleFillTrailing;
                patchStyle.remove();
            } else if (/auto\s?scale\s?fit\s?bottom/.test(value)) {
                reactiveContext.resizeMethod = this.autoScaleFitBottom;
                patchStyle.remove();
            } else if (/auto scale$/.test(value)) {
                reactiveContext.resizeMethod = this.autoScale;
                patchStyle.remove();
            } else if (/auto scale fill$/.test(value)) {
                reactiveContext.resizeMethod = this.autoScaleFill;
                patchStyle.remove();
            } else if (/smart scale/.test(value)) {
                reactiveContext.resizeMethod = this.smartScale;
            }

        } else {
            reactiveContext.resizeMethod = this.resetScale;
            patchStyle.remove();

        }
    }
    methods.resetScale = function () {
        if (this.matrix.join("") == "100100" && this.resize.indexOf("preset") != 0) {
            return;
        }
        $("#decoration").css({
            display: "none"
        });
        $("#log").css({ width: "100%", left: 0, bottom: 0 });
        $(this.$el).css(this.originalSize);
        $(this.$el).css({ transform: "", transformOrigin: "center" });
        var { width, height } = this.originalSize;
        setDocumentStyleProperty("--app-width", this.originalSize.width + "px");
        setDocumentStyleProperty("--app-height", this.originalSize.height + "px");
        if (this.isVertical) {
            setDocumentStyleProperty("--component-space", `${width}px`);
            setDocumentStyleProperty("--app-unit", `${width}px`);
        } else {
            var ideaWidth = Math.min(height, width / 4);
            setDocumentStyleProperty("--component-space", `${ideaWidth}px`);
            setDocumentStyleProperty("--app-unit", `${height}px`);
        }


    }
    methods.autoScale = function () {
        // this.resetScale();
        var { width, height } = this.originalSize;
        var contentScale = width / height;
        var displayWidth = window.innerWidth;
        var displayHeight = window.innerHeight;
        var displayScale = displayWidth / displayHeight;

        if (contentScale < displayScale) {
            var scale = displayHeight / height;
            var contentRect = { x: (displayWidth - width * scale) / 2, y: 0, width: width * scale, height: displayHeight }
        } else {
            var scale = displayWidth / width;
            var contentRect = { x: 0, y: (displayHeight - height * scale) / 2, width: displayWidth, height: height * scale }
        }
        $("#decoration").css({
            display: "none"
        });
        if (contentScale < displayScale) {
            $("#log").css({ width: displayScale / contentScale * 101 + "%", left: (contentScale - displayScale) / contentScale * 50 - 1 + "%", bottom: 0 })
        } else if (contentScale == displayScale) {
            $("#log").css({ width: "100%", left: 0, bottom: 0 });
        } else {
            $("#log").css({ left: 0, width: "100%", bottom: (1 / contentScale - 1 / displayScale) * contentScale * 50 + "%" })
        }

        this.changeFrame(contentRect);


    }

    methods.autoScaleFitBottom = function () {
        // this.resetScale();
        var { width, height } = this.originalSize;
        var contentScale = width / height;
        var displayWidth = window.innerWidth;
        var displayHeight = window.innerHeight;
        var displayScale = displayWidth / displayHeight;

        if (contentScale < displayScale) {
            var scale = displayHeight / height;
            var contentRect = { x: (displayWidth - width * scale) / 2, y: 0, width: width * scale, height: displayHeight }
        } else {
            var scale = displayWidth / width;
            var contentRect = { x: 0, y: (displayHeight - height * scale), width: displayWidth, height: height * scale }
        }
        $("#decoration").css({
            display: "none"
        });
        if (contentScale < displayScale) {
            $("#log").css({ width: displayScale / contentScale * 100 + "%", left: (contentScale - displayScale) / contentScale * 50 + "%", bottom: 0 })
        } else {
            $("#log").css({ width: "100%", left: 0, bottom: 0, marginBottom: 0 });
        }
        this.changeFrame(contentRect);


    }
    methods.autoScaleFillTrailing = function () {
        var { width, height } = this.originalSize;
        var contentScale = width / height;
        var displayWidth = window.innerWidth;
        var displayHeight = window.innerHeight;
        var contentRect = { x: window.innerWidth - window.innerHeight * contentScale, y: 0, width: window.innerHeight * contentScale, height: window.innerHeight }
        $("#decoration").css({ display: "block", width: displayWidth - displayHeight * contentScale + "px", height: displayHeight + "px" });
        this.changeFrame(contentRect);
    }
    methods.autoScaleFill = function () {
        var { width, height } = this.originalSize;
        var displayWidth = window.innerWidth;
        var displayHeight = window.innerHeight;
        var shouldCutSide = width / height > displayWidth / displayHeight;
        var scale = shouldCutSide ? displayHeight / height : displayWidth / width;
        this.changeFrame({ x: -(width * scale - displayWidth) / 2, y: -(height * scale - displayHeight) / 2, width: width * scale, height: height * scale });
    }
    methods.smartScale = function () {

        var { width, height } = this.originalSize;
        var displayHeight = window.innerHeight;
        var displayWidth = window.innerWidth;
        var ideaWidth = displayWidth / displayHeight * height;
        var scale = displayHeight / height;
        $(this.$el).css({ width: ideaWidth, transform: `scale(${scale})`, transformOrigin: "0 0" });

        if (this.isVertical) {
            setDocumentStyleProperty("--app-width", `${ideaWidth}px`);
            setDocumentStyleProperty("--component-space", `${ideaWidth}px`);
            setDocumentStyleProperty("--app-unit", `${ideaWidth}px`);
        } else {
            setDocumentStyleProperty("--app-width", `${ideaWidth}px`);
            var ideaWidth = Math.min(height, ideaWidth / 4);
            setDocumentStyleProperty("--component-space", `${ideaWidth}px`);
            setDocumentStyleProperty("--app-unit", `${height}px`);
        }
        $("head").append(patchStyle)
    }
    methods.refreshLanguage = function () {
        let self = this;
        IGTMediaElements.contentCache.getStateElement("language-" + self.cardId).then(value => {
        self.updateGlobalState({language: value || "en"});
        });

    }
    methods.changeFrame = function (to) {
        changeFrame(el, to)
    }

    methods.stretch = function () {
        // this.resetScale();
        this.changeFrame({ width: window.innerWidth, height: window.innerHeight })
    };
    return [data, computed, watch, methods, mounted, beforeMount, navi];
}



