import {getURLParameterByName} from "../supporting/utils"
import Vue from "vue"

var preLogs = [];
let maxLogNum = 30;
function prelogTrace() {
	var displayIdx = ("00" + (++idx)).substr(-3);
	displayIdx = "[" + displayIdx + "] "
	let logMsg = displayIdx + Array.from(arguments).join("\t");
	// console.log(`%c${logMsg}`, "color: blue;");
	preLogs.push(logMsg);
	preLogs = preLogs.slice(0, maxLogNum);
}
window.trace = prelogTrace;

var idx = 0;
function format(log){
	function styleOf(s) {

		switch (s) {
			case " ":
				return "color: white";
				break;
			case "!":
				return "color: red; font-weight: bold"
				break
			case "?":
				return "color: yellow; font-weight: bold"
				break;
			case ";":
				return "color: lightgray; font-style:italic;";
				break;
			case ".":
				return "color: lightgreen;font-weight: bold;";
				break;
		}
		return "";
	}
	function splitMessageAndStyle(line) {
		if ("!?;. ".indexOf(line.slice(-1)) == -1) {
			return { message: line, style: "" }
		} else {
			return { message: line.slice(0, line.length - 1), style: styleOf(line.slice(-1)) }
		}
	}
	var arr = log.split("\n");
	var lineCount = arr.length;
	if (lineCount == 1) {
		return {
			main: splitMessageAndStyle(log),
			subs: null
		}
	} else {
		return {
			main: splitMessageAndStyle(arr[0]),
			subs: arr.slice(1, arr.length).map(function (item) {
				return splitMessageAndStyle(item);
			})

		}
	}
}

Vue.component("log", {
	// functional: true,
	data(){
		return {
			shown: true,
			logs:[]
		}
	},
	computed:{
		displayLogs(){
			var formattedLogs = this.logs.slice(-maxLogNum).map(function (item) {
				return format(item);
			})
			return formattedLogs;
		}
	},
	methods:{
		trace(){
			var self = this;
			var args = arguments;
			Vue.nextTick(function(){
				var displayIdx = ("000" + (++idx)).substr(-3);
				displayIdx = "[" + displayIdx + "] ";				
				while(self.logs.length > 100){
					self.logs.shift();
				}
				// var cTime = new Date();
				// var currentTime = cTime.getHours() + ":" + cTime.getMinutes() + ":" + cTime.getSeconds() + ":" + cTime.getMilliseconds()+":";
				self.logs.push(displayIdx + Array.from(args).join("\t"));
			})
			
		},
		switchDebug(){
			if(!this.$root.allowDebug) return;
			this.shown = !this.shown;
		}
	},
	template:"<div v-show='shown' id='log' style='pointer-events: none;position: absolute;background: rgba(0, 0, 0, 0.7); width: 100%;color: white;font:  12px Consolas; z-index: 1005; bottom: 0px;'>\
			<ul style='list-style:none;padding:5px;text-align:left'><li v-for='log in displayLogs' :style='log.main.style + \"; word-break: break-all; width: 100%\"'>{{log.main.message}}<ul><li v-for='line in log.subs' :style='line.style + \"; word-break: break-all; width: 100%\"'>{{line.message}}</li></ul></span></li></ul>\
			</div>"

})


function debugPanel(model){
	let el = model.vm.$el;
	let vm = model.vm;
	if(!vm.$refs.log) return;
	if(el){
		
	}
	model.watch("allowDebug", function(value){
		if(value){
			vm.$refs.log.logs = preLogs.concat(["-------------------------------------------------", " Debug Panel Enabled\n framework version:  " + vm.frameworkVersion + "\ncontent version: " + vm.contentVersion, "-------------------------------------------------"]);
			preLogs = [];
			window.trace = model.vm.$refs.log.trace
		}else{
			window.trace = prelogTrace;
		}
	}, {immediate: true});

}

export default debugPanel;