import enteringTickets from '../api/enteringTickets';

const state = {
  freeEntryTicketList: [],
  ticketEntryList: [],
  currentReqSeqNo: 0,
  stopRedeem: false,
  isDone: false
}

const getters = {
  redeemNeededTimes: (state, getters, rootState) => {
    return Math.ceil(rootState.AllTickets / 1000);
  },
  enteringTktNum: (state, getters, rootState) => {
    return state.currentReqSeqNo < getters.redeemNeededTimes - 1 ?
      1000 :
      rootState.AllTickets - state.currentReqSeqNo * 1000;
  }
};

const actions = {
  RedeemTickets({
    state,
    commit,
    dispatch,
    rootGetters,
    rootState
  }, payload) {
    return new Promise((resolve, reject) => {
      enteringTickets.RedeemTickets(rootState.EgmLocation, rootState.CardID, rootState.currentSelectDrawingId,
        rootState.currentSelectPromoAwardId, payload.redeemTktCount, rootGetters.transId, rootGetters.guid).then((data) => {
        if (data.ErrorCode != 0) {
          rootGetters.rootAnalytics.trackException("Error", {Error:data});
          dispatch("addInfo", {
            message: data.ErrorMessage,
            errorCode: data.ErrorCode
          }, {
            root: true
          });
          if (!payload.isSingleReq) {
            commit("setRedeemStopped", true);
            reject();
          }
        } else {
          commit("fillTicketList", data);
          commit("setRateOfRedeemProcess", true);
          commit("setEnteredTktNum", rootState.AllTickets, {
            root: true
          });
          resolve(data);
        }
      }).catch(err => {
        rootGetters.rootAnalytics.trackException("Error", {Error:err.message});
        dispatch("addInfo", {
          "message": err.message
        }, {
          root: true
        });
        if (!payload.isSingleReq) {
          commit("setRedeemStopped", true);
          reject();
        }
      })
    })
  },
  singleReqHandler({
    state,
    commit,
    dispatch,
    getters,
    rootGetters
  }) {
    return new Promise((resolve) => {
      dispatch("RedeemTickets", {
          redeemTktCount: getters.enteringTktNum,
          isSingleReq: false
        })
        .then(() => {
          state.currentReqSeqNo++;
          resolve(true);
        })
        .catch(() => {
          rootGetters.rootAnalytics.trackException("Info", {Error:"Redeem is Stopped."});
          resolve(false);
        });
    });
  },
  multiRedeemReqsHandler({
    state,
    commit,
    dispatch,
    getters
  }) {
    commit("setRedeemStopped", false);

    function next() {
      if (state.currentReqSeqNo < getters.redeemNeededTimes && !state.stopRedeem) {
        dispatch("singleReqHandler").then(data => {
          if (!data) return;
          next();
        })
      }
    }
    next();
  }
};

const mutations = {
  fillTicketList(state, data) {
    if ((data.TicketStart >= 0) && (data.TicketEnd >= 0))
      state.ticketEntryList.push({
        start: data.TicketStart,
        end: data.TicketEnd
      });
    if ((data.FreeEntryTicketStart != 0) || (data.FreeEntryTicketEnd != 0)) {
      state.freeEntryTicketList.push({
        start: data.FreeEntryTicketStart,
        end: data.FreeEntryTicketEnd
      });
    }
  },
  reset(state) {
    state.ticketEntryList = [];
    state.freeEntryTicketList = [];
    state.currentReqSeqNo = 0;
    state.isDone = false;
  },
  sortTicket(state) {
    state.ticketEntryList.sort((a, b) => {
      return a.start - b.start
    });
    state.freeEntryTicketList.sort((a, b) => {
      return Math.abs(a.start) - Math.abs(b.start)
    })

    for (let i = 0; i < state.ticketEntryList.length - 1; i++) {
      if (state.ticketEntryList[i].end + 1 == state.ticketEntryList[i + 1].start) {
        state.ticketEntryList[i+1].start = state.ticketEntryList[i].start
        state.ticketEntryList[i] = NaN;
      }
    }
    state.ticketEntryList = state.ticketEntryList.filter(e => !Object.is(e, NaN));

    for (let i = 0; i < state.freeEntryTicketList.length - 1; i++) {
      if (Math.abs(state.freeEntryTicketList[i].start) + 1 == Math.abs(state.freeEntryTicketList[i + 1].end)) {
        state.freeEntryTicketList[i+1].end = state.freeEntryTicketList[i].end;
        state.freeEntryTicketList[i] = NaN;
      }
    }
    state.freeEntryTicketList = state.freeEntryTicketList.filter(e => !Object.is(e, NaN));

    for (let i = 0; i < state.freeEntryTicketList.length; i++) {
      state.freeEntryTicketList[i].start = state.freeEntryTicketList[i].start.toString().replace("-", "FE");
      state.freeEntryTicketList[i].end = state.freeEntryTicketList[i].end.toString().replace("-", "FE")
    }
  },
  setRedeemStopped(state, status) {
    state.stopRedeem = status;
  },
  setRateOfRedeemProcess(state, status) {
    state.isDone = status;
  }
}

export default {
  'namespaced': true,
  state,
  getters,
  actions,
  mutations
};