const timeoutDuration = 10000;

function timeoutPromise(ms, promise) {
  return new Promise((resolve, reject) => {
    const timeoutId = setTimeout(() => {
      reject(new Error('promise timeout'));
    }, ms);
    promise.then(
      (res) => {
        clearTimeout(timeoutId);
        resolve(res);
      },
      (err) => {
        clearTimeout(timeoutId);
        reject(err);
      }
    );
  });
}

export function getOptionList(endPoint, {
  cardId,
  machineNumber,
  playerId,
  sessionType
}, successCallback, failCallback) {

  trace(`[api] getOptionList [req] cardId[${cardId}] machineNumber[${machineNumber}] playerId[${playerId}] sessionType[${sessionType}]`);

  timeoutPromise(timeoutDuration, fetch(`${endPoint}/RetrieveOperatorInfo?cardId=${cardId}&machineNumber=${machineNumber}&operatorId=${playerId}&sessionType=${sessionType}`)
      .then(function (res) {
        return res.json();
      })
    ).then((data) => {
      trace(`[api] getOptionList [res][${JSON.stringify(data)}]`);

      if (data.Success) {
        successCallback(data);
      } else {
        failCallback({
          title: data.Title,
          message: data.Message
        });
      }
    })
    .catch((exception) => {
      trace(`[api] getOptionList [error][${JSON.stringify(exception)}]`)
      failCallback({
        title: "service call exception",
        message: exception
      });
    });
}

export function getState(endPoint, {
  machineNumber
}, successCallback, failCallback) {

  trace(`[api] getState [req] machineNumber[${machineNumber}]`);

  timeoutPromise(timeoutDuration, fetch(`${endPoint}/RetrieveEGMInfo?machineNumber=${machineNumber}`)
      .then(function (res) {
        return res.json();

      })).then((data) => {
      trace(`[api] getState [res][${JSON.stringify(data)}]`);
      successCallback(data);
    })
    .catch((exception) => {
      trace(`[api] getState [error][${JSON.stringify(exception)}]`);

      failCallback({
        title: "service call exception",
        message: exception
      });
    });
}

export function lock(endPoint, {
  cardId,
  machineNumber,
  playerId,
  sessionType,
  period
}, successCallback, failCallback) {

  trace(`[api] lock [req] cardId[${cardId}] machineNumber[${machineNumber}] OperatorId[${playerId}] sessionType[${sessionType}] period[${JSON.stringify(period)}]`);

  timeoutPromise(timeoutDuration, fetch(`${endPoint}/ReserveEGM`, {
    method: 'POST',
    mode: 'cors',
    cache: 'no-cache',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      cardId,
      machineNumber,
      sessionType,
      OperatorId: playerId,
      period
    })
  }).then((res) => {
    return res.json();
  })).then((data) => {
    trace(`[api] lock [res][${JSON.stringify(data)}]`);
    if (data.Success) {
      successCallback(data);
    } else {
      failCallback({
        title: data.Title,
        message: data.Message
      });
    }
  }).catch((exception) => {
    trace(`[api] lock [error][${JSON.stringify(exception)}]`);
    failCallback({
      title: "service call exception",
      message: exception
    });
  });
}

export function unlock(endPoint, {
  cardId,
  machineNumber,
  playerId,
  sessionType,
  IsLockingTimeExpired,
  IsPinValidated
}, successCallback, failCallback) {

  trace(`[api] unlock [req] cardId[${cardId}] machineNumber[${machineNumber}] OperatorId[${playerId}] sessionType[${sessionType}] IsLockingTimeExpired[${IsLockingTimeExpired}] IsPinValidated[${IsPinValidated}]`);

  timeoutPromise(timeoutDuration, fetch(`${endPoint}/UnReserveEGM`, {
    method: 'POST',
    mode: 'cors',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      cardId,
      machineNumber,
      sessionType,
      OperatorId: playerId,
      IsLockingTimeExpired,
      IsPinValidated
    })
  }).then((res) => {
    return res.json();
  })).then((data) => {
    trace(`[api] unlock [res][${JSON.stringify(data)}]`);
    if (data.Success) {
      successCallback(data);
    } else {
      failCallback({
        title: data.Title,
        message: data.Message,
        errorCode: data.ErrorCode
      });
    }
  }).catch((exception) => {
    trace(`[api] unlock [error][${JSON.stringify(exception)}]`);

    failCallback({
      title: "service call exception",
      message: exception
    });
  });
}
