export function concatUrl(...args: string[]) {
  if (args.length == 0)
    return '';

  var url = arguments[0];
	for(var i = 1; i < args.length; i++) {
    if (endWithSlash(url)) {
      if (beginWithSlash(args[i])) {
        url += args[i].slice(1, args[i].length);
      } else {
        url += args[i];
      }
    } else {
      if (beginWithSlash(args[i])) {
        url += args[i];
      } else {
        url += ("/" + args[i]);
      }
    }
	}

	return url;
}

export function endWithSlash(text) {
  if (text) {
    return (text.slice(-1) == "/");
  } else {
    return false;
  }
}

export function beginWithSlash(text) {
  if (text) {
    return (text.slice(0, 1) == "/");
  } else {
    return false;
  }
}
