import { concatUrl } from './url-helper';


// import 'rxjs/add/operator/catch';
// import 'rxjs/add/operator/map';
// import 'rxjs/add/operator/toPromise';

declare let IGTMediaElements;

export class MediaElementsDelegate {

    public intelOfferHost = "";
    public cmsPlayerId = "";

    private readonly endpointKey = 'offer.io.igt.com';
    private readonly legacyFallBack = 'offer.mio.igt.com'; 
    private readonly playerIdToken = '0x45';

    public contentParams = {height: 560, width: 320};        // U2 screen size by default

    private playerOfferListUrl = '/mobile-service/offer/list?cmsPlayerId={cmsPlayerId}&lastFetchTime={lastFetchTime}';
	private playerOfferRedeemUrl = '/mobile-service/offer/redeem?awardId={awardId}&cmsPlayerId={cmsPlayerId}';
    private playerOfferVoidUrl = '/mobile-service/offer/void?awardId={awardId}&cmsPlayerId={cmsPlayerId}';

    constructor() {
    }

    getOfferListUrl(timestamp:number): any {
        let self = this;
        return new Promise( resolve => {
            
            Promise.resolve().then(() => {
                if (this.intelOfferHost == "") {
                    IGTMediaElements.util.log.info('Calling IGTMediaElements.getServerAddresses with key: ' + self.endpointKey);
                    return IGTMediaElements.getServerAddresses([self.endpointKey]);
                } else {
                    IGTMediaElements.util.log.info('Using IntelOffer host: ' + self.intelOfferHost);
                    return Promise.resolve({[self.endpointKey] : self.intelOfferHost});
                }
            }).then( serverAddresses => {
                this.intelOfferHost = serverAddresses[self.endpointKey];
                IGTMediaElements.util.log.info('IntelOffer host address: ' + this.intelOfferHost);
                if(this.intelOfferHost == undefined){
                    this.intelOfferHost = serverAddresses[self.legacyFallBack];
                    IGTMediaElements.util.log.warn(`Using legacy endpoint key as fallback: ${self.legacyFallBack}`); 
                    IGTMediaElements.util.log.info('IntelOffer host address(legacy key): ' + this.intelOfferHost);
                }
                if(this.intelOfferHost == undefined){
                    IGTMediaElements.util.log.error('cannot get Intelligent Offer service host address with: ' + self.endpointKey);
                }

                if (self.cmsPlayerId == "") {
                    IGTMediaElements.util.log.info('Calling IGTMediaElements.getCurrentTokenValue with token value: ' + self.playerIdToken);
                    return IGTMediaElements.getCurrentTokenValue(self.playerIdToken);
                } else {
                    IGTMediaElements.util.log.info('Using player id passed as parameter: ' + self.cmsPlayerId);
                    return Promise.resolve({token: {value: self.cmsPlayerId}});
                }
            }).then( (data) => {
                IGTMediaElements.util.log.info('CMS player id: ' + data.token.value);
                let url = concatUrl(this.intelOfferHost, self.playerOfferListUrl)
                    .replace('{cmsPlayerId}', data.token.value)
                    .replace('{lastFetchTime}', ''+ timestamp);

                resolve(url);
            });
        });
    }

    getOfferRedeemUrl(awardId: number) {
        return this.getOfferOptionUrl( this.playerOfferRedeemUrl, awardId);
    }

    getOfferVoidUrl(awardId: number) {
        return this.getOfferOptionUrl( this.playerOfferVoidUrl, awardId);
    }

    navToPatronView() {
        if (window.innerHeight / window.innerWidth > 1) {
          IGTMediaElements.loadContent(`${location.origin}/patron/index.html`);
        } else {
          IGTMediaElements.loadContent(`${location.origin}/patron/indexHoriz.html`);
        }
    }

    private getOfferOptionUrl(url, awardId: number): any {
        let self = this;
        return new Promise( resolve => {            
            Promise.resolve().then(() => {
                if (this.intelOfferHost == "") {
                    IGTMediaElements.util.log.info('Calling IGTMediaElements.getServerAddresses with key: ' + self.endpointKey);
                    return IGTMediaElements.getServerAddresses([self.endpointKey]);
                } else {
                    IGTMediaElements.util.log.info('Using IntelOffer host passed as parameter: ' + self.intelOfferHost);
                    return Promise.resolve({[self.endpointKey] : this.intelOfferHost});
                }
            }).then( serverAddresses => {
                this.intelOfferHost = serverAddresses[this.endpointKey];
                IGTMediaElements.util.log.info('IntelOffer host address: ' + this.intelOfferHost);
                if(this.intelOfferHost == undefined){
                    this.intelOfferHost = serverAddresses[self.legacyFallBack];
                    IGTMediaElements.util.log.warn(`Using legacy endpoint key as fallback: ${self.legacyFallBack}`); 
                    IGTMediaElements.util.log.info('IntelOffer host address(legacy key): ' + this.intelOfferHost);
                }
                if(this.intelOfferHost == undefined){
                    IGTMediaElements.util.log.error('cannot get Intelligent Offer service host address with: ' + self.endpointKey);
                }

                if (self.cmsPlayerId == "") {
                    IGTMediaElements.util.log.info('Calling IGTMediaElements.getCurrentTokenValue with token value: ' + self.playerIdToken);
                    return IGTMediaElements.getCurrentTokenValue(self.playerIdToken);
                } else {
                    IGTMediaElements.util.log.info('Using player id passed as parameter: ' + self.cmsPlayerId);
                    return Promise.resolve({token: {value: self.cmsPlayerId}});
                }
            }).then( (data) => {
                IGTMediaElements.util.log.info('CMS player id: ' + data.token.value);
                let optUrl = concatUrl(this.intelOfferHost, url)
                    .replace('{cmsPlayerId}', data.token.value)
                    .replace('{awardId}', ''+awardId);

                resolve(optUrl);
            });
        });
    }

    getUrlParams() {
        IGTMediaElements.contentCache.getStateElement('urlParams').then (value => {
            let params = JSON.parse(value);
            this.contentParams.height = params.contentHeight;
            this.contentParams.width = params.contentWidth;
        });
    }
}
