require("./pointplay.js");
require("./xtracredit.js");
import { genModel, declareModel, mapToken, bindToken, retry } from "m5-content-model"


function modelConstructor(el, modules) {
  var store = {
    state: {
      PlayersPreferredName: "Player",
      PlayerLevelName: "",
      PlayerLevel: 0,
      PointBalance: 0,
      // FreePlayAmountCents: 0,
      // XtraCreditInactiveAmount: 0,
      CompBalanceMoneyToken: "$0.00",
      CurrentMjtMultiplier: 1,
      preferredDisplayType: "normal",
      PlayerPointsEarnedSession: 0,
      PlayerPointPlayBalance: 0,
      // PointInDollar: 0,
      // isPPAvailable: false,
      // isXCAvailable: false,
      isCashlessAvailable: false,
      PlayerSessionGamesPlayed: "N/A"
    },
    mutations: {},
    getters: {},
    actions: {

      changeLanguage(ctx, lan) {
        ctx.state.language = lan
      },
      waitForNavigateToVpcLas() {
        IGTMediaElements.vpc.buttonPress('VPC_KEYPRESS_L')
      }
    },
    modules: {}
  }

  if (IGTMediaElements.isMock) {
    Object.assign(IGTMediaElements.cashless.enabledData, {

      "enabled": true,
      "responseData": {
        "cardType": "9",
        "bonusing": "Big Bonusing",
        "haveData": false,
        "haveLock": true,
        "gameEnabled": true,
        "coinlessAvailable": true,
        "balanceActive": true,
        "cashlessEnabled": true,
        "abandonedStatus": "yes",
        "haveCTSData": true,
        "haveCardData": true,
        "coinlessEnabled": true
      }
    })
  }
  // create model
  const model = genModel(store, modules, el, arguments[2]);

  // bind IGTMediaElements token
  mapToken("0x30", "PlayersPreferredName");
  mapToken("0x11", "PointBalance", { animated: true });
  // mapToken("0x23", "XtraCreditInactiveAmount");
  // mapToken("0x26", "FreePlayAmountCents");
  mapToken("0x2E", "CompBalanceMoneyToken", { notEmpty:true });
  mapToken("0x18", "CurrentMjtMultiplier");
  mapToken("0x13", "PlayerPointsEarnedSession",{notEmpty:true});
  mapToken("0x47", "PlayerSessionGamesPlayed");

  // const centsToCalculate = 100;
  // IGTMediaElements.pointPlay.valueToPoints(centsToCalculate).then(function (data) {
  //   model.state.PointToCentRate = centsToCalculate / data.points;
  //   model.state.PointInDollar = model.state.PlayerPointPlayBalance * model.state.PointToCentRate / 100;
  //   model.state.PointInDollar = parseFloat(Math.floor(model.state.PointInDollar * 100) / 100).toFixed(2);
  //   bindToken("0x1B", (value) => {
  //     model.state.PointInDollar = model.state.PointToCentRate == 0 ? value : value / 100 * model.state.PointToCentRate;
  //     model.state.PointInDollar = parseFloat(Math.floor(model.state.PointInDollar * 100) / 100).toFixed(2);
  //     model.state.PlayerPointPlayBalance = value;
  //     trace("PointToCentRate:", model.state.PointToCentRate, "; PlayerPointPlayBalance: ", model.state.PlayerPointPlayBalance, "; PointInDollar:", model.state.PointInDollar);
  //   });

  // });

  var counter = 0;
  function getPlayerRanking() {
    counter++;
    if (counter > 6) {
      return;
    }
    var trackerG = model.analytics.willTriggerBusiness("getPatronDataExt");
    IGTMediaElements.getPatronDataExt().then(function (res) {
      trackerG(res);

      if (res.patronDataExt && res.patronDataExt.patronDataExt) {
        model.state.PlayerLevel = res.patronDataExt.patronDataExt.playerRankIndex;
        model.state.PlayerLevelName = res.patronDataExt.patronDataExt.playerRank;
      }
      if (model.state.PlayerLevelName.trim() == '') {
        trace("Player Ranking is not ready, will try again in 4 second?")
        setTimeout(function () {
          getPlayerRanking();
        }, 4000);
      }
    }).catch(function (err) {
      trace("error: ", window.JSON.stringify(err), "!")
      getPlayerRanking();
    });
  }

  const retryGetCashless = retry(function(){
    return IGTMediaElements.cashless.checkCashlessEnabled().then(data => {
      return new Promise(function (resolve, reject) {
        if (data.responseData.haveCardData) {
          resolve(data)
        } else {
          reject(new Error("haveCardData is false"));
        }
      })
    })
  }, 3, 1500);
  retryGetCashless.then(data => {
    model.state.isCashlessAvailable = data.enabled
  }).catch(error => {
    trace(`retry 3 times: ${error}!`);
  })
  getPlayerRanking();
  
  // when model is ready, please call start
  model.start(true);

  // setTimeout(start, 1000);

  return model;
}



export default declareModel('com.igt.session_screen', modelConstructor)
