import { genModel, declareModel } from "m5-content-model"

function modelConstructure(el, modules) {
    var store = {
        state: {
        },
        mutations: {},
        getters: {},
        actions: {
          waitForNavigateToVpcLas() {
            IGTMediaElements.vpc.buttonPress('VPC_KEYPRESS_L')
          }
        },
        modules: {}
    }

    const model = genModel(store, modules, el);
    model.start();
    return model;
}

export default declareModel('com.igt.vpc_idle_screen', modelConstructure)