import { fetch } from "../supporting/utils"
import semver from "semver"
import Vue from "vue"

export default function (model) {
    if (!!model.state.igtMediaElementsRequirements.version || !!model.state.igtMediaElementsRequirements.date) {
        fetch("/lib/igt-media-elements/dist/igt-media-elements.js").then(data => {
            var firstline = (data.split('\n').shift());
            var [_, version, date] = /.* - v(\d+\.\d+\.\d+) \- (\d+\-\d+\-\d+) .*/.exec(firstline)
            Vue.set(model.state, "igtMediaElementsVersion", { version, date });
        }).catch(() => {
        });

    }
    const state = model.state;
    const vm = model.vm;

    model.watch(() => [state.igtMediaElementsVersion, state.igtMediaElementsRequirements, state.gitBranch, state.preferredDisplayType, vm.isWarningEnabled,  vm.displayName, state.igtMediaElementsVersion], () => {
        if ("normal" != model.state.preferredDisplayType || !vm.isWarningEnabled){
            model.state.systemValidationErrors = {
                "libExits": null,
                "libVersion": null,
                "libBuildDate": null,
                "branch": null,
                "contentSize": null
            }
            return;
        }
        let versionInfo = model.state.igtMediaElementsVersion;
        if(!!versionInfo){
            model.state.systemValidationErrors["libExits"] = null;
        }else{
            model.state.systemValidationErrors["libExits"] = "Can't find required library from /lib/igt-media-elements/dist/igt-media-elements.js"
        }
        let displayName = model.vm.displayName;
        if(!!displayName){
            if (displayName.toLowerCase().indexOf("unkown") != -1) {
                model.state.systemValidationErrors["contentSize"] = `Unsupported content size ${displayName}`;
            } else {
                model.state.systemValidationErrors["contentSize"] = null;
            }
        }
        var [requiredVersion, requiredDate] = [model.state.igtMediaElementsRequirements.version, model.state.igtMediaElementsRequirements.date];
        var { version, date } = model.state.igtMediaElementsVersion;
        if (!semver.valid(version)) return;
        model.state.systemValidationErrors["libVersion"] = !semver.satisfies(version, requiredVersion) ? `IGTMediaElements is wrong version, expected ${requiredVersion}, got ${version}` : null;
        model.state.systemValidationErrors["libBuildDate"] = Date.parse(date) < Date.parse(requiredDate) ? `IGTMediaElements requires a recent build after ${requiredDate}, but got ${date}` : null
        let gitBranch = model.state.gitBranch;
        if (!gitBranch) return;
        model.state.systemValidationErrors["branch"] = ["dev", "master", "develop", "HEAD"].includes(gitBranch) ? null : `Build branch is not safe: ${gitBranch}(${model.state.gitVersion})`
    }, { immediate: true, deep: true })
}