export const IGTMediaElements = {
  w2gAccrual: {
    cancelW2G: function () {
      console.log("cancel w2g")
    },
    gotoLegacy: function () {
      console.log("legacy")
    },
    init: function () {
      return new Promise(function (resolve) {
        resolve({
          amountInCents: 180000,
          amountInCredits: 180000,
          selfServeOption: 1,
          w2gStatus: 1,
          keytoCreditAvailable: true,
          handpayType: 0,
          w2gTransactionId: 123
        });
      });
    },
    resetToCreditMeter: function () {
      return new Promise(function (resolve, reject) {
        reject("resetToCreditMeter exception");
      });
    },
    requestAttendant: function () {
      return new Promise(function (resolve) {
        setTimeout(() => {
          resolve({
            error: true
          });
        }, 3000);
      });
    },
    setAttendantReturn: function () {
      return new Promise(function (resolve) {
        resolve({
          success: true
        });
      });
    }
  }
}
