function getKeypath(target, keypath) {
    var arr = keypath.split(".")
    var result = target

    while (result = result[arr.shift()]) {
        if (!result) return null
        if (arr.length <= 0) break;
    }
    return result
}

function getDisplayName(width, height){
    let name = getKeypath(size, `${width}px.${height}px.name`)
    return name || getKeypath(size, `${width+1}px.${height}px.name`) // for chrome 49 not accurate bug.
}
export default getDisplayName