import {
  genModel,
  declareModel,
  mapToken
} from "m5-content-model"

const payTableMockData = {
  "bonusId": 54,
  "poolId": 0,
  "paytables": [{
      "matches": 1,
      "awardType": "Tier",
      "cardedAwardAmount": 104,
      "uncardedAwardAmount": 200,
      "payType": "Fixed Money"
    },
    {
      "matches": 2,
      "awardType": "Tier",
      "cardedAwardAmount": 100,
      "uncardedAwardAmount": 200,
      "payType": "Fixed Money"
    },
    {
      "matches": 3,
      "awardType": "Tier",
      "cardedAwardAmount": 10,
      "uncardedAwardAmount": 10,
      "payType": "Fixed Credits"
    },
    {
      "matches": 4,
      "awardType": "Tier",
      "cardedAwardAmount": 0,
      "uncardedAwardAmount": 0,
      "payType": "Meter Amount"
    },
    {
      "matches": 5,
      "awardType": "Primary",
      "cardedAwardAmount": 10,
      "uncardedAwardAmount": 2000000000,
      "payType": "Meter Amount"
    }
  ],
  "dateTime": 1587552017111,
  "pid": 16
};

function modelConstructor(el, modules) {
  var store = {
    state: {
      assignedNumbers: "",
      winningNumbers: "",
      payTable: {},
      drawStatus: null, // 0,1-countdown,2-assign number,3-winning number,4-verification,5-winning,6-non-win
      countdown: null,
      winningAmount: null,
      nextDrawMessage: null
    }
  }

  const model = genModel(store, modules, el);

  if (IGTMediaElements.isMock) {
    Object.assign(IGTMediaElements, {
      bonus: {
        subscribeDrawStatus(cb) {
          cb({
            bonusId: 1,
            drawStatus: 1
          })
        },
        getDrawStatus() {
          return new Promise((resolve, reject) => {
            resolve({
              bonusId: 1,
              drawStatus: 1
            })
          })
        }
      }
    });

    Object.assign(IGTMediaElements.tokenDic, {
      "0xB0": "1 2 3 19 31",
      "0xB1": "1 2 3 15 28",
      "0xB2": "2D 20:20",
      "0x29": "$100",
      "0x46": 20,
    });

    var cdInterval = setInterval(() => {
      let val = IGTMediaElements.tokenDic["0x46"]
      if (val) {
        IGTMediaElements.triggerToken("0x46", val - 1)
      } else {
        clearInterval(cdInterval)
      }
    }, 1000);
  }

  mapToken("0xB0", "assignedNumbers");
  mapToken("0xB1", "winningNumbers");
  mapToken("0xB2", "nextDrawMessage");
  mapToken("0x29", "winningAmount");
  mapToken("0x46", "countdown");
  // mapToken("0x34", "systemTime");
  // '0x4D',    // Universal Screen 1
  // '0x4E',    // Universal Screen 2
  // '0x4F',    // Universal Screen 3

  IGTMediaElements.bonus.getDrawStatus().then((status) => {
    IGTMediaElements.util.log.info(`[ld][model] getDrawStatus [${status.drawStatus}]`);
    model.state.drawStatus = status.drawStatus; //drawTime
    loadPayTable(status.bonusId);
  });

  IGTMediaElements.bonus.subscribeDrawStatus((status) => {
    IGTMediaElements.util.log.info(`[ld][model] subscribeDrawStatus [${status.drawStatus}]`);
    model.state.drawStatus = status.drawStatus; //drawTime
    loadPayTable(status.bonusId);
  });

  function loadPayTable(bonusId) {
    if (bonusId > 0 && Object.keys(model.state.payTable).length === 0) {

      if (IGTMediaElements.isMock) {
        setTimeout(() => {
          model.state.payTable = payTableMockData;
        }, 2000);
      } else {

        IGTMediaElements.getServerAddresses(['payoutconfig.bis.igt.com']).then(function (serverAddresses) {
          var bsId = (bonusId >> 8) & 0xFF;
          var poolId = bonusId & 0xFF;
          var mdsConfigURI = `${serverAddresses['payoutconfig.bis.igt.com']}?bonusId=${bsId}&poolId=${poolId}`;
          IGTMediaElements.util.log.info(`[ld][model] payoutconfig mdsConfigURI [${mdsConfigURI}]`);
          fetch(mdsConfigURI).then(function (res) {
            return res.json();
          }).catch(function (error) {
            IGTMediaElements.util.log.info(`[ld][model] payoutconfig error ${JSON.stringify(error)}`);
          }).
          then(function (res) {
            if (res) {
              model.state.payTable = res;
            } else {
              IGTMediaElements.util.log.info(`[ld][model] payoutconfig response is ${JSON.stringify(res)}`);
            }
          });
        });

      }
    }
  }

  model.start();

  return model;
}

export default declareModel("com.igt.lucky_draw", modelConstructor)
