const callbacks = []

export const IGTMediaElements = {
	subscribeScreenTrigger: function (id, callback) {
		if (!callbacks[id]) {
			callbacks[id] = [];
		}
		callbacks[id].push(callback);
	},
	triggerScreen: function (id) {
		callbacks[id].forEach(function (cb) {
			cb({
				screenId: id,
				displayMessage: "123"
			})
		});
	},
  validatePin: function () {
    return new Promise((resolve) => {
      resolve("valid")
    })
  },
  statuses: {
    pin: {
      validate: {
        0: "waiting",
        1: "valid",
        2: "invalid PIN",
        3: "PIN locked",
        4: "no-response; try again",
        5: "PIN Entry Canceled"
      }
    }
  }
}
