var files = ["abandoned_card_screen.html", "anniversary_screen.html", "apps.html", "attendant_welcome_screen.html", "banner.html", "birthday_screen.html", "card_exit_screen.html", "carded_lucky_coin_non_participation.html", "carded_lucky_draw.html", "cardless_diagnostic.html", "cashless.html", "celebration_award.html", "charViewer.html", "handpay_jackpot.html", "idle_screen.html", "in_session_message_screen_1.html", "intelOffer.html", "invalid_card_screen.html", "jackpot_screen.html", "link_progressive_celebration.html", "link_progressive_mainwin.html", "lucky_coin_reminder.html", "lucky_coin_winner.html", "lucky_draw.html", "lucky_time_celebration.html", "lucky_time_mainwin.html", "magic_coin_non_participation.html", "mjt_session.html", "mjt_session_games_screen.html", "mjt_session_time_screen.html", "multi_property_carded_lucky_coin_non_participation.html", "personal_lucky_coin_status_1.html", "personal_lucky_coin_status_2.html", "personal_lucky_coin_winner.html", "pinpad.html", "pointplay.html", "pool_1_carded_lucky_coin_jackpot.html", "pool_1_carded_lucky_coin_reset.html", "pool_1_magic_coin_jackpot.html", "pool_1_magic_coin_reset.html", "pool_1_multi_property_carded_lucky_coin_jackpot.html", "pool_1_multi_property_carded_lucky_coin_reset.html", "pool_2_carded_lucky_coin_jackpot.html", "pool_2_carded_lucky_coin_reset.html", "pool_2_magic_coin_jackpot.html", "pool_2_magic_coin_reset.html", "pool_2_multi_property_carded_lucky_coin_jackpot.html", "pool_2_multi_property_carded_lucky_coin_reset.html", "pool_3_carded_lucky_coin_jackpot.html", "pool_3_carded_lucky_coin_reset.html", "pool_3_magic_coin_jackpot.html", "pool_3_magic_coin_reset.html", "pool_3_multi_property_carded_lucky_coin_jackpot.html", "pool_3_multi_property_carded_lucky_coin_reset.html", "preliminary_welcome.html", "property_info.html", "return_play_award_screen.html", "session_screen.html", "srp_reminder.html", "srp_tier_reached.html", "srp_toptier_reached.html", "tito_event_screen.html", "universal_bonus_screen_1.html", "vpc_idle_screen.html", "vpc_las.html", "vpc_limit_hit_screen.html", "vpc_pin.html", "vpc_playermessage.html", "vpc_thresholdReached.html", "vpc_uncardedHelp.html", "vpc_zero_limit_screen.html", "w2g_accrual_screen.html", "welcome_screen.html", "xtracredit.html"]
window.onload = function () {
  var app = document.getElementsByTagName("body")[0];
  let root = $("<div id='fastNavigate' v-show='shouldShowFastNavigate' style='display: flex; position: fixed; flex: 1; max-width: 100vw; height: 100vh;  overflow-y: auto; overflow-x: hidden ;background: rgba(26,11,73,.95); z-index:40; '>");
  let p = document.createElement("div");
  p.setAttribute("style", `display:flex; flex-flow: row wrap; width: 100vw; justify-content: space-between;  box-sizing: content-box`);

  $(p).append(`<div  :style="isVertical? 'min-width:90vw; max-width: 90vw; margin: auto;':''" v-for="f in files"  style='color:white; padding: 2px; flex-grow: 1; overflow:hidden; ' ><a style="outline: 1px solid white; color: white; text-decoration: none; font-size:12px; padding:3px;  margin: auto; display: block; " :href="'./'+f">{{f}}</a></div>`)
  $(app).prepend(root)
  root.append(p);

  window.fastNav = $cm("com.igt.tools.FastNavigate", "#fastNavigate", {
    __m5_no_alias__: true,
    data(){
      return {
        shouldShowFastNavigate: false,
        files
      }
    },
    computed:{
      $_isSupportingView(){return true}
    }
  }).start();

// add border
  var isPeering = false;
  var deepArr = ["#app>*,#app>*>*", "#app>*,#app>*>*, #app>*>*>*", "#app>*,#app>*>*, #app>*>*>*, #app>*>*>*>*"];
  var idx = 0;
  window.addEventListener("keydown", e => {
    if(e.keyCode == 68){
      fastNav.vm.shouldShowFastNavigate = true;
    }else if(e.keyCode == 70){
      isPeering = true
      idx = 0;
      $(deepArr[idx]).addClass("border");
    }
    if(isPeering){
      switch (e.keyCode) {
        case 38:
          $(deepArr[idx]).removeClass("border");
          idx++;
          idx = idx >= deepArr.length ? deepArr.length - 1 : idx;
          break;
        case 40:
          $(deepArr[idx]).removeClass("border");
          idx--;
          idx = idx < 0 ? 0: idx;
          break;
        default:
          break;
      }
      $(deepArr[idx]).addClass("border");
    }
  })
  window.addEventListener("keyup", e => {
    if (e.keyCode == 68) {
      fastNav.vm.shouldShowFastNavigate = false;
    } else if (e.keyCode == 70) {
      isPeering = false;
      $(deepArr[deepArr.length-1]).removeClass("border");
    }

  })


}

