function changeFrame(el, from, to) {
    // console.log(rect);
    
    var element = $(el);
    element.css({"transform":"none"});
    if(to == undefined){
        to = from;
        // from = { x: element.position().left, y: element.position().top, width: element.width(), height: element.height() }
        from = { x: element.position().left, y: element.position().top, width: element.outerWidth(), height: element.outerHeight() }
    }
    to = Object.assign({}, { x: from.x, y: from.y,  width: from.width, height: from.height }, to);
    if(from.x == to.x && from.y == to.y && from.width ==to.width && from.height == to.height) return;
    
    var rect = document.querySelector(el).getBoundingClientRect();
    // console.log(from, to);
    var fromX = from.x;
    var fromY = from.y;
    var fromW = from.width;
    var fromH = from.height;

    var toX = to.x;
    var toY = to.y;
    var toW = to.width;
    var toH = to.height;

    var scaleX = toW / fromW;
    var scaleY = toH / fromH;
    var offsetX = toX / scaleX;
    var offsetY = toY / scaleY;
    var matrix = `translate(${-fromX}px, ${-fromY}px) translate(${-fromW/2}px, ${-fromH/2}px) scale(${scaleX}, ${scaleY}) translate(${fromW/2}px, ${fromH/2}px) translate(${offsetX}px, ${offsetY}px)`;
    // console.log(matrix);

    element.css("transform", matrix);
}

export default changeFrame;