import message from '../api/message';

const state = {
  'messagesAll': []
};

const getters = {};

const actions = {
  getMessage({
    dispatch,
    state,
    commit,
    rootState,
    rootGetters
  }) {
    rootState.isFetching = true;
    message.getMessage(rootState.CardID, rootState.EgmLocation).then((data) => {
      if (data.ErrorCode != 0) {
        rootGetters.rootAnalytics.trackException("Error", { Error:data });
        dispatch('addInfo', {
          message: data.ErrorMessage,
          errorCode: data.ErrorCode,
          routertype: "exit"
        }, {
          root: true
        });
      } else {
        commit('setMessage', data.Messages);
      }
      rootState.isFetching = false;
    }).catch((err) => {
      rootGetters.rootAnalytics.trackException("Error", { Error: err.message });
      rootState.isFetching = false;
      dispatch('addInfo', {
        message: err.message,
        routertype: "exit"
      }, {
        root: true
      });
    });
  }
};

const mutations = {
  setMessage(state, message) {
    state.messagesAll = message;
  }
};

export default {
  'namespaced': true,
  state,
  getters,
  actions,
  mutations
};