import { genModel, declareModel,  mapToken } from "m5-content-model"
function modelConstructor(el, modules) {
  var store = {
    state: {
      enabled: true,
      PlayersPreferredName: "",
      transitionName: "",
      transferResult: "",
      DisplayMessage: "",
      toCardAmount: 0,
      toMachineAmount: 0,
      valueOf0X7A: "$0.00",
      valueOf0X7B: "$0.00",
      cashlessBalance: {
        cardCents: 0,
        egmCents: 0,
        minToEgm: 0,
        maxToEgm: 0,
        minFrEgm: 0,
        maxFrEgm: 0,
        maxCardBal: 0,
        egmDenom: 0
      },
      selfDefinedInput: "",
      pinIsValid: true,
      isShowBalance: false
    },
    actions: {
      onClearIntermediaValue: function (context) {
        context.state.transferResult = "";
      },

      onToHome: function (context) {
        getCashlessBalance();
        context.state.transferResult = "";
      },
      transferToCard: function (context, value) {
        context.state.transferResult = "";
        console.log("will transfer", value);
        var trackerI = this.analytics.willTriggerBusiness("initiateTransferFromEgm");
        IGTMediaElements.cashless.initiateTransferFromEgm(value, false, false, false, true, true).then(function (data) {
          trackerI(data);
          if (data.status != 0) {
            context.state.transferResult = "FAIL";
          }
          else {
            context.state.transferResult = "SUCCESS";
          }
          getCashlessBalance();
        });
      },
      transferToMachine: function (context, value) {
        var trackerF = this.analytics.willTriggerBusiness("initiateTransferFromCard");
        IGTMediaElements.cashless.initiateTransferFromCard(value, true).then(function (data) {
          trackerF(data);
          if (data.status != 0) {
            context.state.transferResult = "FAIL";
          }
          else {
            context.state.transferResult = "SUCCESS";
          }
          getCashlessBalance();
        });
      },
    }
  }

  const model = genModel(store, modules, el);
  mapToken("0x7A", "valueOf0X7A");
  mapToken("0x7B", "valueOf0X7B");

  if (IGTMediaElements.isMock) {
    Object.assign(IGTMediaElements, {
      validatePin: function () {
        return new Promise(function (resolve) {
          resolve("valid");
        })
      }

    })
    Object.assign(IGTMediaElements.cashless, {
      data: {
        "cardCents": 10000,
        "egmCents": 20000,
        "minToEgm": 100,
        "maxToEgm": 20000,
        "minFrEgm": 100,
        "maxFrEgm": 20000,
        "maxCardBal": 20000,
        "egmDenom": 5
      }
    })
  }


  function startCashless() {
    console.log("startInit Cashless");

    var tracker = model.analytics.willTriggerBusiness("checkCashlessEnabled");
    IGTMediaElements.cashless.checkCashlessEnabled().then(function (data) {
      tracker(data);
      model.state.enabled = data.enabled;
      if (model.state.enabled) {
        trace("model.enable:", model.state.enabled);
        var trackerV = model.analytics.willTriggerBusiness("validatePin");
        IGTMediaElements.validatePin().then(function (status) {
          trace('pin status:',status);
          trackerV(status);
          if (status === IGTMediaElements.statuses.pin.validate[1]) {
            trace("pin is valid");
            model.state.pinIsValid = true;
            model.state.isShowBalance = true;
          }
          else {
            console.log("Pin is invalid!");
            model.state.pinIsValid = false;
          }
        }).catch(function (status) {
          trace('pin status in catch:',status);
          console.log("can't the get validate pin result!");
          model.state.pinIsValid = false;
        });
      }
    })
  }

  function getCashlessBalance() {
    console.log("getCashless Balance.")

    var trackerC = model.analytics.willTriggerBusiness("getCashlessBalance");
    IGTMediaElements.cashless.getCashlessBalance().then(function (data) {
      trackerC(data);
      model.state.cashlessBalance.cardCents = data.cardCents;
      model.state.cashlessBalance.egmCents = data.egmCents;
      model.state.cashlessBalance.minToEgm = data.minToEgm;
      model.state.cashlessBalance.maxToEgm = data.maxToEgm;
      model.state.cashlessBalance.minFrEgm = data.minFrEgm;
      model.state.cashlessBalance.maxFrEgm = data.maxFrEgm;
      model.state.cashlessBalance.maxCardBal = data.maxCardBal;
      model.state.cashlessBalance.egmDenom = data.egmDenom;
      trace("cashless egmDenom", data.egmDenom);
    });
  }


  mapToken("0x30", "PlayersPreferredName");

  model.watch(() => {
    return [model.state.valueOf0X7A, model.state.valueOf0X7B]
  }, function () {

    model.state.cashlessBalance.cardCents = parseFloat(model.state.valueOf0X7A.replace(/[\$,.]/g, ""));
    var temp = parseFloat(model.state.valueOf0X7B.replace(/[\$,.]/g, ""));
    var result = temp - model.state.cashlessBalance.cardCents;
    trace("0x7B-0x7A=egmCents:", result);
    if (result >= 0)
      model.state.cashlessBalance.egmCents = result;
  }, { immediate: true });

  // model.watch("valueOf0X7A",function (value) {
  //   model.state.cashlessBalance.cardCents = parseFloat(value.replace(/[\$,.]/g, ""));
  // }, { immediate: true });


  // model.watch("valueOf0X7B", function (value) {
  //   var temp = parseFloat(value.replace(/[\$,.]/g, ""));
  //   var result = temp - model.state.cashlessBalance.cardCents;
  //   trace("0x7B-cardCents=egmCents:", result);
  //   if (result >= 0)
  //     model.state.cashlessBalance.egmCents = result;
  // }, { immediate: true });

  model.start();
  startCashless();
  model.watch("isShowBalance", function (value) {
    if (value) {
      console.log("will get cashless balance.");
      getCashlessBalance();
    }
  }, { immediate: true });
  return model;
}


export default declareModel('com.igt.cashless', modelConstructor)
