const clientErrorMapping = {
  '2': 'The system is busy, Please try again later.',
  '1002': 'Player PIN is locked.',
  '1005': 'The player doesn\'t exist.',
  '1101': 'Please exit and go back to refresh available drawings.',
  '1102': 'There are no active drawings at this time.',
  '1201': 'Auto-entry is not available for VIP drawings. Your ticket is automatically entered.',
  '1202': 'Auto-entry is not available at this time. The drawing you have selected is already in progress.',
  '1203': 'Auto-entry is not enabled for this drawing. Please exit and go back to refresh available drawings.',
  '1204': function (autoRedeemDrawingName) {
    return `Auto-entry has already been enabled for ${autoRedeemDrawingName} drawing. Auto-entry can only be enabled on 1 drawing at a time.`;
  },
  '1301': 'Your ticket to the VIP drawing will automatically be entered for you. No further action is needed.',
  '1302': function (checkinStartTime, checkinEndTime) {
    return `Ticket Check-in does not start until ${checkinStartTime}.\nTicket Check-in will end at ${checkinEndTime}.`;
  },
  '1303': function (count, time) {
    return `You have already checked in for the ${count} drawing that is at ${time}. No further action is needed.`;
  },
  '1401': function (drawingDay) {
    return `This drawing is only available on ${drawingDay}.`;
  },
  '1501': 'Number of tickets must be greater than 0.',
  '1502': 'You are not eligible to enter the selected drawing. Please see staff for assistance.',
  '1503': 'You are already entered into the VIP drawing.',
  '1504': 'Tickets cannot be redeemed at this time.',
  '1505': 'You do not have enough available tickets for the current Promotion Award',
  '9999': 'Server internal error. Please check service log for more details.'
}

export default function(errorMessage, errorCode = null) {
  if (errorCode === null || !clientErrorMapping[errorCode]) return errorMessage

  if (typeof (clientErrorMapping[errorCode]) === 'function') {
    const sArr = errorMessage.split('|');
    const argArray = [];
    // 0 is errorCode and 1 is errorMessage so start from 2
    for (let i = 2; i < sArr.length; i++) {
      argArray.push(sArr[i]);
    }
    return clientErrorMapping[errorCode].apply(null, argArray);
  } else {
    return clientErrorMapping[errorCode];
  }
}