
export default {
    actions:{
        reloadConfig(ctx){
            if (ctx.state._isSubModule) return;
            let wasReady = ctx.state.isReady;
            ctx.state.isReady = false;
            IGTMediaElements.contentCache.deleteStateElement ("__url_maps__");
            IGTMediaElements.contentCache.deleteStateElement ("__app_config__");

            ctx.dispatch("getAppConfig");
            ctx.dispatch("getURLMaps");
            ctx.state.isReady = wasReady;
        }
    }
}