import { genModel, declareModel, mapToken } from "m5-content-model"

function modelConstructure(el, modules) {
    var store = {
        state: {
            PlayersPreferredName: ""
        },
        mutations: {},
        getters: {},
        actions: {},
        modules: {}
    }

    const model = genModel(store, modules, el);
    mapToken("0x30","PlayersPreferredName");
    model.start();
    return model;
}

export default declareModel('com.igt.birthday_screen', modelConstructure)