export const IGTMediaElements = {
	activateXtraCreditWithAmount: function(validatePin, value) { //success
		console.log('ncep: using activateXtraCreditWithAmount')
		var data={};

		setTimeout(() => {
			if(value < 0.5){
				data.status = 3;
				data.message = "Extra Credit balance is too low";
				this.pubSub.publish('igt-xc-activated_topic', data);
			}
			else{
				data.status = 1;
				data.message = 'active succeed.';
	
				this.pubSub.publish('igt-xc-activated_topic', data);
				var availableXC = Number(this.tokenDic["0x23"].replace(/[^0-9\.]+/g, "")) - value / 100;
				var activatedXC = Number(this.tokenDic["0x26"].replace(/[^0-9\.]+/g,"")) + value / 100;
	
				this.triggerToken({
					"0x23": '$'+ availableXC.toFixed(2),
					"0x26": '$'+ activatedXC.toFixed(2)
				});
			}
		}, 1500);
	},
	activateXtraCredit: function () { //success
		console.log('ncep: using activateXtraCredit')
		var data = {
			status: 1,
			message: 'Active XC succeeded!'
		};
		this.pubSub.publish('igt-xc-activated_topic', data);
   
		setTimeout(() => {
			var availableXC = Number(this.tokenDic["0x23"].replace(/[^0-9\.]+/g, ""));
			this.triggerToken({
				"0x23": '$0.00',
				"0x26": '$'+ availableXC.toFixed(2)
			});
		}, 1500);
	},
    cancelXtraCredit: function () { //success
		var data = {
			status: 11,
			message: 'Extra Credit in game cycle'
		};

		setTimeout(() => {
			this.pubSub.publish('igt-xc-canceled_topic', data);
        
			var availableXC = Number(this.tokenDic["0x23"].replace(/[^0-9\.]+/g, "")) + Number(this.tokenDic["0x26"].replace(/[^0-9\.]+/g, ""));
			this.triggerToken({
				"0x23": '$'+ availableXC.toFixed(2),
				"0x26": '$0.00'
			});
		}, 1000);
	},
  }