export function customizeMessage (path, rawMessage) {
  const [parent, child] = path.split('.')
  if (child) {
    if (window.messageDefinition && window.messageDefinition[parent]) {
      return window.messageDefinition[parent][child] || rawMessage
    } else {
      return rawMessage
    }
  } else {
    return window.messageDefinition[parent] || rawMessage
  }
}

export function timeoutPromise (ms, promise) {
  return new Promise((resolve, reject) => {
    const timeoutId = setTimeout(() => {
      reject(new Error('promise timeout'))
    }, ms)
    promise.then(
      (res) => {
        clearTimeout(timeoutId)
        resolve(res)
      },
      (err) => {
        clearTimeout(timeoutId)
        reject(err)
      }
    )
  })
}
