import * as util from '../util.js';

export default {
  getMessage(cardID, egmLocation) {
    if (IGTMediaElements.isMock) {
      return Promise.resolve({
        "Messages": ["You Have Been Enrolled in a VIP Drawing! &lt;br/&gt;Test Drawing", "You Have Received 1 Free Entry! &lt;br/&gt;Promo Award 602"],
        "EgmId": "",
        "Success": true,
        "ErrorCode": 0,
        "ErrorMessage": ""
      })
    } else {
      var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/MessagesData`
      IGTMediaElements.util.log.info(`vdm getMessage serverAddress is [${serverAddress}]`)
      console.log(`vdm getMessage serverAddress is [${serverAddress}]`)
      return new Promise((resolve, reject) => {
        var url = `${serverAddress}/?cardId=${cardID}&egmLocation=${egmLocation}`;
        util.timeoutPromise(60000, fetch(url,
          {
            method: "get",
            mode: 'cors'
          })).then((response) => {
            resolve(response.json())
          }).catch((error) => {
            reject(error)
          })
      });
    }
  }
};