import { genModel, declareModel, getURLParameterByName } from "m5-content-model"
var intervalId;
var cachedSrc = null;
function modelConstructure(el, modules) {
  var store = {
    state: {
      qrcode: null,
    },
    mutations: {},
    getters: {},
    actions: {
      getQRCode(context) {
        var cb = this.analytics.willTriggerBusiness("getQRCode", "firstCall");
        IGTMediaElements.ewallet.getQRCode().then(function (value) {
          cb(value);
          context.state.qrcode = value;
          var n = getURLParameterByName("n", value.qrCode) || 0;
          var u = getURLParameterByName("u", value.qrCode) || 0;
          context.state.qrcode.qrCode = `http://sai.eastasia.cloudapp.azure.com/qr?n=${n}&u=${u}`;
          context.state.qrcode.duration = value.duration;
          if (intervalId) {
            clearInterval(intervalId);
          }
          intervalId = setInterval(() => {
            context.state.qrcode.duration--;
            if (context.state.qrcode.duration <= 0) {
              context.state.qrcode = null;
              clearInterval(intervalId);
            }
          }, 1000);
        });
      }
    },
    modules: {}
  }
  const model = genModel(store, modules, el);
  model.start();
  //remove sessionId-* after cards out.
  Object.keys(localStorage).forEach(el=>{if(el.startsWith('sessionId-')){localStorage.removeItem(el)}})
  return model;
}

export default declareModel('com.igt.idle_screen', modelConstructure)
