export class Paging {
    public offerCountPerPage = 7;
    public currentPageIndex = 0;
    public totalPageCount = 0;

    toNextPage() {
        if (!this.isLastPage()) {
          this.currentPageIndex++;
          return true;
        }

        return false;
      }
    
    toPrevPage() {
        if (!this.isFirstPage()) {
            this.currentPageIndex--;
            return true;
        }
        return false;
    }

    isLastPage() {
        return (this.currentPageIndex + 1 >= this.totalPageCount);
    }

    isFirstPage() {
        return this.currentPageIndex <= 0;
    }

    setTotal(count) {
        console.log('page item total count: '+ count)
        this.totalPageCount = Math.ceil(count / this.offerCountPerPage);
        console.log('page count: '+ this.totalPageCount)
        if (this.currentPageIndex + 1 > this.totalPageCount) {
            this.currentPageIndex--;
            if(this.currentPageIndex < 0) {
                this.currentPageIndex = 0;
            }
        }
    }
}