import * as util from '../util.js';

export default {
  ClaimPrizeOnEgm(cardId, egmLocation, ticketId, guid) {
    if (IGTMediaElements.isMock) {
      return Promise.resolve({
        "PlayerClaimPrizeOnEgm": true,
        "EgmId": "", "Success": true,
        "ErrorCode": 0,
        "ErrorMessage": ""
      })
    } else {
      var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/PlayerClaimPrizeOnEgm`;
      return new Promise((resolve, reject) => {
        var reqParam =
        {
          CardId: cardId,
          EgmLocation: egmLocation,
          TicketId: ticketId,
          ServiceMessageId: guid
        }

        util.timeoutPromise(10000, fetch(serverAddress,
          {
            method: 'post',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
            },
            mode: 'cors',
            body: util.encodeURLParams(reqParam)
          })).then(response => {
            return response.json()
          }).then((data) => {
            resolve(data)
          }).catch((error) => {
            reject(error)
          })
      })
    }
  }
};