import { genModel, declareModel } from "m5-content-model"

function modelConstructor(el, modules) {
  var store = {
    state: {
      Password: "",
      StatusKey: "default",
      Status: "default",
      isShown: true,
      isCancelable: true
    },
    mutations: {},
    getters: {

    },
    actions: {
      ValidatePin: function (context) {
        var passwd = context.state.Password;
        var trackerV = this.analytics.willTriggerBusiness("validatePin");
        context.state.isCancelable = false;
        IGTMediaElements.validatePin(passwd).then(function (status) {
          trackerV(status);
          trace("returned validate result:" + status)
          context.state.Status = status;
          context.state.isShown = false;
          context.state.isCancelable = true;
          IGTMediaElements.pinEntryComplete();
        }).catch(function (status) {
          trackerV(status);
          trace("returned validate result:" + status)
          context.state.Status = status;
          context.state.isCancelable = true;
          
        });
      },

      CancelPin: function (context) {
        context.state.isShown = false;
        var cb = this.analytics.willTriggerBusiness("CancelPin");
        IGTMediaElements.validatePin(null, true).catch(d=>{
          cb(d);
        });
        IGTMediaElements.pinEntryComplete();
      }

    },
    modules: {}
  }
  // create model
  const model = genModel(store, modules, el);

  IGTMediaElements.subscribeShowPINEntryScreen(function () {
    model.vm.refreshLanguage();
    model.state.isShown = true;
    trace("current isShown: ", model.state.isShown)
  });


  IGTMediaElements.subscribeHidePINEntryScreen(function () {
    model.state.isShown = false;
    trace("current isShown: ", model.state.isShown)
  });


  model.watch("isShown", function(value){
    if(!value){
      model.state.Password = "";
      model.state.StatusKey = "default";
    }
  })
  model.start();
  if(model.state.isDemo){
    model.state.isShown = true;
  }
  return model;
}



export default declareModel('com.igt.pinpad', modelConstructor)
