import Vue from 'vue'
import Vuex from 'vuex'
import Router from 'vue-router'
import debugPanel from "../feature/DebugPanel"
import set from "lodash/set"
import get from "lodash/get"
import modelVersion from "../supporting/version"
import { getURLParameterByName, injectVendorLibs, merge, timeoutPromise, getURLParams, injectIGTMediaElements, updateGlobalState, typeName, retry, shellWindow } from "../supporting/utils"
import storeComposer from "../supporting/StoreComposer"
import changeFrame from "../feature/FreeTransform"
import template from "../vue/template"
import getDefaultStore from "../vue/store"
import analytics from "../feature/Analytics"
import watchAttr from "../feature/AttributeWatcher"
import config from "../feature/Configuration"
import warningRibbon from "../feature/WarningRibbon"
import values from "object.values"
import help from "../feature/Helper"

if (!Object.values) {
	values.shim();
}


injectVendorLibs();
injectIGTMediaElements();


Vue.use(Vuex);
Vue.use(Router);

Vue.config.productionTip = false;
Vue.config.devtools = false;
Vue.config.ignoredElements = [
	/^igt\-*/, "group"
];

Array.prototype.subtract = function (a) {
	return this.filter(function (i) { return a.indexOf(i) < 0; });
};



var start = function () {
	console.error("model has not been constructed!!")
};
var mapToken = function () {
	console.error("model has not been constructed!!")
}






function genModel(store, customOptions, el, beforeGenerate) {
	let {__m5_is_secondary_model__} = Object.assign({}, store, customOptions); 
	if(typeof __m5_is_secondary_model__ == "undefined"){
		__m5_is_secondary_model__ = window.hasOwnProperty("m");
	}
	if (beforeGenerate && typeof beforeGenerate == "function") {
		beforeGenerate.apply(Vue);
	};
	// var allowDebug = Number(getURLParameterByName("debug")) == 1;
	store = store || {};
	var router;
	if (customOptions) {
		if (customOptions.hasOwnProperty("routes")) {
			if (typeof customOptions.routes){
				router = new Router(
					{ routes: customOptions.routes }
				);
				delete customOptions.routes;
			}
		} else if (customOptions.hasOwnProperty("router")) {
			router = customOptions.router;
			delete customOptions.router;
		}else{
			// router = new Router(
			// 	{ routes:[] }
			// )
		}
	}


	// default store with default states
	var defaultStore = getDefaultStore(el, !__m5_is_secondary_model__);
	if (el && !__m5_is_secondary_model__) {
		$(el).attr("v-on:dblclick", "switchDebug()");
		$(el).prepend("<log v-if=\"this.$root.allowDebug\" ref='log'></log>");
		$(el).prepend("<ul style='text-align: left; position: fixed; margin: 0; top: 0; left: 0; right: 0; z-index: 1000; background: rgba(255, 0, 0 , 0.9); color: white;'  id='warning' v-if='this.$root.isWarningEnabled'><li v-for='m in systemValidationErrorDescriptions'>{{m}}</li></ul>")
	}

	var mergedStore;
	if (el) {
		mergedStore = storeComposer(defaultStore, store, customOptions, {
			state: { namespace: "" },
			getters: {
				isPrimary(){
					return !__m5_is_secondary_model__;
				},
				screenName(state) {
					return state.namespace.substr(state.namespace.lastIndexOf(".") + 1)
				},
				isAnalyticsReady: analytics.getters.isAnalyticsReady
			},
			actions: Object.assign({}, analytics.actions, config.actions)
		});
	} else {
		var emptyCall = () => { };
		mergedStore = storeComposer(defaultStore, store, customOptions, {
			state: { namespace: "" },
			actions: Object.assign({}, analytics.actions, { trackView: emptyCall, trackNavigate: emptyCall }, config.actions)
		});
	}

	var _store = store;
	store = new Vuex.Store(mergedStore);


	var data, computed, watch, methods, mounted, beforeMount, navi;
	if (el) {
		[data, computed, watch, methods, mounted, beforeMount, navi] = template(el, !__m5_is_secondary_model__);
	}
	const vm = new Vue(merge({
		el,
		computed: Object.assign({}, Vuex.mapState(Object.keys(store.state)), Vuex.mapGetters(Object.keys(store.getters)), computed, _store.computed || {}),
		store,
		data: Object.assign({}, data, (function () {
			if (customOptions && customOptions.hasOwnProperty("data")) {
				if (typeof customOptions.data == "function") {
					return customOptions.data()
				} else {
					return customOptions.data
				}
			} else {
				return {};
			}
		})()),
		watch,
		beforeMount,
		router,
		methods
	},
		customOptions));
	store = vm.$store;
	String.prototype.translate = function (language) {
		return vm.$options.filters.translate(this, language);
	}
	String.prototype.changePlaceholder = function (...args) {
		return vm.$options.filters.changePlaceholder(this, ...args)
	}
	String.prototype.keepWhitespace = function (...args) {
		return vm.$options.filters.keepWhitespace(this, ...args)
	}

	function press(el, cb, ...args) {
		var self = this;
		$(el).click(function () {
			self.dispatch("trackPress", { target: el });
			cb.apply(this, args);
		})
	}

	function bindAPIs(target, scope) {
		var self = scope || this;
		Object.keys(target).forEach(function (key) {
			target[key] = target[key].bind(self)
		})
		return target;
	}

	var model = {
		state: store.state,
		// computed: store.getters,
		getters: store.getters,
		watch: function (keypath, callback, options) {
			if (typeof keypath == "function") {
				return store.watch(keypath, callback, options)
			} else {
				return vm.$watch(keypath, callback, options);
			}

		},
		onStart: function (cb) {
			if (store.state.isReady) {
				cb()
			} else {
				store.watch(
					(state) => state.isReady,
					(newValue, oldValue) => {
						cb();
					});
			}
		},
		mapToken: function (token, keypath, rules) {
			rules = Object.assign({ notEmpty: false, animated: false }, rules || {})
			if (get(model.state, keypath) === undefined) {
				function checkNested(obj, keypath) {
					if(!obj || !keypath) return false;
					let idx = keypath.indexOf(".");
					var propertyName, deepKeypath;
					if (idx == -1) {
						propertyName = keypath;
					} else {
						propertyName = keypath.substr(0, idx)
						deepKeypath = keypath.substr(idx + 1)
					}
					var hasKey = obj.hasOwnProperty(propertyName);
					if (hasKey && !deepKeypath) {
						return true;
					} else {
				
						if (hasKey) {
							return checkNested(obj[propertyName], deepKeypath)
						} else {
							return false;
						}
					}
				}
				if(checkNested(model.state, keypath)){
					console.warn(`The initical value for keypath "${keypath}" is undefined, you should define it null instead. Otherwise, the token will be mapped to model.state.ext.${keypath} as an extended property.`);
				}
				Vue.set(vm.ext, keypath, "")
				keypath = "ext." + keypath;
			};
			window.addEventListener("cm_broadcast_token_" + token, function (e) {
				var value = e.detail.value;
				if (value == undefined) return;
				if (rules.notEmpty && String(value).trim().length == 0) return;
				set(model.state, keypath, value)
			});
			IGTMediaElements.subscribeToken(token, function (value) {
				window.dispatchEvent(new CustomEvent("cm_broadcast_token_" + token, { detail: { value } }));
			});

			IGTMediaElements.getCurrentTokenValue(token).then((e) => {
				if (e.token == undefined || e.token.value == undefined) return;
				if (rules.notEmpty && String(e.token.value).trim().length == 0){
					return;
				}	
				set(model.state, keypath, e.token.value);
			});
			if(rules.refresh){
				IGTMediaElements.getTokens([token]);
			}

		},
		unMapToken: function (token, keypath) {
			IGTMediaElements.unsubscribeToken(token);
			if (keypath) {
				Vue.delete(vm.ext, keypath);
			}
		},
		start(emptyCache = false) {
			this.state.isReady = true;
			if (vm.$store == store) {
				if (emptyCache) {
					store.dispatch("reloadConfig");
				} else {
					store.dispatch("getAppConfig");
					store.dispatch("getURLMaps");
				}
			}
			return model;
		},
		updateGlobalState,getURLParameterByName, getURLParams,timeoutPromise,
		retry,
		typeName,
		defineCSSGroup: function(name, rules){
			if(typeName(rules) == "Function"){
				rules = rules.bind(this);
			}
			Vue.set(model.state.$_rootClass, name, rules);
		},
		vm,
		analytics: bindAPIs(Object.assign({}, analytics.apis), vm),
		commit: store.commit.bind(store),
		dispatch: store.dispatch.bind(store),
		watchAttr
	}
	
	if (el) {
		model = Object.assign({}, model, {
			navigate: navi.bind(vm),
			router: vm.$router,
			logger: vm.$refs.log,

			changeFrame,
			press
		})
	} else {
		model.analytics.trackView = model.analytics.trackNaivgation = model.analytics.trackPress = function () { };
	}


	Object.defineProperty(model, "help", {
		value: help.bind(model),
		writable: false
	});
	Object.defineProperty(store, "analytics", {
		value: model.analytics,
		writable: false
	});

	Object.defineProperty(model, "computed", {
		get() {
			console.warn("This API is deprecated, will be removed in next version, Please use `model.getters` instead")
			return store.getters
		}
	})

	Object.defineProperty(model, "raw", {
		value: mergedStore,
		writable: false
	});

	mapToken = model.mapToken;



	if (el) {
		debugPanel(model);
		IGTMediaElements.urlparams.getUrlParams().then(function (params) {
			if (params) {
				vm.$store.state.urlparams = params;
				// Vue.set(vm.$store.state, "urlparams", params);
			}
		});
		// only the main module will subscribe all screen triggers 
		// mapToken('0xFE', "displayMessage", { notEmpty: true });
		// mapToken('0xFF', "displayMessage", { notEmpty: true });
		window.addEventListener("cm_broadcast_subscribeAllScreenTrigger", function (e) {
			let screenTrigger = e.detail.screenTrigger;
			if (screenTrigger) {
				model.state.screenTrigger = screenTrigger;
				model.state.screenId = screenTrigger.screenId || "";
				model.state.displayMessage = screenTrigger.displayMessage || "";
				model.state.messageTokens = screenTrigger.messageTokens;
			}
		});
		IGTMediaElements.subscribeAllScreenTriggers(function (screenTrigger) {
			window.dispatchEvent(new CustomEvent("cm_broadcast_subscribeAllScreenTrigger" , { detail: { screenTrigger } }));
			
		});
		IGTMediaElements.contentCache.getTriggers().then( value => {
			if (model.state.screenId != "") return;
			let len = value && value.length || 0;
			if(len > 0){
				let lastTrigger = value[len-1];
				let screenTrigger = {screenId: lastTrigger.id, displayMessage: lastTrigger.value, messageTokens: lastTrigger.messageTokens};
				window.dispatchEvent(new CustomEvent("cm_broadcast_subscribeAllScreenTrigger" , { detail: { screenTrigger } }));
			}
		});

		window.press = model.press.bind(model);
		window.navigate = model.navigate.bind(model);
		try {
			function registerRootStateUpdate (e) {
				// if the event is sent from ourself, then ignore it.
				// if (e.detail.context == window) return;
				vm.isSyncingData = true;
				for (var key in e.detail.state) {
					Vue.set(model.state.$_global, key, e.detail.state[key]);

				}
				Vue.nextTick(function () {
					vm.isSyncingData = false;
				})
			}
			shellWindow.addEventListener("cm_state_updated", registerRootStateUpdate )
			window.addEventListener("beforeunload", function(){
				console.log("unload window, will remove cm_state_updated listener");
				shellWindow.removeEventListener("cm_state_updated", registerRootStateUpdate)
			})
		}
		catch (error) {
			console.log(error, "window and window's top may have different origin");
		}



	}

	start = function(){
		console.trace("%cThe start api is deprecacted, please use model.start() instead!!", "color: grey; background-color: yellow;");
		model.start();
	}

	mapToken("0x31", "cardId");
	mapToken("0xAF", "floorLocation");
	mapToken("0x10", "machineNumber");
	warningRibbon(model);
	
	var i = 1, modelAlias = "m1"
	while (window.hasOwnProperty(modelAlias)) { i++; modelAlias = "m" + i; };
	Object.defineProperty(global, el && !__m5_is_secondary_model__ && !window.hasOwnProperty("m") ? "m" : modelAlias, {
		value: model,
		writable: false
	})
		

	if (!global.hasOwnProperty("timeoutPromise")) {
		Object.defineProperty(global, "timeoutPromise", {
			value: timeoutPromise,
			writable: false
		})
	}

	model.watch("appConfig", function(value){
		if(value && value.hasOwnProperty("isDemo")){
			model.state.isDemo = value.isDemo;
		}
		var subModules = Object.values(mergedStore.modules);
		subModules.forEach(function(module){
			module.state.appConfig = value;
		})
	},{immediate: true});
	window.addEventListener("beforeunload", function(){
		console.log("will destroy model: ", vm.namespace);
		vm.$destroy();
		// delete model;
	})
	return model;
}


function resolveConflictNamespace(ns, constructor, ext){
	let extendedNamespace = ns;
	if(ext!=0){
		extendedNamespace = `${ns}.~${ext}`
	}
	if(global.hasOwnProperty(extendedNamespace)){
		if(global[extendedNamespace].toString() == constructor.toString()){
			return null
		}else{
			return resolveConflictNamespace(ns, constructor, ++ext);
		}
	}else{
		return extendedNamespace;
	}
}

function declareModel(ns, constructor) {
	if(!global.__all_models__){
		global.__all_models__ = [];
	}
	let resolvedNamespace = resolveConflictNamespace(ns, constructor, 0);
	if(resolvedNamespace === null) return; // the namespace is already defined, and the constructor is the same one
	if(ns != resolvedNamespace){
		console.warn(`Redefine namespace ${ns}, will rename it to ${resolvedNamespace}` );
		ns = resolvedNamespace;
	}

	let wrapper = function wrapper(){
		let model =  constructor.apply(null, Array.from(arguments));
		Object.defineProperty(model, "namespace", {
			value: resolvedNamespace,
			writable: false
		})
		model.state.namespace = resolvedNamespace;
		return model;
	};
	Object.defineProperty(global, ns, {
		value: wrapper,
		writable: false
	});
	wrapper.context = {
		Vue, Router
	}
	global.__all_models__.push(ns);
	return wrapper
}




// utilities

function bindToken(token, block) {
	window.addEventListener("cm_broadcast_token_" + token, function (e) {
		block(e.detail.value);
	});
	IGTMediaElements.getCurrentTokenValue(token).then((e) => {
		if (e.token) block(e.token.value);
	});
	IGTMediaElements.subscribeToken(token, function (value) {
		window.dispatchEvent(new CustomEvent("cm_broadcast_token_" + token, { detail: { value } }));
	});
}



export {
	injectIGTMediaElements,
	genModel,
	declareModel,
	bindToken,
	mapToken,
	start
}

