import * as util from '../util.js';
import data from "../mock/data";

export default {
  getAward(drawingId, cardId, egmLocation) {
    if (IGTMediaElements.isMock) {
      return Promise.resolve(data().award)
    } else {
      var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/PlayerSpecificDrawing`;
      return new Promise((resolve, reject) => {
        var url = `${serverAddress}/?drawingId=${drawingId}&cardId=${cardId}&egmLocation=${egmLocation}`;
        util.timeoutPromise(60000, fetch(url,
          {
            method: "get",
            mode: 'cors'
          })).then((response) => {
            resolve(response.json())
          }).catch((error) => {
            reject(error)
          })
      });
    }
  },
  TicketAutoRedeemSet(drawingId, cardId, egmLocation, autoRedeemSet, ticketsToExclude, guid) {
    if (IGTMediaElements.isMock) {
      return Promise.resolve({
        "AutoRedeemSet": true,
        "EgmId": "",
        "Success": true,
        "ErrorCode": 0,
        "ErrorMessage": ""
      })
    } else {
      var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/TicketAutoRedeemSet`;
      return new Promise((resolve, reject) => {
        var reqParam =
        {
          DrawingId: drawingId,
          CardId: cardId,
          EgmLocation: egmLocation,
          AutoRedeemSet: autoRedeemSet,
          TicketsToExclude: ticketsToExclude,
          ServiceMessageId: guid
        }

        util.timeoutPromise(60000, fetch(serverAddress,
          {
            method: 'post',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
            },
            mode: 'cors',
            body: util.encodeURLParams(reqParam)
          })).then(response => {
            return response.json()
          }).then((data) => {
            resolve(data)
          }).catch((error) => {
            reject(error)
          })
      });
    }
  },
  PlayerDrawingCheckin(drawingId, cardId, egmLocation, guid) {
    if (IGTMediaElements.isMock) {
      return Promise.resolve({
        "PlayerCheckIn": true,
        "EgmId": "",
        "Success": true,
        "ErrorCode": 0,
        "ErrorMessage": ""
      })
    } else {
      var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/PlayerDrawingCheckIn`;
      return new Promise((resolve, reject) => {
        var reqParam =
        {
          DrawingId: drawingId,
          CardId: cardId,
          EgmLocation: egmLocation,
          ServiceMessageId: guid
        }

        util.timeoutPromise(60000, fetch(serverAddress,
          {
            method: 'post',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
            },
            mode: 'cors',
            body: util.encodeURLParams(reqParam)
          })).then(response => {
            return response.json()
          }).then((data) => {
            resolve(data)
          }).catch((error) => {
            reject(error)
          })
      });
    }
  }
};