import {
  getURLParameterByName
} from "m5-content-model"

export function createDemoSpace(el, appName, defaultDemoScenario) {

  const demoSpace = getURLParameterByName('demoSpace')
  const demoScenario = getURLParameterByName('demoScenario')
  /* istanbul ignore if */
  if (IGTMediaElements.isMock || demoSpace || demoScenario) {
    //Object.assign(IGTMediaElements, require('../../../node_modules/m5-content-model/src/mock/MockIGTMediaElements'))
    let scenario = defaultDemoScenario
    if (demoScenario) {
      scenario = demoScenario
    }

    if (demoSpace === "1") {
      const mapping = require(`./${appName}/description`).mapping

      const selector = document.createElement("select")
      selector.style.position = "absolute"
      selector.style.top = 0
      selector.style.left = 0
      selector.style.zIndex = 999

      mapping.forEach(element => {
        const option = document.createElement('option')
        option.value = element.key
        option.textContent = element.value
        if (element.key === scenario) {
          option.selected = "selected"
        }
        selector.appendChild(option)
      })

      selector.onchange = function (event) {
        window.location.href = `${window.location.origin}${window.location.pathname}?demoSpace=1&demoScenario=${event.target.value}`
      }

      $(el)[0].appendChild(selector)
    }

    try {
      Object.assign(IGTMediaElements, require(`./${appName}/${scenario}`).IGTMediaElements)
    } catch (err) {
      console.log(`make sure to store demo cases in specific folder like [./${appName}/${scenario}]`)
    }

    try {
      Object.assign(IGTMediaElements, require(`./${appName}/common`).IGTMediaElements)
    } catch (err) {
      console.log(`ignore this message if don't have any common code in file [./${appName}/common] for demo mode`)
    }
  }
}
