import { genModel, declareModel } from "m5-content-model"

function modelConstructure(el, modules) {
    var store = {
        state: {
            Password: "",
        },
        mutations: {},
        getters: {},
        actions: {
            validatePin() {
                IGTMediaElements.vpc.validatePin(this.state.Password);
            }
        },
        modules: {}
    }

    const model = genModel(store, modules, el);
    model.start();
    return model;
}

export default declareModel('com.igt.vpc_pin', modelConstructure)