import * as util from '../util.js';

export default {
  RedeemTickets(egmLocation, cardId, drawingId, promoAwardId, numberOfTickets, transactionId, guid) {
    if (IGTMediaElements.isMock) {
      return Promise.resolve({
        "CardId": "123456000020000123456",
        "Notes": "",
        "NoCharge": false,
        "PrinterEnabled": false,
        "DrawingId": 8,
        "PromoAwardId": -1,
        "NumberOfTickets": 1,
        "TicketStart": -1,
        "TicketEnd": -1,
        "FreeEntryTicketStart": -13,
        "FreeEntryTicketEnd": -13,
        "FreeEntryLength": 1,
        "TicketLength": 0,
        "EgmId": "TT0101",
        "Success": true,
        "ErrorCode": 0,
        "ErrorMessage": ""
      })
    } else {
      var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/RedeemTicket`;
      return new Promise((resolve, reject) => {
        var reqParam =
        {
          CardId: cardId,
          EgmLocation: egmLocation,
          PromoAwardId: promoAwardId,
          DrawingId: drawingId,
          NumberOfTickets: numberOfTickets,
          TransactionId: transactionId,
          ServiceMessageId: guid
        }
        
        util.timeoutPromise(10000, fetch(serverAddress,
          {
            method: 'post',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded',
            },
            mode: 'cors',
            body: util.encodeURLParams(reqParam)
          }).then(response => {
            return response.json()
          }).then((data) => {
            resolve(data)
          }).catch((error) => {
            reject(error)
          }))
      })
    }
  }
};