import { genModel, declareModel, mapToken, start } from "m5-content-model"


function modelConstructor(el, modules) {
    if (IGTMediaElements.isMock) {
        Object.assign(IGTMediaElements, {
            vpc: {
                vpcplayerdata: {
                    "limits": [
                        {
                            "type": 1,
                            "period": 0,
                            "alertThresholds": [
                                7000,
                                9000
                            ],
                            "threshold": 10000,
                            "session": 2000,
                            "current": 4000,
                            "hit": 0,
                        },
                        {
                            "type": 2,
                            "period": 0,
                            "alertThresholds": [
                                4000,
                                6000
                            ],
                            "threshold": 10000,
                            "session": 3000,
                            "current": 5000,
                            "hit": 0
                        }
                    ],
                    "playerMessage": "This is player message",
                    "limitToShow": 0,
                    "lastTimeLimitSet": 1421972198,
                    "cardId": "12345678901234567890",
                    "haveHostData": false
                },
                subscribePlayerData: function (callback) {
                    callback(this.vpcplayerdata);
                },
                getPlayerData : function () {
                    var self = this;
                    return new Promise(function (resolve) {
                        resolve(self.vpcplayerdata);
                    });
                },
                buttonPress : function( action){
                    console.log("vpc buttonPress ", action);
                }
            }
        })
      }

    var store = {
        state: {
            playerData: {
                "limits": [
                    {
                        "type": 1,
                        "period": 0,
                        "alertThresholds": [],
                        "threshold": 0,
                        "session": 0,
                        "current": 0,
                        "hit": 0,
                    },
                    {
                        "type": 2,
                        "period": 0,
                        "alertThresholds": [],
                        "threshold": 0,
                        "session": 0,
                        "current": 0,
                        "hit": 0
                    }
                ],
                "playerMessage": "",
                "limitToShow": -1,
                "lastTimeLimitSet": 0,
                "cardId": "0",
                "haveHostData": 0
            }
        },
        getters: {
            vpcPlayerData(state){
                let data = {};
                try {
                    data = processVPCData(state.playerData, state);
                }catch(error){
                    let str = '{"Limit1":{"RemainingText":"","TotalText":"",\
                    "LimitSummary":"","SessionText":"","RemainingAmount":"",\
                    "SessionAmount":"","TotalAmount":"","OverlimitText":"","Name":"LOSS","Type":"Loss","Hit":0,"ZeroLimit":false,\
                    "Period":"monthly","Percentage":0},\
                    "Limit2":{"RemainingText":"","TotalText":"","LimitSummary":"",\
                    "SessionText":"","RemainingAmount":"","SessionAmount":"","TotalAmount":"","OverlimitText":"",\
                    "Name":"TIME","Type":"Time","Hit":0,"ZeroLimit":false,"Period":"daily","Percentage":0},"PlayerMessage":"This is player message",\
                    "LimitToShow":0,"TimeSinceLastLimitSet":"Time since you last set a limit","IsCrownLoyalty":true,"HaveHostData":false}'

                    data = JSON.parse(str);
                }

                return data;
            }
        },
        actions: {
            buttonPressed(context, value) {
              IGTMediaElements.vpc.buttonPress(value)
            }
          },
    }
    const model = genModel(store, modules, el);

    let cb = model.analytics.willTriggerBusiness("vpc.getPlayerData");
    IGTMediaElements.vpc.subscribePlayerData(receivePlayerUpdate);

    IGTMediaElements.vpc.getPlayerData().then(function (playerdata) {
        receivePlayerUpdate(playerdata);
    });

    function receivePlayerUpdate(playerdata) {
        cb(playerdata);
        model.state.playerData = playerdata;
    }

    model.start();

    return model;
}

export default declareModel("com.igt.vpc", modelConstructor)