import {
  genModel,
  declareModel,
  getURLParameterByName
} from "m5-content-model";

const viewStateDict = {
  "player": ["keyoff", "confirm", "keyoffFail", "keyoffComplete", "systemPinError"],
  "attendant": ["waitCardin", "confirm", "keyoff", "error", "retry", "notAuthorized", "keyoffComplete", "keyoffFail", "systemPinError"],
  "witness": ["waitCardin", "retry", "error"]
};

function modelConstructor(el, modules) {

  /* istanbul ignore if */
  if (IGTMediaElements.isMock) {
    Object.assign(IGTMediaElements, {
      validatePin: function () {
        return Promise.resolve("valid")
      }
    })
  }

  var store = {
    state: {
      demoSpace: getURLParameterByName("demoSpace"),
      demoScenario: getURLParameterByName("demoScenario"),
      w2gInitData: {
        handpayAmount: 0,
        creditAmount: 0,
        selfServeOption: 0,
        w2gStatus: 1,
        keytoCreditAvailable: true,
        handpayType: 0
      },
      cardId: null,
      playerId: null,
      attendantRetryTimesCounter: 0,
      witnessRetryTimesCounter: 0,
      maxRetryTimes: 3,
      witnessCardIn: false,
      isValidPin: false,
      currentView: "",
      currentViewState: "",
      isCancelled: false,
      systemPinMessage: "",
      transactionID: null,
      viewStateDict
    },
    mutations: {
      CHANGE_VIEW_STATE(state, {
        view,
        viewState
      }) {
        /* istanbul ignore if */
        if (!viewStateDict[view]) {
          console.error(`error view[${view}] is not existed`);
          return;
        }
        /* istanbul ignore if */
        if (!viewStateDict[view].includes(viewState)) {
          console.error(`error viewState[${viewState}] is not existed`);
          return;
        }

        state.currentView = view;
        state.currentViewState = viewState;
      }
    },
    getters: {
      isCardout(state) {
        return state.cardId + "" === "0" && state.playerId + "" === "0";
      }
    },
    actions: {
      cancelW2G(context) {
        IGTMediaElements.w2gAccrual.cancelW2G();
        context.state.isCancelled = true;
      },
      playerPinValid(context) {
        IGTMediaElements.util.log.info("[w2g][model] playerPinValid");
        context.state.isValidPin = true;
        if (context.state.w2gInitData.selfServeOption === 3) {
          context.commit("CHANGE_VIEW_STATE", {
            view: "attendant",
            viewState: "waitCardin"
          });
        } else {
          context.commit("CHANGE_VIEW_STATE", {
            view: "player",
            viewState: "confirm"
          });
        }
      },
      playerKeyoff(context) {
        // {0: "waiting", 1: "valid", 2: "invalid PIN", 3: "PIN locked", 4: "no-response; try again", 5: "PIN Entry Canceled"}
        const resultCode = IGTMediaElements.statuses.pin.validate;
        IGTMediaElements.validatePin(null, null, true).then(status => {
          IGTMediaElements.util.log.info(`[w2g][model] playerKeyoff then ${status}`);
          if (status === resultCode[1]) {
            context.dispatch("playerPinValid");
          }
        }).catch(function (status) {
          IGTMediaElements.util.log.info(`[w2g][model] playerKeyoff catch ${status}`);
          if (status === resultCode[2]) {
            context.dispatch("playerKeyoff");
          } else if ([resultCode[3], resultCode[4], resultCode[5]].includes(status)) {
            context.state.systemPinMessage = status;
            context.commit("CHANGE_VIEW_STATE", {
              view: "player",
              viewState: "systemPinError"
            });
            setTimeout(() => {
              context.dispatch("cancelW2G");
            }, 2000);
          }
        });
      },
      completeW2G(context) {
        IGTMediaElements.w2gAccrual.resetToCreditMeter(context.state.w2gInitData.handpayAmount,
          context.state.w2gInitData.creditAmount, context.state.witnessCardIn,
          context.state.isValidPin).then(resp => {

          context.state.isValidPin = false;
          context.state.witnessCardIn = false;

          if (!resp.success) {
            context.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "waitCardin"
            });
            IGTMediaElements.util.log.error("[w2g][model] completeW2G resetToCreditMeter failed");
            return;
          }

          if (resp.handpayResetCode === 0) {
            if (context.state.witnessCardIn) {
              context.commit("CHANGE_VIEW_STATE", {
                view: "attendant",
                viewState: "keyoffComplete"
              });
            } else {
              if (context.state.w2gInitData.selfServeOption === 1) {
                context.commit("CHANGE_VIEW_STATE", {
                  view: "player",
                  viewState: "keyoffComplete"
                });
              } else {
                context.commit("CHANGE_VIEW_STATE", {
                  view: "attendant",
                  viewState: "keyoffComplete"
                });
              }
            }
          } else if (resp.handpayResetCode === 1) {
            context.commit("CHANGE_VIEW_STATE", {
              view: context.state.currentView,
              viewState: "keyoffFail"
            });
            IGTMediaElements.util.log.error("[w2g][model] keyoff failed unable to reset handpay");
          } else if (resp.handpayResetCode === 2) {
            context.commit("CHANGE_VIEW_STATE", {
              view: context.state.currentView,
              viewState: "keyoffFail"
            });
            IGTMediaElements.util.log.error("[w2g][model] keyoff failed not in handpay");
          } else {
            context.commit("CHANGE_VIEW_STATE", {
              view: context.state.currentView,
              viewState: "keyoffFail"
            });
            IGTMediaElements.util.log.error("[w2g][model] keyoff failed");
          }

        }).catch(function (error) {

          context.state.isValidPin = false;
          context.state.witnessCardIn = false;

          context.commit("CHANGE_VIEW_STATE", {
            view: "attendant",
            viewState: "waitCardin"
          });
          IGTMediaElements.util.log.error(`[w2g][model] completeW2G resetToCreditMeter error [${error}]`);

        });
      },
      attendantRetry(context) {
        context.state.attendantRetryTimesCounter += 1;

        context.commit("CHANGE_VIEW_STATE", {
          view: "attendant",
          viewState: "waitCardin"
        });
      },
      witnessRetry(context) {
        context.state.witnessRetryTimesCounter += 1;

        context.commit("CHANGE_VIEW_STATE", {
          view: "witness",
          viewState: "waitCardin"
        });
      },
      attendantPinValid(context) {
        IGTMediaElements.util.log.info("[w2g][model] attendantPinValid");
        IGTMediaElements.w2gAccrual.isWitnessRequired().then(function (resp) {
          if (resp.required) {
            context.commit("CHANGE_VIEW_STATE", {
              view: "witness",
              viewState: "waitCardin"
            });
          } else {
            context.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "confirm"
            });
          }
        });
      },
      attendantKeyoff(context) {
        // {0: "waiting", 1: "valid", 2: "invalid PIN", 3: "PIN locked", 4: "no-response; try again", 5: "PIN Entry Canceled"}
        const resultCode = IGTMediaElements.statuses.pin.validate;
        IGTMediaElements.validatePin(null, null, true).then((status) => {
          IGTMediaElements.util.log.info(`[w2g][model] attendantKeyoff then ${status}`);
          if (status === resultCode[1]) {
            context.dispatch("attendantPinValid");
          }
        }).catch(function (status) {
          IGTMediaElements.util.log.info(`[w2g][model] attendantKeyoff catch ${status}`);
          if (status === resultCode[2]) {
            context.dispatch("attendantKeyoff");
          } else if ([resultCode[3], resultCode[4], resultCode[5]].includes(status)) {

            context.state.systemPinMessage = status;

            context.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "systemPinError"
            });
          }
        });
      },
      initW2G(context) {

        IGTMediaElements.util.log.info("[w2g][model] call initW2G");

        IGTMediaElements.w2gAccrual.init().then(resp => {

          IGTMediaElements.util.log.info(`[w2g][model] init response[${JSON.stringify(resp)}] transactionID[${context.state.transactionID}]`);

          if (resp.w2gTransactionId == context.state.transactionID) {

            context.state.w2gInitData.handpayAmount = resp.amountInCents;
            context.state.w2gInitData.creditAmount = resp.amountInCredits;
            context.state.w2gInitData.selfServeOption = resp.selfServeOption;
            context.state.w2gInitData.w2gStatus = resp.w2gStatus;
            context.state.w2gInitData.keytoCreditAvailable = resp.keytoCreditAvailable;
            context.state.w2gInitData.handpayType = resp.handpayType;

            if (resp.selfServeOption === 0 || resp.keytoCreditAvailable === false || resp.amountInCents <= 0) {
              context.dispatch("cancelW2G");
              return;
            }

            if (resp.selfServeOption === 1 || resp.selfServeOption === 3) {
              context.commit("CHANGE_VIEW_STATE", {
                view: "player",
                viewState: "keyoff"
              });
            }

            if (resp.selfServeOption === 2) {
              context.commit("CHANGE_VIEW_STATE", {
                view: "attendant",
                viewState: "waitCardin"
              });
            }
          } else {
            // this should not be happended actually, so log it here for sure
            IGTMediaElements.util.log.error(`[w2g][model] W2G transaction id of trigger message [${context.state.transactionID}] is mistached with init response [${resp.w2gTransactionId}]`);
            context.dispatch("cancelW2G");
          }
        }).catch(function (error) {
          IGTMediaElements.util.log.error(`[w2g][model] init w2g error [${error}]`);
          context.dispatch("cancelW2G");
        });
      }
    },
    modules: {}
  };

  const model = genModel(store, modules, el);

  if (!model.vm._isSubModule) {
    require("./demo/initiator").createDemoSpace(el, "w2g", "case1");
  }

  model.mapToken("0x31", "cardId");
  model.mapToken("0x45", "playerId");

  model.watch(() => {
    return [model.state.currentView, model.state.currentViewState];
  }, ([currentView, currentViewState]) => {

    IGTMediaElements.util.log.debug(`[w2g][model] currentView[${currentView}] currentViewState[${currentViewState}]`);

    if (currentView === "attendant" && currentViewState === "waitCardin") {
      IGTMediaElements.w2gAccrual.requestAttendant(model.state.w2gInitData.handpayAmount).then((resp) => {
        if (resp.error) {
          if (model.state.attendantRetryTimesCounter >= model.state.maxRetryTimes) {
            model.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "error"
            });
          } else {
            model.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "retry"
            });
          }
        } else {
          if (resp.success === true) {
            IGTMediaElements.w2gAccrual.setAttendantReturn().then(() => {
              model.commit("CHANGE_VIEW_STATE", {
                view: "attendant",
                viewState: "keyoff"
              });
            }).catch((err) => {
              IGTMediaElements.util.log.error(`[w2g][model] setAttendantReturn error [${err}]`);
              model.commit("CHANGE_VIEW_STATE", {
                view: "attendant",
                viewState: "error"
              });
            });
          } else {
            model.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "notAuthorized"
            });
          }
        }

      }).catch((error) => {
        IGTMediaElements.util.log.error(`[w2g][model] requestAttendant error [${error}]`);
        model.commit("CHANGE_VIEW_STATE", {
          view: "attendant",
          viewState: "error"
        });
      });
    }

    if (currentView === "witness" && currentViewState === "error") {
      setTimeout(() => {
        model.commit("CHANGE_VIEW_STATE", {
          view: "witness",
          viewState: "waitCardin"
        });
      }, 2000);
    }

    if (currentView === "witness" && currentViewState === "waitCardin") {
      IGTMediaElements.w2gAccrual.validateWitness().then(resp => {
        if (resp.error) {
          if (model.state.witnessRetryTimesCounter >= model.state.maxRetryTimes) {
            model.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "error"
            });
          } else {
            model.commit("CHANGE_VIEW_STATE", {
              view: "witness",
              viewState: "retry"
            });
          }
        } else {
          if (resp.success) {
            model.state.witnessCardIn = true;
            model.commit("CHANGE_VIEW_STATE", {
              view: "attendant",
              viewState: "confirm"
            });
          } else {
            model.commit("CHANGE_VIEW_STATE", {
              view: "witness",
              viewState: "error"
            });
          }
        }

      });

    }
  }, {
    //immediate: true
  });

  model.watch(() => {
    return [model.getters.isCardout];
  }, (newVal, oldVal) => {

    IGTMediaElements.util.log.info(`[w2g][model] isCardout newVal[${JSON.stringify(newVal)}] oldVal[${JSON.stringify(oldVal)}]`);

    if (newVal[0] !== oldVal[0] && newVal[0] === true) {

      if (model.state.currentView === "player" && model.state.currentViewState === "keyoff") {
        model.state.systemPinMessage = "PIN closed";
        model.state.isCancelled = true;
        return;
      }

      if (model.state.currentView === "attendant" && ["keyoff", "systemPinError"].includes(model.state.currentViewState)) {
        model.commit("CHANGE_VIEW_STATE", {
          view: "attendant",
          viewState: "waitCardin"
        });
        return;
      }

      if (model.state.currentViewState === "confirm") {
        model.dispatch("cancelW2G");
        return;
      }

    }

  }, {
    // immediate: true
  });

  model.watch((state) => [state.screenId, state.displayMessage], function (after, before) {

    IGTMediaElements.util.log.info(`[w2g][model] before[${before}] after[${after}]`);

    const displayMessage = after[1];

    if (after[0].toLowerCase() === "0x4b" && displayMessage && displayMessage.length > 1) {

      const currentTxnId = displayMessage;

      IGTMediaElements.util.log.info(`[w2g][model] 0x4B transactionID[${model.state.transactionID}] currentTxnId[${currentTxnId}] displayMessage [${displayMessage}]`);
      if (model.state.transactionID !== null && currentTxnId === model.state.transactionID) return;

      if (model.state.transactionID !== null && currentTxnId !== model.state.transactionID) {
        IGTMediaElements.util.log.info(`[w2g][model] new id [${currentTxnId}] old id [${model.state.transactionID}]`);
      }

      model.state.transactionID = currentTxnId;
    }
  }, {
    //immediate: true
  });

  /* istanbul ignore if */
  if (IGTMediaElements.isMock || model.state.demoSpace || model.state.demoScenario) {
    IGTMediaElements.subscribeScreenTrigger("0x4B", ({
      screenId,
      displayMessage
    }) => {
      model.state.screenId = screenId;
      model.state.displayMessage = displayMessage;
    });

    setTimeout(() => {
      IGTMediaElements.triggerScreen("0x4B");
    }, 2000);
  }

  model.start();

  return model;
}

export default declareModel("com.igt.w2g_accrual_screen", modelConstructor);
