require("./pointplay.js");
require("./xtracredit.js");
import { genModel, declareModel, bindToken } from "m5-content-model"

function modelConstructor(el, modules) {
  var store = {
    state: {
      MoneyAll: null,
      MoneyOnCard: null,
      egmDenom: null,
      LastGameWinAmount: null,
      shouldClaimBonus: false,
      shouldShowBannerDisplayMessage: true,
      screenTrggiersThatShouldInitiallyHideDisplayMessage: [],
      BalanceInCard: 0,
      XCInactive: 0,
      PlayerPointsEarnedSession: 0,
      PointsCountdownInMoney: 0,
      PointMultiplierIntegerToken: 1,
      shouldHidePersonalData: false,
      serviceWindowOpen: true,
      bodOn: false,
      InSession: false,
      preferredDisplayType: "banner"
    },
    mutations: {},
    getters: {
      BalanceInCardDollar(state) {
        return parseFloat(Math.floor(state.BalanceInCard) / 100).toFixed(2);
      },
      CreditOnMachine(state) {
        if (state.MoneyAll == null || state.MoneyOnCard == null) {
          return null;
        } else {
          return (parseFloat(state.MoneyAll.replace(/[\$,.]/g, "")) - parseFloat(state.MoneyOnCard.replace(/[\$,.]/g, ""))) / 100;
        }
      }
    },
    actions: {
      toggleServiceWindow,
      toggleBOD(ctx, msg) {
        if (!msg) {
          msg = 'Thank you! Your Drink has been ordered.';
        }
        ctx.state.bodOn = !ctx.state.bodOn;
        toggleHostEvent(0, 0, false, 34, msg, 'HostEventButton');
      }
    },
    modules: {}
  }

  // create model
  const model = genModel(store, modules, el);
  model.start();


  var processWindowState = function (data) {
    console.log("will change service to", data);
    model.state.serviceWindowOpen = data.windowState;
  };

  IGTMediaElements.pubSub.subscribe(IGTMediaElements.WINDOW_STATE_TOPIC, processWindowState);
  let timeoutId;
  function toggleServiceWindow(ctx, refresh) {
    if (timeoutId) {
      clearTimeout(timeoutId);
      timeoutId = undefined;
    }
    timeoutId = setTimeout(() => {
      if (model.state.serviceWindowOpen) {
        model.analytics.willTriggerBusiness("hideWindow");
        if (refresh) {
          IGTMediaElements.showWindow();
        }
        IGTMediaElements.hideWindow();
      } else {
        model.analytics.willTriggerBusiness("showWindow");
        if (refresh) {
          IGTMediaElements.hideWindow();
        }
        IGTMediaElements.showWindow();
      }
    }, 500) // the default timing in Windows is 500 ms (half a second). https://en.wikipedia.org/wiki/Double-click#:~:text=The%20maximum%20delay%20required%20for,basis%20for%20other%20timed%20actions.
  }

  function toggleHostEvent(hostEventId, subEventId, onlineOnly, bonusId, Message, ButtonID) {
    model.analytics.willTriggerBusiness("hostEventButtonPress");
    IGTMediaElements.hostEventButtonPress(hostEventId, subEventId, onlineOnly, bonusId);
  }


  // "0x10,0x11,0x12,0x13,0x14,0x15,0x16,0x17,0x18,0x19,0x1A,0x1B,0x1C,0x1D,0x1E,0x1F,0x20,0x21,0x22,0x23,0x24,0x25,0x26,0x27,0x28,0x29,0x2A,0x2B,0x2C,0x2D,0x2E,0x2F,0x30,0x31,0x32,0x33,0x34,0x35,0x36,0x37,0x38,0x39,0x3A,0x3B,0x3C,0x3D,0x3E,0x40,0x41,0x42,0x43,0x44,0x45,0x46,0x47,0x48,0x49,0x4A,0x4D,0x4E,0x4F,0x50,0x51,0x52,0x53,0x54,0x55,0x56,0x57,0x58,0x59,0x5A,0x5B,0x5C,0x5D,0x5E,0x5F,0x68,0x69,0x6A,0x6B,0x6C,0x6D,0x6E,0x6F,0x78,0x79,0x7A,0x7B,0x7C,0x7D,0x7E,0x7F,0x80,0x95,0x82,0x83,0x84,0x85,0x86,0x87,0x88,0x89,0x8A,0x8B,0x8C,0x8D,0x8E,0x8F,0x90,0x91,0x92,0x93,0x94,0x96,0x97,0x98,0x99,0x9A,0x9B,0x9C,0x9D,0x9E,0x9F,0xA0,0xA1,0xA2,0xA3,0xA4,0xA5,0xA6,0xA7,0xA8,0xA9,0xAA,0xAB,0xAC,0xAD,0xAE".split(",").forEach(st => {
  //   IGTMediaElements.subscribeScreenTrigger(st, function (trigger) {
  //     if (model.state.screenId == '0x47') {
  //       model.state.screenId = trigger.screenId;
  //       model.state.displayMessage = trigger.displayMessage;
  //     }
  //   })
  // })

  model.watch(state => state.screenId, function (n, o) {
    model.state.shouldShowBannerDisplayMessage = !model.state.screenTrggiersThatShouldInitiallyHideDisplayMessage.includes(n);
    if (n == "0x11" || n == "0x14") {
      model.state.InSession = true;
    } else {
      model.state.InSession = false;
    }

  })


  // animate the value change
  // bindToken("0x23", (value) => {
  //   TweenMax.to(model.state, 1, {
  //     XtraCreditInactiveAmount: value,
  //     roundProps: "XtraCreditInactiveAmount"
  //   });
  // });
  bindToken("0x2E", (value) => {
    model.state.CompBalanceMoneyToken = value;
  });
  bindToken("0x1D", (value) => {
    model.state.PointMultiplierIntegerToken = value;
  });
  bindToken("0x7A", (value) => {
    model.state.MoneyOnCard = value;
  });
  bindToken("0x7B", (value) => {
    model.state.MoneyAll = value;
  });

  IGTMediaElements.game.subscribeGameEnd(function (gameEnd) {
    if (gameEnd) {
      IGTMediaElements.util.log.debug("banner content got gameend: " + JSON.stringify(gameEnd));
      model.state.LastGameWinAmount = gameEnd.winAmount;

      IGTMediaElements.game.getGameEnd().then((opt) => { 
        IGTMediaElements.util.log.debug("banner content got gameend options: " + JSON.stringify(opt));
        if (opt.ext && opt.ext.denom) {
          model.state.egmDenom = opt.ext.denom
        } else {
          model.state.egmDenom = NaN
        }
      })
    }
  });

  // model.mapToken("0x72", "PointsCountdownInMoney");
  model.mapToken("0x13", "PlayerPointsEarnedSession");
  // model.mapToken("0x12", "PointsCountdownInMoney", {notEmpty: true});
  model.mapToken("0x75", "PointsCountdownInMoney", { notEmpty: true });

  model.mapToken("0xFE", "displayMessage", { notEmpty: true });
  model.mapToken("0xFF", "displayMessage", { notEmpty: true });

  if (model.state.isDemo) model.state.screenId = "0x11"
  return model;
}



export default declareModel('com.igt.banner', modelConstructor)
