import {genModel, declareModel,mapToken} from "m5-content-model"


function modelConstructor(el, modules) {
  var store = {
    state: {
      PlayersPreferredName: "Player",
      PlayerLevelName: "",
      PlayerLevel: 0
    },
    mutations: {},
    getters: {},
    actions: {},
    modules: {}
  }

  // create model
  const model = genModel(store, modules, el, arguments[2]);

  // bind IGTMediaElements token
  mapToken("0x30", "PlayersPreferredName");
  mapToken("0x11", "PointBalance", {animated: true});


  // visit IGTMediaElements globally
  var hasPatronDataExt = false;
  function getPatronData() {
    IGTMediaElements.getPatronDataExt().then(function (res) {

      if (res.patronDataExt && res.patronDataExt.patronDataExt && res.patronDataExt.patronDataExt.playerRankIndex != undefined) {
        hasPatronDataExt = true;
        model.state.PlayerLevel = res.patronDataExt.patronDataExt.playerRankIndex;
        model.state.PlayerLevelName = res.patronDataExt.patronDataExt.playerRank;
      }

    });
    IGTMediaElements.getPatronData().then(function (data) {
      if (hasPatronDataExt) return;
      model.state.PlayerLevel = data.patronData.patronData.ranking;    
    });
  }

  getPatronData();

  // when model is ready, please call start
  model.start();
  // setTimeout(start, 1000);
  return model;
}



export default declareModel('com.igt.welcome_screen', modelConstructor)