import { genModel, declareModel } from "m5-content-model"

function modelConstructure(el, modules) {
    var store = {
        state: {},
        mutations: {},
        getters: {},
        actions: {
            hideFullScreen() {
                this.analytics.willTriggerBusiness("hideFullScreen");
                IGTMediaElements.hideFullScreen();
            },
            showFullScreen() {
                this.analytics.willTriggerBusiness("showFullScreen");
                IGTMediaElements.showFullScreen();
            }
        },
        modules: {}
    }

    const model = genModel(store, modules, el);
    // IGTMediaElements.pubSub.subscribe(IGTMediaElements.IGT_GAMEPLAY_ACTIVE, function(value){
    //   trace(JSON.stringify(value))
    //   model.dispatch("syncDisplayType");
    // })
    model.start();
    return model;
}

export default declareModel('com.igt.property_info', modelConstructure)
