import { fetch, defineDefaultFilterFunction } from "../supporting/utils"
require("../supporting/Polyfill");

function isObject (object) {
	return object !== null && typeof object === 'object'
  }
  
function isEmptyObject (object) {
	return isObject(object) && Object.keys(object).length < 1
}
var isFetching = false;
if (!window.hasOwnProperty("__m5_localization_dictionary__")) {
	window.__m5_localization_dictionary__ = {};
	if(!isFetching){
		isFetching = true;
		fetch("localization.json")
			.then(function (response) {
				let data = JSON.parse(response);
				window.__m5_localization_dictionary__ = downloadedLocalizationData = data;

				for(let key in localizationMerged){
					if(!localizationMerged.get(key)){
						mergeLocalization(key, data);
					}
				}
					// console.log("%cforceUpdate","color: red;");
					// vm.$forceUpdate();
				
			}).catch(function (error) {
				console.log("can't find localization.json file ", error)
			}).finally(()=>{
				isFetching = false;
			});

	}



}

function mergeLocalization(vm, data){
	for(var key in data){
		Object.assign(data[key], vm.localization[key])
	}
	vm.$set(vm, "localization", data);
	localizationMerged.set(vm,true);
}


let localizationMerged = window.localizationMerged = new WeakMap();
let downloadedLocalizationData;
defineDefaultFilterFunction("translate");
export default (vue) => (msg, lan) => {
	let vm = vue;
	if(!localizationMerged.get(vm) && downloadedLocalizationData){
		mergeLocalization(vm, downloadedLocalizationData);
	}
	var language = lan || vm.language;
	var dictionary = vue.localization;
	if (dictionary.hasOwnProperty(language) && dictionary[language].hasOwnProperty(msg)) {
		return dictionary[language][msg];
	}
	if (vue.autoTranslateCurrencySymbol) {
		msg = msg.replace(/\${2}/g, "__escaped_dollar_sign__").replace(/\$/g, vue.currencySymbol).replace(/__escaped_dollar_sign__/g, "$");
	}
	return msg
}