const state = {
  'drawingAll': []
};

const getters = {
  'drawingsInPromotion': (state, getters, rootState) => {
    rootState.promotions.promotionsAll.forEach((element) => {
      if (element.PromoAwardId == rootState.currentSelectPromoAwardId) {
        state.drawingAll = element;
        rootState.AvailableFreeEntryNumber = element.AvailableFreeEntryNumber
        rootState.TicketsAvailable = element.TicketsAvailable
        rootState.TicketsUsed = element.TicketsUsed
      }
    });
  }
};

const actions = {};

const mutations = {};

export default {
  'namespaced': true,
  state,
  getters,
  actions,
  mutations
};