export const IGTMediaElements = {
    activateXtraCredit: function () { //success
		var data = {
			status: 1,
			message: 'active succeed.'
		};
		this.pubSub.publish('igt-xc-activated_topic', data);
        
    var availableXC = Number(this.tokenDic["0x23"].replace(/[^0-9\.]+/g, ""));
    this.triggerToken({
			"0x23": '$0.00',
			"0x26": '$'+ availableXC.toFixed(2)
		});
	},
    cancelXtraCredit: function () { //fail
		var data = {
			status: 12,
			message: 'Extra Credit in game cycle'
		};
		this.pubSub.publish('igt-xc-canceled_topic', data);
	},
  }
  