import cm from "./index"
import { injectIGTMediaElements, typeName } from "./supporting/utils";


function defaultConstructor(el, customOptions, beforeGenerate) {
  var model = cm.genModel({}, customOptions, el, beforeGenerate)
  return model;

}

let $cm;
window.$cm = $cm = function (factory, ...args) {
  let namespace = factory, ctr = defaultConstructor;
  if (Array.isArray(factory)) {
    namespace = factory[0]
    ctr = function (el, customOptions, beforeGenerate) {
      let model = this.genModel(factory[1], customOptions, el, beforeGenerate)
      return model;
    }
  } else if (typeof factory == "object" && factory != null && factory.hasOwnProperty("namespace")) {
    namespace = factory.namespace;
    if (factory.hasOwnProperty("constructor")) {
      ctr = factory.constructor
    }
  }
  if (typeof namespace !== "string") {
    console.warn("name space is not String");
    return;
  }
  let wrapper = cm.declareModel(namespace, ctr.bind(cm));
  
  if(typeName(args[0]) == "Object"){
    args.unshift(null)
  };

  return wrapper.apply(null, args);
}
Object.defineProperty($cm, "injectIGTMediaElements", {
  get: function(){return injectIGTMediaElements}
});
Object.defineProperty($cm, "genModel", {
  get: function(){ return cm.genModel }
});

export default $cm;
