import { genModel, declareModel, start } from "m5-content-model";

function modelConstructor(el, modules) {
  var store = {
    state: {
      SRPEarnedPoints: null,
      CumulativeAwardAmount: null,
      SrpAwardAmountForNextTier: null,
      SrpThresholdForNextTier: null,
      SrpThresholdForTopTier: null,
      SumofSRPPlayerLevelAwards: null,
      BonusAmount: null,
      AwardAmount: null,
    },
    mutations: {},
    getters: {},
    actions: {},
    modules: {}
  }

  // create model
  const model = genModel(store, modules, el, arguments[2]);

  if (IGTMediaElements.isMock) {
    Object.assign(IGTMediaElements.tokenDic, {
      "0x21": "$50,000,000.00",
    });
  }

  model.mapToken("0x0F", "SRPEarnedPoints");
  model.mapToken("0x21", "CumulativeAwardAmount");
  model.mapToken("0x22", "SrpAwardAmountForNextTier");
  model.mapToken("0x40", "SrpThresholdForNextTier");
  model.mapToken("0x42", "SrpThresholdForTopTier");
  model.mapToken("0x70", "SumofSRPPlayerLevelAwards");
  model.mapToken("0x29", "BonusAmount");
  
  model.watch((state) => [state.screenId, state.displayMessage], function (after, before) {

    IGTMediaElements.util.log.info(`[srp][model] before[${before}] after[${after}]`);

    const displayMessage = after[1];

    if (after[0] === "0x16" && displayMessage && displayMessage.length > 1) {
      IGTMediaElements.util.log.info(`[srp][model] displayMessage [${displayMessage}]`);
      const awardmessage = displayMessage;
        const regex  = /\d+\.\d{2}/;
        var awardamount = awardmessage.match(regex);
        if (awardamount){
            model.state.AwardAmount = model.state.currencySymbol + awardamount;      
            IGTMediaElements.util.log.info(`[srp][model] awardamount: [${model.state.AwardAmount}]`);         
        }
    }
  }, {});
  model.start();
  return model;
}

export default declareModel("com.igt.srp", modelConstructor);
