import cm, {
  getURLParameterByName
} from 'm5-content-model';
import message from './vdm/modules/message';
import promotions from './vdm/modules/promotions';
import drawing from './vdm/modules/drawing';
import award from './vdm/modules/award';
import enteringTickets from './vdm/modules/enteringTickets';
import parseMessage from './vdm/errorMapping';
import claimPrize from './vdm/modules/claimPrize';
import data from './vdm/mock/data'

// trace("The mode is" + process.env.NODE_ENV);

function modelConstructor(el, modules, beforeGen) {
  let model;
  var store = {
    state: {
      // backURL: "http://www.google.com", // default VDM entry
      version: "6.0",
      PlayerName: "", //"Player",
      PlayerId: "",
      Type: "vue",
      CardID: getURLParameterByName("cardId") || "0", //"12345665106192625000",
      EgmLocation: getURLParameterByName("egmLocation") || "", //"25001"
      currentSelectPromoAwardId: -1,
      currentSelectDrawingId: -1,
      AvailableFreeEntryNumber: -1,
      TicketsAvailable: -1,
      TicketsUsed: -1,
      AwardTicketsUsed: -1,
      info: [], //{title, message, routertype, pathname}
      isFetching: false,
      AllTickets: 0,
      enteredTktNum: 0,
      launchURL: getURLParameterByName("launchPage"),
      serverIP: getURLParameterByName("serverIP"),
      serverAddressReady: false,
      dataMap: {
        "/VDMWebPortal/api/vdm/PlayerPromoAwardsData/": data().promotions,
        "/VDMWebPortal/api/vdm/PlayerSpecificDrawing/": data().award
        //todo
      },
      mockAPI: {}
    },
    'mutations': {
      setEnteredTktNum(state, data) {
        state.enteredTktNum = data;
      }
    },
    'getters': {
      guid() {
        function S4() {
          return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
        }
        return S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4();
      },
      transId() {
        return Math.abs(Math.floor(
          Math.random() * (Number.MAX_SAFE_INTEGER - Number.MIN_SAFE_INTEGER + 1) + Number.MIN_SAFE_INTEGER));
      },
      isReadyToGetNewMessage(state) {
        return parseInt(state.CardID) != 0 && state.EgmLocation != "" && state.serverAddressReady;
      },
      ableToInputTickets(state) {
        return state.AvailableFreeEntryNumber + state.TicketsAvailable;
      },
      rootAnalytics() {
        return model.analytics;
      }
    },
    'actions': {
      addInfo(ctx, payload) {
        ctx.state.info.push({
          'message': parseMessage(payload.message, payload.errorCode),
          //'message': parseMessage("1401|ddd|myddd", "1401"),
          'title': payload.title || "Error",
          'routerType': payload.routertype, //"exit", "router"
          'pathName': payload.pathname
        });
      },
      removeInfo(ctx) {
        ctx.state.info.splice(0, 1);
      }
    },
    'modules': {
      message,
      promotions,
      drawing,
      award,
      enteringTickets,
      claimPrize
    }
  };

  if (process.env.NODE_ENV == "development") {
    IGTMediaElements = Object.assign({}, IGTMediaElements, {
      getServerAddresses: function (namespaces) {
        return Promise.resolve({
          "vdm.igt.com": "http://10.222.30.91"
        })
      },
      util: {
        log: {
          info: function (msg) {
            console.log(msg)
          }
        }
      }
    })
  }

  // create model
  model = cm.genModel(store, modules, el, beforeGen);

  window.VDM_END_POINT = `http://${model.state.serverIP}`;
  IGTMediaElements.getServerAddresses(["vdm.igt.com"]).then(serverAddresses => {
    window.VDM_END_POINT = serverAddresses["vdm.igt.com"];
    model.state.serverAddressReady = true;
    IGTMediaElements.util.log.info(`vdm endpoint is ${window.VDM_END_POINT}`)
  })

  // map IGTMediaElements token
  model.mapToken('0x30', 'PlayerName');
  model.mapToken('0x31', 'CardID');
  model.mapToken('0xAF', 'EgmLocation');
  model.mapToken('0x45', 'PlayerId');

  setTimeout(() => {
    if (!model.vm.$store.getters.isReadyToGetNewMessage) {
      model.vm.$store.state.isFetching = false;
      //model.vm.$store.getters.rootAnalytics("Error - get CardId and EgmLocation", {Error:"get CardId and EgmLocation timeout, please reinsert your card"});
      model.dispatch('addInfo', {
        message: "Timeout, please reinsert your card",
        routertype: "exit"
      });
    }
  }, 12000);

  let originFetch = window.originFetch || fetch;
  fetch = function(url, payload) {
    if(IGTMediaElements.isMock){
      let a = new URL(url);
      return new Promise(function (resolve, reject) {
        let res = new Response(JSON.stringify(model.state.mockAPI[a.pathname] || model.state.dataMap[a.pathname]));
        resolve(res);
      });
    } else {
      return originFetch.apply(null, Array.from(arguments));
    }
  }
  // model.router.mode = 'history'
  // when model is ready, please call start
  model.start();
  return model;
}

export default cm.declareModel('com.igt.VDM', modelConstructor);