import {genModel, declareModel} from "m5-content-model"

import {Paging} from "./intelOffer/paging"
import {MediaElementsDelegate} from './intelOffer/media.elements.delegate'
declare function trace(...args) : void;

var delegate = new MediaElementsDelegate();
var paging = new Paging();
var timeout_ms = 12000;


var store = {
  state: {
    OfferList: null,
    ProcessStatus: "INIT",
    CurrentOffer: null,
    ProcessMessage: "",
    ProcessFailReason:"",
    Paging: paging
  },
  getters: {
    CurrentPageOffers: state => {
      if(state.OfferList == null) return [];

      var start = state.Paging.currentPageIndex * state.Paging.offerCountPerPage;
      if(state.Paging.currentPageIndex == state.Paging.totalPageCount){
        return state.OfferList.slice(start, state.Paging.totalPageCount);
      }else if(state.Paging.currentPageIndex<state.Paging.totalPageCount){
        return state.OfferList.slice(start, start + state.Paging.offerCountPerPage);
      } else{
        console.log('invalid paging access..');
        return [];
      }
    },
    Orientation: (state, getters) => {
      var ratio = getters.contentWidth/getters.contentHeight;
      if( ratio <1){
        return 'portrait'
      } else if(ratio > 1 && ratio<5){
        return 'landscape'
      } else{
        return 'landscape_narrow'
      }
    }
  },
  actions: {
    RedeemOffer: function (context, AwardId) {
      var self = this;
      self.state.ProcessStatus = 'INPROGRESS';
      self.state.ProcessMessage = 'REDEEM IN PROGRESS';
      var trackerR = self.analytics.willTriggerBusiness("RedeemOffer", AwardId);
      delegate.getOfferRedeemUrl(AwardId).then(function(url){
        return window["jQuery"].ajax({url, 
          type: 'post',
          timeout: timeout_ms
        }).then(function(status){
          trackerR(status);
          if(status.success){
            self.state.OfferList.filter(offer=>{return offer.sourceAwardId == AwardId}).map(offer=>{offer.status =1});
            self.state.ProcessStatus = 'SUCCEEDED';
            self.state.ProcessMessage = 'REDEEM SUCCEEDED';
          }else{
            self.state.OfferList.filter(offer=>{return offer.sourceAwardId == AwardId}).map(offer=>{offer.status =2});
            self.state.ProcessStatus = 'FAILED';
            self.state.ProcessFailReason = status.errorReason;
            self.state.ProcessMessage = 'REDEEM FAILED';
          }
        }).catch(function(jqXHR, textStatus, errorThrown){
          self.state.ProcessStatus = 'FAILED';
          self.state.ProcessFailReason = textStatus;
          self.state.ProcessMessage = 'REDEEM FAILED';
          trace("failed to redeem offer:", url, "due to ", self.state.ProcessFailReason,"!");
      });
      });
    },
    RejectOffer: function (context, AwardId) {
      var self = this;
      self.state.ProcessStatus = 'INPROGRESS';
      self.state.ProcessMessage = 'REJECT IN PROGRESS';
      var trackerR = self.analytics.willTriggerBusiness("RejectOffer", AwardId);
      delegate.getOfferVoidUrl(AwardId).then(function(url){        
        return window["jQuery"].ajax({url, 
          type: 'post',
          timeout: timeout_ms
        }).then(function(status){
          trackerR(status)
          if(status.success){
            self.state.OfferList.filter(offer=>{return offer.sourceAwardId == AwardId}).map(offer=>{offer.status =1});
            self.state.OfferList.splice(self.state.OfferList.findIndex(offer=>{return offer.sourceAwardId == AwardId}), 1);
            paging.setTotal(self.state.OfferList.length);
            self.state.ProcessStatus = 'SUCCEEDED';
            self.state.ProcessMessage = 'REJECT SUCCEEDED';
          }else{
            self.state.ProcessStatus = 'FAILED';
            self.state.ProcessFailReason = status.errorReason;
            self.state.ProcessMessage = 'REJECT FAILED';
          }          
        }).catch(function(jqXHR, textStatus, errorThrown){
            self.state.ProcessStatus = 'FAILED';
            self.state.ProcessFailReason = textStatus;
            self.state.ProcessMessage = 'REJECT FAILED';
            trace("failed to reject offer:", url, "due to ", self.state.ProcessFailReason,"!");
        });
      });
    },
    PrepareOfferList: function(context, options){
      var self = this;
      
      var timestamp = null;
      var latestOfferForDays = 30;
      var nonRedeemed = "no";
      if(options){
        timestamp = options.timestamp;
        latestOfferForDays = options.latestOfferForDays || ( self.state.appConfig["com.igt.intelOffer"] &&  self.state.appConfig["com.igt.intelOffer"].latestOfferForDays);
        nonRedeemed = options.nonRedeemed || (self.state.appConfig["com.igt.intelOffer"] &&  self.state.appConfig["com.igt.intelOffer"].nonRedeemed);
      }
      var trackerP = self.analytics.willTriggerBusiness("PrepareOfferList", timestamp);
      delegate.getOfferListUrl(timestamp).then(function(url){
        if(timestamp == null){
          url = url.substr(0, url.indexOf('&'));
        }
        if(latestOfferForDays != null){
          url = url + "&latestOfferForDays=" +latestOfferForDays;
        }
        if(nonRedeemed != null){
          url = url + "&nonRedeemed=" +nonRedeemed;
        }
        trace('offer list url: '+ url);
        console.log('offer list url: '+ url);
        return window["jQuery"].ajax({
          url: url,
          type: 'get',
          timeout: timeout_ms
        }).then(function(data){
          trackerP();
          self.state.ProcessStatus = 'FETCHSUCCEEDED';
          self.state.OfferList = data['offerList'];
          self.state.OfferList.sort((a,b)=>{return b.createDateTime - a.createDateTime});
          self.state.Paging.setTotal(self.state.OfferList.length);    
        }).catch(function(jqXHR, textStatus, errorThrown) {
          self.state.OfferList = null;
          self.state.ProcessStatus = 'FETCHFAILED';
          self.state.ProcessFailReason = textStatus;          
          trace("failed to fetch offers from server:", url, "due to ", self.state.ProcessFailReason,"!");
        });
        
      });
    }
  },
  mutations: {
    SetCurrentOffer (state, offer) {
      state.CurrentOffer = offer;
    }
  }
}

var storeMock = {
  state: store.state,
  actions: {
    RedeemOffer: function (context, AwardId) {
      var self = this;
      self.state.ProcessStatus = 'INPROGRESS';
      self.state.ProcessMessage = 'REDEEM IN PROGRESS';
      return new Promise(function(resolve){
        setTimeout(function() {
          if (Math.floor(Math.random() * 10) > 2) {
            //succeed
            self.state.OfferList.filter(offer=>{return offer.sourceAwardId == AwardId}).map(offer=>{offer.status =1});
            self.state.ProcessStatus = 'SUCCEEDED';
            self.state.ProcessMessage = 'REDEEM SUCCEEDED';
            resolve({success: true, errorCode: null, errorReason: null});
          } else {
            //failed
            self.state.OfferList.filter(offer=>{return offer.sourceAwardId == AwardId}).map(offer=>{offer.status =2});
            self.state.ProcessStatus = 'FAILED';
            self.state.ProcessMessage = 'REDEEM FAILED';
            self.state.ProcessFailReason = "the offer is already redeemed";
            resolve({success: false, errorCode: "OFFER-REDEEMED", errorReason: "the offer is already redeemed"});
          }
        }, 1500);
      });
    },
    RejectOffer: function (context, AwardId) {
      var self = this;
      self.state.ProcessStatus = 'INPROGRESS';
      self.state.ProcessMessage = 'REJECT IN PROGRESS';
      return new Promise(function(resolve){
        setTimeout(function() {
          if (Math.floor(Math.random() * 10) > 2) {
            //succeed
            self.state.OfferList.filter(offer=>{return offer.sourceAwardId == AwardId}).map(offer=>{offer.status =1});
            self.state.OfferList.splice(self.state.OfferList.findIndex(offer=>{return offer.sourceAwardId == AwardId}), 1);
            paging.setTotal(self.state.OfferList.length);
            self.state.ProcessStatus = 'SUCCEEDED';
            self.state.ProcessMessage = 'REJECT SUCCEEDED';
            resolve({success: true, errorCode: null, errorReason: null});
          } else {
            //failed
            self.state.ProcessStatus = 'FAILED';
            self.state.ProcessMessage = 'REJECT FAILED';
            self.state.ProcessFailReason = "the offer is already rejected";
            resolve({success: false, errorCode: "OFFER-REJECT", errorReason: self.ProcessFailReason});
          }
        }, 1500);
      });
    },
    PrepareOfferList: function(context, timestamp){
      var self = this;
      return fetch('./assets/intel-offers.json').then(function(response){
        console.log(response);
        return response.json();
      }).then(function(data){
        self.state.ProcessStatus = 'FETCHSUCCEEDED';
        self.state.OfferList = data['offerList'];
        self.state.OfferList.sort((a,b)=>{return b.createDateTime - a.createDateTime});
        self.state.Paging.setTotal(self.state.OfferList.length);    
      });
      
    }
  },
  getters: store.getters,
  mutations: store.mutations
}

function getParameter(name) {
  let params = location.search.split(name + '=')[1];
  if (params === undefined) {
    return "";
  }
  let values = params.split('&');
  if (values.length > 0) {
    return values[0];
  }
  return "";
}

var constrctor = function modelConstructor(el, modules, beforeGen) {
  var isDemo = parseInt(getParameter('demo')) == 1;
  if(isDemo){
    return intelOfferInitialization(el, modules,storeMock,beforeGen);
  }else{
    return intelOfferInitialization(el, modules,store,beforeGen);
  }
}


function intelOfferInitialization(el, modules, store, beforeGen){
  if(modules != null ){
    paging.offerCountPerPage = modules.OfferCountPerPage||paging.offerCountPerPage;
    paging.currentPageIndex = modules.CurrentPageIndex||paging.currentPageIndex;    
  }  
  const model = genModel(store, modules, el, beforeGen);
  model.start();      
  
  return model;
}

declareModel('com.igt.intelOffer', constrctor);
