import cm, {
  getURLParameterByName
} from 'm5-content-model'

// IGTMediaElements.util.log.info(`rr-message-model page load`)

function modelConstructor (el, modules, beforeGen) {
  var store = {
    state: {
      demo: getURLParameterByName('demo'),
      // playerName: "",
      message: '',
      currentTheme: '',
      promoName: undefined,
      error: ''
    },
    getters: {
      getPromotionTheme (state) {
        return promotionName => {
          const result = state.RR_config.themePromotionMap.find((t) => {
            return t.promotionName === promotionName
          })
          return result ? result.theme : state.RR_config.themeList[0]
        }
      }
    }
  }

  const model = cm.genModel(store, modules, el, beforeGen)

  // model.mapToken("0x30", "playerName")

  let serviceWindowOpen = false
  IGTMediaElements.pubSub.subscribe(IGTMediaElements.WINDOW_STATE_TOPIC, data => {
    IGTMediaElements.util.log.debug(`rr-message-model windowState[${data.windowState}]`)
    serviceWindowOpen = data.windowState
  });

  if (model.state.demo || process.env.NODE_ENV === 'development') {
    setTimeout(() => {
      IGTMediaElements.triggerScreen("0x68")
    }, 1000)
  }

  model.start(true)

  model.watch('appConfig', (data) => {
    model.state.RR_config = data['com.igt.randomriches']
    IGTMediaElements.util.log.info('rr-message-model load config')
  })

  model.watch(() => {
    return [model.state.appConfig, model.state.promoName, model.state.RR_config, model.state.isReady]
  }, ([appConfig, promoName, RR_config]) => {
    if (!appConfig || !promoName || !RR_config) {
      if (model.state.isReady) {
        model.state.isReady = false
      }
      return
    }
    
    model.state.currentTheme = model.getters.getPromotionTheme(model.state.promoName)

    IGTMediaElements.util.log.info(`rr-message-model promoName[${model.state.promoName}] currentTheme[${model.state.currentTheme}]`)

    if (!model.state.isReady) {
      setTimeout(function () {
        model.state.isReady = true
      }, 50)
    }
  }, {
    immediate: true
  })

  model.watch('screenTrigger', (message) => {
    if(['0x68', '0x69', '0x6a', '0x6b', '0x6c', '0x6d', '0x6e', '0x6f'].includes(message.screenId.toLowerCase())) {
      
      IGTMediaElements.util.log.info(`rr-message-model screenTrigger[${message.screenId}] rr service configured message[${JSON.stringify(message)}]`)

      let dm
      if(message.displayMessage && typeof message.displayMessage.split === 'function') {
        dm = message.displayMessage
      } else {
        dm = message // used for M5 5.2
      }

      if(model.state.demo || process.env.NODE_ENV === 'development') {
        dm = " Mmessage |-| promo 1"
      }

      let [msg, pName] = dm.trim().split('|-|')

      if (msg[0] !== 'M') return

      msg = msg.substring(1)

      if (!serviceWindowOpen) {
        IGTMediaElements.showWindow()
      }

      model.state.promoName = pName && pName.trim()
      if (!pName) {
        IGTMediaElements.util.log.error('rr-message-model promotion name could be truncated since display message configued too long, so use defalut theme instead')
        model.state.promoName = 'notexisted'
      }

      model.state.message = msg.replace(/\|-$/, '').replace(/\|$/, '').trim() // with pattern |-| have to replace |- to empty and then replace | to empty

      IGTMediaElements.util.log.info(`rr-message-model rr service configured message[${dm}] promoName[${model.state.promoName}] display message[${model.state.message}]`)
    }
  })

  return model
}

export default cm.declareModel('com.igt.RR.message', modelConstructor)
