	subscribeToken: function (id, cb) {
		var dic = this.tokenDic;
		if (!this.callbacks[id]) {
			this.callbacks[id] = [];
		}
		this.callbacks[id].push(cb);
		setTimeout(function () {
			cb(dic[id]);
		}, 0);
	},

	unsubscribeToken(id) {
		var dic = this.tokenDic;
		// delete dic[id];
		if (this.callbacks[id]) {
			console.log("will delete %s callback ", id, this.callbacks[id]);
			delete this.callbacks[id];
		}
	},
	validatePin: function (password, canceled) {
		return new Promise(function (resolve, reject) {
			if (canceled == true) {
				console.log("PIN Canceled by player");
				reject("PIN Entry Canceled");
			}
			if (password == "1234") {
				console.log("password is right");
				resolve("valid");
			} else if (password == "000") {
				reject("waiting");
			} else if (password == "222") {
				console.log("invalid PIN");
				reject("invalid PIN");
			} else if (password == "333") {
				console.log("PIN locked");
				reject("PIN locked");
			} else if (password == "444") {
				reject("no-response; try again")
			} else {
				resolve("valid");
			}
		});
	},

