import * as util from '../util.js';
import data from "../mock/data";

export default {
  getPromotionsAll(cardID, egmLocation) {
    var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/PlayerPromoAwardsData`;
    IGTMediaElements.util.log.info(`vdm getPromotionsAll serverAddress is [${serverAddress}]`)
    console.log(`vdm getPromotionsAll serverAddress is [${serverAddress}]`);
    return new Promise((resolve, reject) => {
      var url = `${serverAddress}/?cardId=${cardID}&egmLocation=${egmLocation}`;
      util.timeoutPromise(60000, fetch(url,
        {
          method: 'get',
          mode: 'cors'
        })).then((response) => {
          resolve(response.json())
        }).catch((error) => {
          reject(error)
        })
    }
    )
  },
  batchTicketCheckIn(cardID, egmLocation, drawingIdList, guid) {
    if (IGTMediaElements.isMock) {
      return Promise.resolve({
        "PlayerCheckIn": true,
        "EgmId": "",
        "Success": true,
        "ErrorCode": 0,
        "ErrorMessage": ""
      })
    } else {
      var serverAddress = `${VDM_END_POINT}/VDMWebPortal/api/vdm/PlayerDrawingCheckIn`;
      return new Promise((resolve, reject) => {
        const requests = drawingIdList.map(id => {
          var reqParam =
          {
            CardId: cardID,
            egmLocation: egmLocation,
            DrawingId: id,
            ServiceMessageId: guid
          }

          return util.timeoutPromise(10000, fetch(serverAddress,
            {
              method: 'post',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
              },
              mode: 'cors',
              body: util.encodeURLParams(reqParam)
            }));
        })
        Promise.all(requests).then(response => {
          var res = [];
          response.forEach(element => {
            element.json().then((data) => {
              res.push(data)
            })
          });
          resolve(res);
        }).catch((error) => {
          reject(error)
        })
      });
    }
  }
};