var helpStyles = "font-size: 32px; border: double 1px black; border-left-width: 0; border-right-width: 0;"

export default function(arg) {
    let model = this;
    let mergedStore = model.raw;
    arg = arg || "";
    switch (arg.toLowerCase()) {
        case "state":
            console.log("%c                         " + arg + "                         ", helpStyles);
            console.log("The properties that represents the %cbusiness state", "color: red; border: solid 1px red; border-radius: 5px");
            console.log("You can access it by using %c m.state.STATE_NAME ", 'background: green; color: white; display: block; ', "to get the model business state");
            var s = Object.assign({}, this.state);
            // delete s._gsTweenID;
            delete s.urlMaps;
            delete s.appConfig;
            delete s.urlparams;
            delete s.version;
            delete s.frameworkVersion;
            delete s.contentVersion;
            delete s.isDemo;
            delete s.isReady;
            delete s.namespace;
            delete s.isChangingDisplayType;
            console.log(JSON.stringify(s, null, 2));
            break;
        case "getters":
            console.log("%c                         " + arg + "                         ", helpStyles);
            console.log("The %cread only", "color: red; border: solid 1px red; border-radius: 5px", "properties in the model.");
            console.log("You can access it by using %c m.getters.GETTER_NAME ", 'background: green; color: white; display: block; ', "to get the model readonly/calculated value");
            var g = Object.assign({}, this.getters);
            delete g.apps;
            delete g.navigationMapping;
            delete g.isAnalyticsReady;
            console.log(JSON.stringify(g, null, 2));
            break;
        case "action":
            console.log("%c                         " + arg + "                         ", helpStyles);
            console.log("Action represents the command names defined in model object.");
            console.log("You can use %c m.dispatch(ACTION_NAME, PAYLOAD_OBJECT) ", 'background: green; color: white; display: block; ', "to drive the model to run it's business logic");
            console.table(Object.keys(model.vm.$store._actions).subtract(["trackView", "trackPress", "trackNavigate", "trackException", "willTriggerBusiness", "didTriggerBusiness"]).map(f => { return { name: f, "call with PAYLOAD_OBJECT": mergedStore.actions.hasOwnProperty(f) ? mergedStore.actions[f].length > 1 : "unkown" } }))
            break;
        case "data":
            console.log("%c                         " + arg + "                         ", helpStyles);
            console.log("Data object represents the %cui logic properties", 'background: green; color: white; display: block; ', "\nThe view state need to be stored here.");
            if (this.vm) {
                console.log(JSON.stringify(this.vm._data, null, 2))
            } else {
                console.log("The model is a not mounted or is a submodule. no view data.")
            }
            break;
        default:
            console.log("Please type %c m.help('state'|'getters'|'action'|'data')", 'background: green; color: white; display: block; ', "for the detail")

    }
}