import cm, {
  getURLParameterByName
} from 'm5-content-model';
import * as serviceAPI from './ireserve/serverAPI';
import * as mockData from './ireserve/mock-data';

if (IGTMediaElements.isMock) {
  IGTMediaElements = Object.assign({}, IGTMediaElements, {
    hostEventButtonPress: function () {
      //3, 1, false, 0
      console.log("press host event button");
      var willExit = !arguments[1];
      if (willExit) {
        // window.location.href = "./index.html"
      } else {
        if (!window.location.search) {
          window.location.href = "./ireserve.html?runLocal=1"
        } else if (!window.location.search.includes('runLocal=')) {
          window.location.href = `./ireserve.html${window.location.search}&runLocal=1`
        }
      }
    }
  });
}

IGTMediaElements.util.log.info("[ireserve][model] model loaded")

function modelConstructor(el, modules, beforeGen) {
  var store = {
    state: {
      runLocal: getURLParameterByName('runLocal'),
      serviceAddress: '',
      machineNumber: '',
      cardId: null,
      playerId: null,
      cardType: '',
      isAttendant: null,
      options: [],
      showPin: null,
      isLocked: null,
      shouldShowCountDown: null,
      RemainedSeconds: null,
      LockingMessage: null,
      info: {
        title: '',
        message: ''
      },
      lockRetryCount: 5,
      dataMap: {
        // normal case
        "/iReserveWebPortal/api/iReserve/RetrieveOperatorInfo": mockData.operationInfo,
        "/iReserveWebPortal/api/iReserve/RetrieveEGMInfo": mockData.egmState_locked_with_countdown,
        "/iReserveWebPortal/api/iReserve/ReserveEGM": mockData.lockResponse_with_countdown,
        "/iReserveWebPortal/api/iReserve/UnReserveEGM": mockData.unlockResponse_success

        // // show pin
        // "/iReserveWebPortal/api/iReserve/RetrieveOperatorInfo": mockData.attendant_OperationInfo,
        // "/iReserveWebPortal/api/iReserve/RetrieveEGMInfo": mockData.egmState_unlocked,
        // "/iReserveWebPortal/api/iReserve/ReserveEGM": mockData.lockResponse_with_countdown,
        // "/iReserveWebPortal/api/iReserve/UnReserveEGM": mockData.unlockResponse_pinpad
      },
      mockAPI: {}
    },
    getters: {
      validServicePlayerId(state) {
        return state.playerId + "" === "-2" ? 0 : state.playerId;
      },
      showLockCountDown(state) {
        return state.shouldShowCountDown;
      },
      lockMessage(state) {
        return state.LockingMessage ? state.LockingMessage.replace('\r\n', '<br>') : "Please see staff for assistance.";
      },
      lockRemainingSeconds(state) {
        return state.RemainedSeconds;
      },
      isCardout(state) {
        state.showPin = false; // to make sure showPin watcher works in skin when attendent cards out without clicking cancel or enter button
        IGTMediaElements.util.log.debug(`[ireserve][model] isCardout [${state.cardId === '0' && state.playerId === '0'}]`);
        return state.cardId === '0' && state.playerId === '0';
      },
      isCardin(state) {
        IGTMediaElements.util.log.debug(`[ireserve][model] isCardin [${state.cardId && state.cardId !== '0' && state.playerId && state.playerId !== '0'}]`);
        return state.cardId && state.cardId !== '0' && state.playerId && state.playerId !== '0';
      },
      sessionType(state) {
        // NONE: 0x00,
        // UNKNOWN: 0x01,
        // PLAYER: 0x02,
        // VIP: 0x03,
        // DROP_CARD: 0x04,
        // ATTENDANT: 0x05,
        // SUPERVISOR: 0x06,
        // WITNESS: 0x07,
        // UNREG_PLAYER: 0x08,
        // REG_PLAY: 0x09,
        // REG_VIP_PLAYER: 0x0A
        var playCardTypes = [2, 3, 9, 10];
        var attendantCardTypes = [5, 6];
        var ct = Number(state.cardType);
        return playCardTypes.includes(ct) ? 1 : attendantCardTypes.includes(ct) ? 2 : 0;
      },
      endPoint(state) {
        return `${state.serviceAddress}/iReserveWebPortal/api/iReserve`;
      },
      shouldLock(state) {
        trace(`[m] shouldLock isLocked[${state.isLocked}] screenId[${state.screenId}]`)
        return state.isLocked && state.screenId.toLowerCase() === "0x4c"
      }
    },
    mutations: {},
    actions: {
      getOptions(ctx) {
        IGTMediaElements.util.log.info(`[ireserve][model] getOptions for machine[${ctx.state.machineNumber}]`);

        serviceAPI.getOptionList(ctx.getters.endPoint, {
          cardId: ctx.state.cardId,
          machineNumber: ctx.state.machineNumber,
          playerId: ctx.getters.validServicePlayerId,
          sessionType: ctx.getters.sessionType
        }, (response) => {
          ctx.state.options = response.Periods;
        }, (error) => {
          IGTMediaElements.util.log.info(`[ireserve][model] getOptions error [${JSON.stringify(error)}]`);
          ctx.state.info.title = error.title;
          ctx.state.info.message = error.message;
        });
      },
      getState(ctx) {
        serviceAPI.getState(ctx.getters.endPoint, {
          machineNumber: ctx.state.machineNumber
        }, (data) => {
          IGTMediaElements.util.log.info(`[ireserve][model] getstate data ${JSON.stringify(data)}`);

          if (data.IsLocked) {

            if (data.RemainedSeconds <= 0) {

              IGTMediaElements.util.log.info('[ireserve][model] lock one day in getState method');

              IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', 86400).then(function () {
                ctx.dispatch('keep0x4c');
                ctx.state.isLocked = true;
                ctx.state.shouldShowCountDown = false;
                ctx.state.LockingMessage = "";
                ctx.state.RemainedSeconds = 86400;
              })

            } else {

              IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', data.RemainedSeconds).then(function () {
                ctx.dispatch('keep0x4c');
                ctx.state.isLocked = true;
                ctx.state.shouldShowCountDown = data.IsCountdownTimerShown;
                ctx.state.LockingMessage = data.LockingMessage;
                ctx.state.RemainedSeconds = data.RemainedSeconds;
              })
            }
          } else {
            IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', null).then(function () {
              ctx.state.isLocked = false;
              ctx.state.shouldShowCountDown = data.IsCountdownTimerShown;
              ctx.state.LockingMessage = data.LockingMessage;
              ctx.state.RemainedSeconds = data.RemainedSeconds;
            })
          }

        }, (error) => {
          IGTMediaElements.util.log.info(`[ireserve][model] lockInterval getstate error [${JSON.stringify(error)}]`);
          ctx.state.info.title = error.title;
          ctx.state.info.message = error.message;
        });
      },
      lock(ctx, payload) {
        if (ctx.state.options.includes(payload)) {
          IGTMediaElements.util.log.info(`[ireserve][model] lock cardId[${ctx.state.cardId}] machineNumber[${ctx.state.machineNumber}] playerId[${ctx.state.playerId}] sessionType[${ctx.getters.sessionType}] payload[${JSON.stringify(payload)}]`);

          serviceAPI.lock(ctx.getters.endPoint, {
            cardId: ctx.state.cardId,
            machineNumber: ctx.state.machineNumber,
            playerId: ctx.getters.validServicePlayerId,
            sessionType: ctx.getters.sessionType,
            period: payload
          }, (data) => {
            IGTMediaElements.util.log.info(`[ireserve][model] lock response data [${JSON.stringify(data)}]`);

            if (data.IsLocked) {

              if (data.RemainedSeconds <= 0) {

                if (ctx.state.lockRetryCount > 0) {

                  IGTMediaElements.util.log.info('[ireserve][model] retry to lock after 2 seconds in lock method');

                  ctx.state.lockRetryCount -= 1;

                  setTimeout(() => {
                    ctx.dispatch('lock', payload);
                  }, 2000);

                } else {
                  IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', 86400).then(function () {
                    ctx.dispatch('keep0x4c');
                    ctx.state.isLocked = true;
                    ctx.state.shouldShowCountDown = false;
                    ctx.state.LockingMessage = "";
                    ctx.state.RemainedSeconds = 86400;
                  })
                }

              } else {
                ctx.state.lockRetryCount = 5
                IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', data.RemainedSeconds).then(function () {
                  ctx.dispatch('keep0x4c');
                  ctx.state.isLocked = true;
                  ctx.state.shouldShowCountDown = data.IsCountdownTimerShown;
                  ctx.state.RemainedSeconds = data.RemainedSeconds;
                })
              }
            } else {
              IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', null).then(function () {
                ctx.dispatch('release0x4c');
                ctx.state.isLocked = false;
                ctx.state.shouldShowCountDown = data.IsCountdownTimerShown;
                ctx.state.RemainedSeconds = data.RemainedSeconds;
              })
            }

          }, (error) => {
            IGTMediaElements.util.log.info(`[ireserve][model] lock error [${JSON.stringify(error)}]`);
            model.state.info.title = error.title;
            model.state.info.message = error.message;
          });
        }
      },
      unlock(ctx, payload) {
        IGTMediaElements.util.log.info(`[ireserve][model] unlock cardId[${ctx.state.cardId}] machineNumber[${ctx.state.machineNumber}] playerId[${ctx.state.playerId}] sessionType[${ctx.getters.sessionType}] payload[${JSON.stringify(payload)}]`);

        serviceAPI.unlock(ctx.getters.endPoint, {
          cardId: ctx.state.cardId,
          machineNumber: ctx.state.machineNumber,
          playerId: ctx.getters.validServicePlayerId,
          sessionType: ctx.getters.sessionType,
          IsLockingTimeExpired: payload.IsLockingTimeExpired,
          IsPinValidated: payload.IsPinValidated
        }, (data) => {
          IGTMediaElements.util.log.info(`[ireserve][model] unlock response data [${JSON.stringify(data)}]`);

          if ((data.RemainedSeconds !== 0 && !data.IsLocked) || (data.RemainedSeconds <= 0 && data.IsLocked)) {
            IGTMediaElements.util.log.info('[ireserve][model] unlock failed. keep locking 24 hours.');

            IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', 86400).then(function () {
              ctx.dispatch('keep0x4c');
              ctx.state.isLocked = true;
              ctx.state.shouldShowCountDown = false;
              ctx.state.LockingMessage = "";
              ctx.state.RemainedSeconds = 86400;
            })

          } else {

            if (data.IsLocked) {
              IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', data.RemainedSeconds).then(function () {
                ctx.dispatch('keep0x4c');
                ctx.state.isLocked = true;
                ctx.state.RemainedSeconds = data.RemainedSeconds;
              })
            } else {
              IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', null).then(function () {
                ctx.dispatch('release0x4c');
                ctx.state.isLocked = false;
                ctx.state.RemainedSeconds = data.RemainedSeconds;
              })
            }

            if (!data.IsLocked && data.ErrorCode !== -100 && ctx.state.cardId + '' !== '0') {
              ctx.dispatch('getOptions');
            } else {
              ctx.state.info.title = "";
              ctx.state.info.message = "Exiting iReserve";
            }
          }
        }, (error) => {
          IGTMediaElements.util.log.info(`[ireserve][model] unlock response error [${JSON.stringify(error)}]`);

          if (error.errorCode + '' === '-100') {
            IGTMediaElements.util.log.info('[ireserve][model] showpin');
            ctx.state.showPin = true;
          } else {
            ctx.state.info.title = error.title;
            ctx.state.info.message = error.message;
          }
        });
      },
      release0x4c() {
        if (window.intervalId) {
          clearInterval(window.intervalId);
          window.intervalId = null;
        }
        IGTMediaElements.hostEventButtonPress(3, 0, false, 0);

        IGTMediaElements.util.log.info('[ireserve][model] calling host event to release 0x4C screen trigger');
      },
      keep0x4c() {
        IGTMediaElements.util.log.info('[ireserve][model] start calling custom event to keep iReserve showing.');

        IGTMediaElements.hostEventButtonPress(3, 1, false, 0);
        if (window.intervalId) {
          clearInterval(window.intervalId);
          window.intervalId = null;
        }

        window.intervalId = setInterval(function () {
          IGTMediaElements.util.log.debug('[ireserve][model] calling host event to stay in 0x4C screen');
          IGTMediaElements.hostEventButtonPress(3, 1, false, 0);
        }, 10000);
      }
    }
  };

  const model = cm.genModel(store, modules, el, beforeGen);

  model.mapToken('0x10', 'machineNumber');
  model.mapToken('0x31', 'cardId');
  model.mapToken('0x45', 'playerId');

  if (IGTMediaElements.isMock && model.state.runLocal) {
    IGTMediaElements.subscribeScreenTrigger("0x4c", function (message) {
      model.state.screenId = message.screenId
    })
    IGTMediaElements.triggerScreen("0x4c")
  }

  IGTMediaElements.getServerAddresses(['ireserve.igt.com']).then(serverAddresses => {
    model.state.serviceAddress = serverAddresses['ireserve.igt.com'].trim().replace(/\/$/, '');
  });

  //model.state.serviceAddress = "http://10.222.41.132" 

  model.watch(() => {
    return [model.state.machineNumber, model.state.serviceAddress];
  }, ([machineNumber, serviceAddress]) => {

    if (!machineNumber || !serviceAddress) {
      if (serviceAddress && serviceAddress === 'UNKNOWN') {
        model.state.info.title = "Error";
        model.state.info.message = "Incorrect URL configured for ireserve.igt.com";
      }

      if (model.state.isReady) {
        model.state.isReady = false;
      }
      return;
    }

    model.dispatch('getState');

    if (!model.state.isReady) {
      setTimeout(function () {
        model.state.isReady = true;
      }, 50);
    }
  }, {
    // immediate: true
  });


  let shouldRevertLockState;

  model.watch(() => {
    return [model.state.cardId, model.state.playerId];
  }, (newVal, oldVal) => {
    const [cardId, playerId] = newVal;
    //const [oldCardId, oldPlayerId] = oldVal;

    IGTMediaElements.util.log.debug(`[ireserve][model] cardId[${cardId}] playerId[${playerId}]]`);

    if (cardId + '' === '0' && shouldRevertLockState && model.state.isLocked) {
      IGTMediaElements.util.log.info('[ireserve][model] nines card card out and machine is still locked');
      model.dispatch('keep0x4c');
      model.state.isLocked = true; // todo in this case 0x4c not fired
      //IGTMediaElements.showFullScreen();
    }

    if (cardId && cardId + '' !== '0') {
      if (cardId === 'DIAGNOSTIC') {
        shouldRevertLockState = model.state.isLocked;
      } else {
        IGTMediaElements.attendant.isAttendantCardInserted().then(function (response) {
          model.state.isAttendant = response.attendantInserted;
          model.state.cardType = response.cardType;

          trace(`[m] cardId[${cardId}] playerId[${playerId}] isAttendant[${response.attendantInserted}] cardType[${response.cardType}]`);
          IGTMediaElements.util.log.debug(`[ireserve][model] cardId[${cardId}] playerId[${playerId}] isAttendant[${response.attendantInserted}] cardType[${response.cardType}]`);

          IGTMediaElements.contentCache.getStateElement('irLockRemainedSeconds').then(value => {
            IGTMediaElements.util.log.info(`[ireserve][model] getStateElement irLockRemainedSeconds[${value}]`);

            if (!value) {
              IGTMediaElements.util.log.info(`[ireserve][model] irLockRemainedSeconds unlock`);
              if (playerId && playerId + '' !== '0') {
                model.dispatch('unlock', {
                  IsLockingTimeExpired: false,
                  IsPinValidated: false
                });
              }
            }

            IGTMediaElements.contentCache.setStateElement('irLockRemainedSeconds', null).then(function () {
              IGTMediaElements.util.log.info(`[ireserve][model] setStateElement irLockRemainedSeconds[${value}]`);
            })
          });

        });
      }
    }

    // if (!model.state.isReady) {
    //   setTimeout(function () {
    //     model.state.isReady = true;
    //   }, 50);
    // }
  }, {
    // immediate: true
  });

  let originFetch = window.originFetch || fetch;
  fetch = function (url, payload) {
    if (IGTMediaElements.isMock) {
      let a = new URL(url);
      return new Promise(function (resolve, reject) {
        let res = new Response(JSON.stringify(model.state.mockAPI[a.pathname] || model.state.dataMap[a.pathname]));
        resolve(res);
      });
    } else {
      return originFetch.apply(null, Array.from(arguments));
    }
  }

  model.start();

  return model;
}

export default cm.declareModel('com.igt.ireserve', modelConstructor);
