import { genModel, declareModel } from "m5-content-model"

function modelConstructure(el, modules) {
    var store = {
        state: {},
        mutations: {},
        getters: {},
        actions: {},
        modules: {}
    }

    const model = genModel(store, modules, el);
    model.start();
    return model;
}

export default declareModel('com.igt.invalid_card_screen', modelConstructure)