import { genModel, declareModel, bindToken } from "m5-content-model"

var status = {
  "0": "Point Play available. ",
  "1": "Point Play not available: Bonusing not available on this machine.",
  "2": "Point Play not available: PIN Locked. ",
  "3": "Point Play not available:  see players club.",
  "4": "Point Play not available: Point balance too low to convert.",
  "5": "Point Play not available: Conversion to Xtra Credit not available, see players club.",
  "6": "Point Play not available: Conversion to Xtra Credit not available on this machine. ",
  "7": "Point Play not available: PIN validation failed.",
  "8": "Point Play not converted. Amount not compatible with this denomination."
}

function modelConstructor(el, modules, beforeGen) {
  var store = {
    state: {
      PlayersPreferredName: "",
      PlayerPointPlayBalance: 0,
      PlayerLevelName: "",
      DollarInput: '0',
      ConvertStatus: "INIT",
      DisplayMessage: "",
      PointToCentRate: 0,
      Enabled: null,
      DisabledReason: null,
      pinIsValid: true,
      egmDenom: ""
    },
    mutations: {},
    getters: {
      PointInDollar(state) {
        //  let pointDollar = state.PlayerPointPlayBalance * state.PointToCentRate / 100;
        // trace("pointDollar: ", pointDollar, "!");
        const result = parseFloat(Math.floor(state.PlayerPointPlayBalance * state.PointToCentRate) / 100).toFixed(2);
        // trace(result, "!");
        return result == null ? 0 : (isNaN(result) || result == Infinity) ? "" : result;
      },
      PointConversionMinimumDollar(state) {
        if (!state.appConfig || !state.appConfig.mapEgmDenomToMinCentsBalance)
          return 0;

        let miniCents = state.appConfig.mapEgmDenomToMinCentsBalance.default;
        if (state.appConfig.mapEgmDenomToMinCentsBalance[state.egmDenom])
          miniCents = state.appConfig.mapEgmDenomToMinCentsBalance[state.egmDenom];
        return miniCents / 100;
      },
    },
    actions: {
      StartConvert: function (context, to) {
        var toCredit = to !== undefined && to == 'TOCREDIT' ? true : false;
        var self = this;
        var amount = this.state.DollarInput * 100 / this.state.PointToCentRate;
        var tracker = this.analytics.willTriggerBusiness("PointplayConversion", { 'amount': amount, 'toCredit': toCredit });
        IGTMediaElements.pointPlay.convert(parseInt(amount), toCredit).then(function (resp) {
          tracker(resp)
          for (var key in resp) {
            if (resp.hasOwnProperty(key))
              trace(key + '|' + resp[key]);
          }
          if (resp.status == 0) {
            self.state.ConvertStatus = "SUCCEEDED";
          } else {
            self.state.ConvertStatus = "FAILED";
          }
        });

      }
    },
    modules: {}
  }

  // create model
  const model = genModel(store, modules, el, beforeGen);

  if(model.state.isDemo){
    const demoAmounts = [15000, 10000, 6000, 4000, 1500, 1000, 600, 500, 300];
    window.DemoAmountChange = function(){
      console.log('demo amounts change: ', demoAmounts);
      const iid = setInterval(()=>{
        model.state.PlayerPointPlayBalance = demoAmounts.shift() || 0;
      },2000);

      setTimeout(()=>{clearInterval(iid)},20000);
    }
  }

  model.start();
  prepareModelData(model);
  return model;
}

function prepareModelData(model) {

  bindToken("0x30", (value) => { model.state.PlayersPreferredName = value; });

  IGTMediaElements.getBonusLevel().then((level) => { model.state.PlayerLevelName = level; });
  let track = model.analytics.willTriggerBusiness("pointPlay.isEnabled");
  IGTMediaElements.pointPlay.isEnabled().then((data)=>{
    track(data);
    if(model.vm.$el == null || !data.pointPlayEnabled) {      
      model.state.Enabled = data.pointPlayEnabled;
      model.state.egmDenom = data.egmDenom;
    }

    if(model.vm.$el != null && data.pointPlayEnabled){    
      track = model.analytics.willTriggerBusiness("pointPlay.getInitialBalances");        
      IGTMediaElements.pointPlay.getInitialBalances(false, false).then(function (resp) {        
        track(resp);
        if(resp.status != 0 && resp.status != 4 && resp.status != 6) {
          model.state.Enabled = false;
          model.state.DisabledReason = status[resp.status];
        }else{
          model.state.Enabled = true;          
        }        
        model.state.egmDenom = resp.egmDenom;        
      });
    }

  })


  const centsToCalculate = 100;
  IGTMediaElements.pointPlay.valueToPoints(centsToCalculate).then(function (data) {
    model.state.PointToCentRate = centsToCalculate / data.points;
    trace("PointToCentRate: ", model.state.PointToCentRate, ".");
  });

  bindToken("0x1B", (value) => {
    model.state.PlayerPointPlayBalance = value;
    trace("0x1B PlayerPointPlayBalance: ", model.state.PlayerPointPlayBalance);
  });

  if(model.vm.$el == null){
    IGTMediaElements.pubSub.subscribe("enablePointPlayResp", (data)=>{
      model.state.Enabled=data.pointPlayEnabled;
    })
  }


}

export default declareModel('com.igt.pointplay', modelConstructor)
