function doesFileExist(urlToFile) {
  return new Promise(function (resolve, reject) {
    var xhr = new XMLHttpRequest()
    xhr.onreadystatechange = function onreadystatechange() {

      if (xhr.readyState === 4) {
        if (xhr.status >= 200 && xhr.status < 300 || xhr.status == 0 || xhr.status == 304) {
          resolve(urlToFile)
        } else {
          reject(new Error(xhr.status))
        }
      }
    }
    xhr.open('HEAD', urlToFile, true)
    xhr.send()
  })
}

function noop(){};

function injectVendorLibs(isLocalContent, forceDev = false) {

  if (!window.hasOwnProperty("loadSkinTemplate")) {
    window.loadSkinTemplate = httpVueLoader;
  }

  if (!window.hasOwnProperty("TweenMax")) {
    global.TweenMax = {
      to: noop,
      from: noop,
      staggerFrom: noop,
      staggerTo: noop
    };
    global.TimelineLite = function(){
      var tl;
      function chain(){
        return tl;
      }
      var tl = {
        add: chain,
        from: chain,
        to:chain,
        staggerFrom: chain,
        staggerTo: chain,
        staggerFromTo:chain,
        isActive(){
          return false;
        }
      };
      return tl;
    };
    global.Back = new Proxy({}, {
      get(){
        return "TweenMax::UNSUPPORTED"
      }
    });
     
  }
  if (!window.hasOwnProperty("anime")) {
    global.anime = require("animejs").default;
  }

  if ((!window.hasOwnProperty("$")) || (!window.hasOwnProperty("jQuery"))) {
    window.$ = window.jQuery = require("../vendor/jquery-3.4.1.js");
  }
  window.jQuery.fn.setVisible = function (yes) {
    return this.css('visibility', yes ? 'visible' : 'hidden');
  };
}

