import {
  genModel,
  declareModel,
  mapToken
} from "m5-content-model"

function modelConstructor(el, modules) {
  var store = {
    state: {
      PlayersPreferredName: null,
      PlayerID: null,
      CardID: null,
      cldMessagePattern: {"WINNER":"Congratulations.*won the Mystery bonus!",
      "WINNERFOUND":"Lucky member.*has been found.",
      "WINNERNOTFOUND":"Lucky member.*has not been found.",
      "SEARCHINGFORWINNER":"Now searching for.*",
      "VERIFYWINNER":"Lucky member.*selected as the winner!",
      "CELEBRATIONWINNER":"You've won.*celebration prize!",
      "VERIFYING":"Verification in progress for Lucky member.*",
      "DENIED":"Mystery bonus denied for Lucky Member.*",
      "REDRAW":"Mystery bonus redraw.*",
      "SELECTING":"Mystery bonus selecting.*"
      },
      WinAmountDollars: null,
      WinAmountCoins: null,
      HandpayAmount: null
    },
    mutations: {},
    getters: {
      CurrentScreen: state => {
        var pattern = null;

        for(const key in state.cldMessagePattern){
          if(state.cldMessagePattern.hasOwnProperty(key)){
            pattern = new RegExp(state.cldMessagePattern[key],"i");
            if(pattern.test(state.displayMessage)){
              return key;
            }
          }
        }
        return "UNKNOWN";
      }
    },
    actions: {},
    modules: {}
  }

  const model = genModel(store, modules, el);

  mapToken("0x30", "PlayersPreferredName");
  mapToken("0x45", "PlayerID");
  mapToken("0x31", "CardID");
  mapToken("0x1E", "WinAmountCoins");
  mapToken("0x25", "HandpayAmount");
  mapToken("0x29", "WinAmountDollars", {refresh: true});

  if (IGTMediaElements.isMock) {
    var messagees = ["Mystery bonus selecting winners...", "Now searching for lucky member #1234567", "Lucky member #12345 Sarina has been found.", "Verification in progress for Lucky member #12345 Sarina", "Lucky member Sarina you've been selected as the winner!",
                    "Congratulations Vicky! You've won the Mystery bonus!", "You've won a Mystery bonus celebration prize!","Mystery bonus denied for Lucky Member #1234", "Lucky member #1234 has not been found.", "Mystery bonus redraw in progress..."];
    var index = 0;
    model.state.displayMessage = messagees[index];
    document.onmousedown=(event)=>{
      if(event.shiftKey){
        index++;
        model.state.displayMessage = messagees[index%messagees.length];
      }
    }
  }
  model.start();

  return model;
}

export default declareModel("com.igt.carded_lucky_draw", modelConstructor)
