import cm, {
  getURLParameterByName
} from 'm5-content-model'

import {
  mockData
} from './award_mock-data.js'

import * as util from './model.util.js'

// IGTMediaElements.util.log.info(`rr-award-model page load`)

function modelConstructor (el, modules, beforeGen) {
  if (IGTMediaElements.isMock) {
    IGTMediaElements = Object.assign({}, IGTMediaElements, {
      tokenDic: {
        ...IGTMediaElements.tokenDic,
        "0x17": "10010"
      }
    })
  }

  var store = {
    state: {
      // Type: "html",
      demo: getURLParameterByName('demo'),
      serverMockData: getURLParameterByName('serverMockData'),
      serverIP: getURLParameterByName('serverIP'),
      protocal: getURLParameterByName('protocal'),
      domain: getURLParameterByName('domain'),

      uniqueId: '', // Unique Id (of BE2)
      cardId: '',
      asset: '',
      egmLocation: '',
      playerId: '',
      egmId () {
        const pad = `0000000000${this.uniqueId}`
        return `IGT_${pad.substr(-10)}`
      },

      supportFullScreen: false,
      playerName: '',
      award: undefined,
      currentTheme: '',
      errorMessage: '',
      awardMessage: '',
      awardNotification: '',
      pName: undefined,
      pId: undefined
    },
    getters: {
      getPromotionTheme (state) {
        return promotionName => {
          const result = state.RR_config.themePromotionMap.find((t) => {
            return t.promotionName === promotionName
          })
          return result ? result.theme : state.RR_config.themeList[0]
        }
      }
    }
  }

  const model = cm.genModel(store, modules, el, beforeGen)

  function getServiceUrl ({
    cardId,
    asset,
    egmLocation,
    egmId,
    awardId
  }) {
    return new Promise((resolve, reject) => {
      if (model.state.serverMockData) {
        const protocal = model.state.protocal || 'http'
        const ip = model.state.serverIP
        const domain = model.state.domain
        if (protocal === 'http' && ip) {
          resolve(`http://${ip}:15360/randomRichesAward?cardid=&asset=&location=&egmId=&awardId=1`)
        } else if (protocal === 'https' && domain) {
          resolve(`https://${domain}:15360/randomRichesAward?cardid=&asset=&location=&egmId=&awardId=1`)
        } else {
          reject('specify serverIP=10.222.31.103 or protocal=https&domain=randomriches.igt.com as query string of request url in serverMockData mode')
        }
      } else {
        const addrNamespace = 'randomriches.igt.com'
        IGTMediaElements.getServerAddresses([addrNamespace]).then(function (serverAddresses) {
          const h = serverAddresses[addrNamespace]

          IGTMediaElements.util.log.debug(`rr-award-model randomriches.igt.com[${h}]`)

          if (h && h !== 'UNKNOWN') {
            resolve(`${h}/randomRichesAward?cardid=${cardId}&asset=${asset}&location=${egmLocation}&egmId=${egmId}&awardId=${awardId}`)
          } else {
            reject(util.customizeMessage('general.host_ip_configure_issue', 'incorrect ip configured'))
          }
        }).catch(function (error) {
          IGTMediaElements.util.log.error(`rr-award-model error happened while getting server address, error is ${error}`)
          reject(util.customizeMessage('unknown', error))
        })
      }
    })
  }

  function fetchData (awardId) {
    return new Promise((resolve, reject) => {
      getServiceUrl({
        cardId: model.state.cardId,
        asset: model.state.asset,
        egmLocation: model.state.egmLocation,
        egmId: model.state.egmId(),
        awardId
      }).then((url) => {

        IGTMediaElements.util.log.debug(`rr-award-model web request full url is ${url}`)

        util.timeoutPromise(3000, fetch(url)).then((response) => {
          const contentType = response.headers.get('content-type')
          if (contentType && contentType.includes('application/json')) {
            resolve(response.json())
          }

          IGTMediaElements.util.log.debug(`rr-award-model fetchData response is ${contentType}`)

          reject(util.customizeMessage('award.cannot_get_award', 'award id could be truncated since display message configued too long'))
        }).catch((error) => {
          IGTMediaElements.util.log.error(`rr-award-model fetchData response error is ${error}`)

          reject(util.customizeMessage('general.service_timeout', error))
        })
      }).catch((error) => {
        IGTMediaElements.util.log.error(`rr-award-model getServiceUrl error is ${error}`)
        reject(error)
      })
    })
  }

  function getServerData (awardId) {
    return new Promise((resolve, reject) => {
      if ((model.state.demo || process.env.NODE_ENV === 'development') && !model.state.serverMockData) {
        setTimeout(() => {
          resolve(mockData)
          // reject('time out while receiving data from service')
        }, 1000)
      } else {
        fetchData(awardId).then((json) => {
          resolve(json)
        }).catch((error) => {
          reject(error)
        })
      }
    })
  }

  function callSvc (awardId) {
    const cb = model.analytics.willTriggerBusiness('getServerData')
    getServerData(awardId).then((json) => {
      IGTMediaElements.util.log.debug(`rr-award-model callSvc result is ${JSON.stringify(json.randomRichesAward)}`)
      model.state.award = json.randomRichesAward

      cb()
    }).catch((error) => {
      model.state.errorMessage = error
      IGTMediaElements.util.log.error(`rr-award-model callSvc error is ${error}`)
    })
  }

  let serviceWindowOpen = false
  IGTMediaElements.pubSub.subscribe(IGTMediaElements.WINDOW_STATE_TOPIC, data => {
    IGTMediaElements.util.log.debug(`rr-award-model windowState[${data.windowState}]`)
    serviceWindowOpen = data.windowState
  });

  model.mapToken('0x30', 'playerName')

  model.start(true)

  if (model.state.demo || process.env.NODE_ENV === 'development' ) {
    setTimeout(() => {
      IGTMediaElements.triggerScreen("0x69")
    }, 1000);
  }

  model.watch('appConfig', (data) => {
    model.state.RR_config = data['com.igt.randomriches']
    IGTMediaElements.util.log.info('rr-award-model load config')
  })

  model.watch(() => {
    return [model.state.appConfig, model.state.pName, model.state.pId, model.state.RR_config, model.state.errorMessage, model.state.isReady]
  }, ([appConfig, promoName, pId, RR_config, errorMessage]) => {
    if (errorMessage) {
      model.state.isReady = true
    } else {
      if (!appConfig || (!promoName && !pId) || !RR_config) {
        if (model.state.isReady) {
          model.state.isReady = false
        }
        return
      }

      model.state.currentTheme = model.getters.getPromotionTheme(model.state.pName)

      IGTMediaElements.util.log.info(`rr-award-model promoName[${model.state.pName}] currentTheme[${model.state.currentTheme}]`)

      if (!model.state.isReady) {
        setTimeout(function () {
          model.state.isReady = true
        }, 50)
      }
    }
  }, {
    immediate: true
  })
  
  model.watch('screenTrigger', (message) => {
    if(['0x68', '0x69', '0x6a', '0x6b', '0x6c', '0x6d', '0x6e', '0x6f'].includes(message.screenId.toLowerCase())) {
      IGTMediaElements.util.log.info(`rr-award-model screenTrigger[${message.screenId}] rr service configured message[${message}]`)

        let dm
        if(message.displayMessage && typeof message.displayMessage.split === 'function') {
          dm = message.displayMessage
        } else {
          dm = message // used for M5 5.2
        }
        
        if(model.state.demo || process.env.NODE_ENV === 'development') {
          dm = " AYou win 10 points! |-| 123|-| promo 1"
          //dm = "You win 10 pointswwwwwwwwwwwwwww asasd  asdasdasdasd!"
          // dm = "Aaward 1 at location |-|0|-| promo 1"
        }

        let [msg, awardId, promoName] = dm.trim().split('|-|')

        if (msg[0] !== 'A') return
        
        msg = msg.substring(1)

        if (!serviceWindowOpen) {
          IGTMediaElements.showWindow()
        }

        const trimmedMessage = msg.replace(/\|-$/, '').replace(/\|$/, '').trim() // with pattern |-| have to replace |- to empty and then replace | to empty

        if (!awardId) {
          IGTMediaElements.util.log.error('rr-award-model award id could be truncated since display message configued too long')
          model.state.errorMessage = trimmedMessage // util.customizeMessage('award.display_message_configued_too_long')
          return
        }

        if (!promoName) {
          IGTMediaElements.util.log.error('rr-award-model promotion name could be truncated since display message configued too long, so use defalut theme instead')
        }

        model.state.pId = awardId = awardId.replace(/\|-$/, '').replace(/\|$/, '').trim()

        model.state.pName = promoName && promoName.trim()

        if (awardId === '0') {
          model.state.awardNotification = trimmedMessage
          IGTMediaElements.util.log.info(`rr-award-model screenTrigger awardNotification[${model.state.awardNotification}] promoName[${model.state.pName}]`)
        } else {
          model.state.awardMessage = trimmedMessage

          IGTMediaElements.util.log.info(`rr-award-model screenTrigger awardId[${awardId}] promoName[${model.state.pName}] awardMessage[${model.state.awardMessage}]`)

          if (model.state.serverMockData) {
            callSvc(awardId)
          } else {
            model.watch(() => [model.state.uniqueId, model.state.cardId, model.state.asset, model.state.egmLocation, model.state.playerId], function (newValue, oldValue) {
              if (newValue.includes('0') || newValue.includes('') || newValue.includes(null)) return
              callSvc(awardId)
            }, {
              immediate: true
            })

            model.mapToken('0x45', 'playerId')
            model.mapToken('0x17', 'uniqueId')
            // model.mapToken('0x31', 'cardId')
            model.mapToken('0x10', 'asset')
            model.mapToken('0xAF', 'egmLocation')
          }
        }
    }
  })

  return model
}

export default cm.declareModel('com.igt.RR.award', modelConstructor)
