import {genModel, declareModel, mapToken, getURLParameterByName, retry} from "m5-content-model"


function modelConstructor(el, modules, beforeGen) {
  var store = {
    state: {
      isLoading: false,
      MarkerBalance: null,
      TotalCreditLine: 0,
      AvailableCredit: 0,
      DollarInput: '0',
      Enabled: null,
      isPinValid: null,
      egmDenom: "",
      transactionID: null,
      sessionId: null,
      isCashlessAvailable: null,
      isCardlessAvailable: null,
      Options: null,
      invalidNumbers: [],
      propertyId: null,
      assetId: null,
      playerId: null
    },
    mutations: {},
    getters: {
    },
    actions: {
      Initialize: function(context){
        var self = this;
      }
    },
    modules: {}
  }

  const model = genModel(store, modules, el, beforeGen);

  mapToken("0x45", "playerId");
  mapToken("0x10", "assetId");

  function setSessionId(){
    /*
    if(localStorage['sessionId-'+model.state.cardId]){
      model.state.sessionId = localStorage['sessionId-'+model.state.cardId];
    }else{
      model.state.sessionId = model.getters.newGUID;
      localStorage['sessionId-'+model.state.cardId] = model.state.sessionId;
    }*/
  }

  model.watch((state)=>state.appConfig, function (after, before) {
    //Populate the Options state with the markerTraxOptions object values from appconfig.json
    model.state.Options = model.state.appConfig.markerTraxOptions;
    trace("Read markerTraxOptions from appconfig: " + model.state.Options);
    IGTMediaElements.util.log.debug("MarkerTrax: Read markerTraxOptions from appconfig: " + model.state.Options);
  })
  model.dispatch("Initialize");

  //Detects if user has a Cashless account enabled, stores result of detection in isCashlessAvailable state
  const retryGetCashless = retry(function () {
    return IGTMediaElements.cashless.checkCashlessEnabled().then(data => {
      return new Promise(function (resolve, reject) {
        if (data.responseData.haveCardData) {
          resolve(data)
        } else {
          reject(new Error("haveCardData is false"));
        }
      })
    })
  }, 3, 1500);
  //Obtains if user has Cashless
  retryGetCashless.then(data => {
    console.log('retryGetsCashless: data:', data);
    if (data.enabled === undefined || data.enabled === null) {
      model.state.isCashlessAvailable = false;
    } else {
      model.state.isCashlessAvailable = data.enabled;
    }
  }).catch(error => {
    trace(`retry 3 times: ${error}!`);
    model.state.isCashlessAvailable = false;
  })

  //Detects if user has a Cardless account enabled, stores result of detection in isCardlessAvailable state
  const retryGetCardless = retry(function () {
    //return IGTMediaElements.cardless.getCardlessStatus().then(data => {
    return IGTMediaElements.getPatronData().then(data => {
      return new Promise(function (resolve, reject) {
        if (data) {
          resolve(data);
        } else {
          reject(new Error("Can't obtain patronData"));
        }
      })
    })
  }, 3, 1500);
  //Obtains if user has Cardless available
  retryGetCardless.then(data => {
    console.log('retryGetCardless: data.patronData:', data.patronData);
    //In case of eCard doesn't exists in patronData
    if (data.patronData.patronData.eCard === undefined || data.patronData.patronData.eCard === null) {
      model.state.isCardlessAvailable = false;
    } else {
      model.state.isCardlessAvailable = data.patronData.patronData.eCard;
    }
  }).catch(error => {
    trace(`retry 3 times: ${error}!`);
    model.state.isCardlessAvailable = false;
  });

  model.start();

  return model;
}

export default declareModel('com.igt.markertrax', modelConstructor)
