import cm from 'm5-content-model'

export default cm.declareModel('com.igt.PlcStatus', function (el, options, beforeGen) {
  
  /* istanbul ignore if */
  if (IGTMediaElements.isMock) {
    Object.assign(IGTMediaElements.tokenDic, {
      "0x73": "$1000", //PLC max pool
      "0x28": "$500", //PLC current pool      
      "0xFF": "Keep playing to be eligible for a Personal Progressives award!",
    })
  }


  const model = cm.genModel({
    state: {
      currentPoolToken: "",
      currentPool: 0,
      maxPoolToken: "",
      maxPool: 0,
      plcPercentage: 0,
    },
    actions: {

    }
  }, options, el, beforeGen);

  model.mapToken("0x73", "maxPoolToken");
  model.mapToken("0x28", "currentPoolToken");

  model.watch("currentPoolToken", function (newValue) {
    if (newValue) {
      model.state.currentPool = Number(newValue.replace(/[^0-9.-]+/g, ""));

      if (model.state.maxPool > 0) {
        let percentage = (model.state.currentPool / model.state.maxPool) * 100;
        model.state.plcPercentage = Number(percentage.toFixed());
      }
    }
  });


  model.watch("maxPoolToken", function (newValue) {
    if (newValue) {
      let maxPool = Number(newValue.replace(/[^0-9.-]+/g, ""));
      if (!isNaN(maxPool) && maxPool >= 0)
        model.state.maxPool = maxPool;

      if (model.state.maxPool > 0) {
        let percentage = (model.state.currentPool / model.state.maxPool) * 100;
        model.state.plcPercentage = Number(percentage.toFixed());
      }
    }
  });

  model.start();




  return model;
})
