import { genModel, declareModel, bindToken } from "m5-content-model"

function modelConstructor(el, modules) {
  var store = {
    state: {
      PlayersPreferredName: '',
      XtraCreditInactiveAmount: '$0.00',
      FreePlayAmountCents: '',
      isEnabled: true,
      XCActiveStatus: {},
      XCCancelStatus: {},
      CasinoNameForXtraCredit: 'Xtra Credit',
      hasNewApi: false,
      preferToUseOldAPI: false,
      needToUseRoundDown: true,
      egmDenom: 0,
      formattedXCAmount: 0,
      PinInvalidStatus: {}
    },
    getters: {
      isXCActivated: state => {
        const a = Number(state.FreePlayAmountCents.replace(/[^0-9\.]+/g, ""));
        return a > 0;
      },
      XCInactiveAmout: state => {
        return Number(state.XtraCreditInactiveAmount.replace(/[^0-9\.]+/g, ""));
      },
      isNewVersionUsed: state => {
        return !state.preferToUseOldAPI && state.hasNewApi;
      }
    },
    actions: {
      activeXC(context, amount) {
        this.analytics.willTriggerBusiness("activeXC");
        trace("start to actvieXtraCredit.")

        if (this.state.needToUseRoundDown) {
          this.state.formattedXCAmount = roundDown(amount);
        } else {
          this.state.formattedXCAmount = amount;
        }

        if (this.getters.isNewVersionUsed)
          IGTMediaElements.activateXtraCreditWithAmount(true, (this.state.formattedXCAmount * 100).toFixed());
        else
          IGTMediaElements.activateXtraCredit(true);
      },
      cancelXC() {
        this.analytics.willTriggerBusiness("cancelXC");
        IGTMediaElements.cancelXtraCredit();
      }
    },
    modules: {}
  }
  const model = genModel(store, modules, el);

  /* istanbul ignore if */
  if(!model.vm._isSubModule) {
    require('./demo/initiator').createDemoSpace(el, 'ncep', "case1");
  }

  //get data
  bindToken("0x30", (value) => {
    model.state.PlayersPreferredName = value;
  });

  //get inactive XC balance 0x23
  bindToken("0x23", (value) => {
    trace('0x23 XtraCreditInactiveAmount:', value);
    model.state.XtraCreditInactiveAmount = value;
  });

  bindToken("0x26", (value) => {
    trace('0x26 FreePlayAmountCents:', value);
    model.state.FreePlayAmountCents = value;
  });

  bindToken("0x32", (value) => {
    model.state.CasinoNameForXtraCredit = value;
  });

  function roundDown(value) {
    var denom = model.state.egmDenom || 1;
    var xcToActiveCents = (value * 100).toFixed();
    var multi = xcToActiveCents / denom;
    if (Math.round(multi) !== multi)
      return Math.floor(xcToActiveCents / denom) * denom / 100;
    else
      return value;
  }

  function getMediaElementVersion() {
    model.state.hasNewApi = IGTMediaElements.activateXtraCreditWithAmount && typeof (IGTMediaElements.activateXtraCreditWithAmount) == "function";
  }

  function getCashlessBalance() {
    var trackerC = model.analytics.willTriggerBusiness("getCashlessBalance");

    IGTMediaElements.cashless.getCashlessBalance().then(function (data) {
      trackerC(data);
      model.state.egmDenom = data.egmDenom;
      trace("cashless egmDenom", data.egmDenom);
    });
  }

  //sub IGT_XC_ACTIVATED_TOPIC and IGT_XC_NOT_ACTIVATED_TOPIC
  var processXCActiveStatus = function (data) {
    trace("xc activated topic received:", JSON.stringify(data))
    model.state.XCActiveStatus = data;
  }

  //sub igt-xc-enable-response
  var processXCEnableStatus = function (data) {
    trace("xtraCreditEnabled=", data.xtraCreditEnabled);
    console.log("xtraCreditEnabled=", data.xtraCreditEnabled);
    model.state.isEnabled = data.xtraCreditEnabled;
  }

  var processPinInvalidStatus = function (data) {
    trace("pin invalid topic received: ", JSON.stringify(data));
    model.state.PinInvalidStatus = data;
  }

  IGTMediaElements.pubSub.subscribe('igt-xc-activated_topic', processXCActiveStatus);
  IGTMediaElements.pubSub.subscribe('igt-xc-not-activated_topic', processXCActiveStatus);
  IGTMediaElements.pubSub.subscribe(IGTMediaElements.IGT_PIN_NOT_VERIFIED_TOPIC, processPinInvalidStatus);

  //sub IGT_XC_CANCELED_TOPIC and IGT_XC_NOT_CANCELED_TOPIC
  var processXCCancelStatus = function (data) {
    model.state.XCCancelStatus = data;
  }

  IGTMediaElements.pubSub.subscribe('igt-xc-canceled_topic', processXCCancelStatus);
  IGTMediaElements.pubSub.subscribe('igt-xc-not-canceled_topic', processXCCancelStatus);

  IGTMediaElements.pubSub.subscribe('igt-xc-enable-response', processXCEnableStatus);
  IGTMediaElements.util.sendMessage({ "cmd": "enableXtraCredit", "ver": "1.0" });

  getMediaElementVersion();
  getCashlessBalance();

  var bodyClickHandler = function (e) {
  /* istanbul ignore if */
    if (e.shiftKey) {
      if (model.state.isEnabled) {
        if (model.getters.isXCActivated) {
          model.state.isEnabled = false;
        }
        else {
          model.state.FreePlayAmountCents = this.currencySymbol + '1.00';
        }
      }
      else {
        model.state.FreePlayAmountCents = this.currencySymbol + '0.00';
        model.state.isEnabled = true;
      }
    }
  }
  
  /* istanbul ignore if */
  if (model.state.isDemo) {
    $("body").click(function (e) {
      bodyClickHandler(e);
    });
  }
  model.start();
  return model;
}



export default declareModel("com.igt.xtracredit", modelConstructor)
