import { doesFileExist } from "../supporting/utils"


var isShiftDown = false;
if (process.env.NODE_ENV !== "production") {
    window.addEventListener("mousedown", function (e) {
        if (e.shiftKey) {
            isShiftDown = true;
        } else {
            isShiftDown = false;
        }
    })
}

var javascript_object = document.createElement('a');
function relativeToAbsolute(url) {

    javascript_object.href = url;
    return javascript_object.href;
}

let a = document.createElement("a");

export default function navi(href, payload) {
    console.log('[Analytics]', window.location.href, "will navigate to", href);
    let urlmap = this.navigationMapping;
    var target = href;

    var self = this;
    if (!isShiftDown) {
        if (urlmap.hasOwnProperty(href)) {
            target = urlmap[href]
            IGTMediaElements.loadContent(target);
        } else {
            // this will workaround a relative path bug
            a.href = window.location.href;
            a.href = target;
            window.location = a.href;
        }
    }
    doesFileExist(target).then(function () {
        self.analytics.trackNavigate(target, payload)
    }).catch(function () {
        self.analytics.trackException("URLNotFound", relativeToAbsolute(target));
    })
}
