import modelVersion from "../supporting/version"
import semver from "semver";
import { getURLParameterByName, getURLParams, shouldDemo } from "../supporting/utils"
import { fetch, injectVendorLibs, typeName, shellWindow } from "../supporting/utils"
import getDisplayName from "../supporting/extendedSizeTables"
import debounce from "lodash/debounce";

var gitVersion = process.env.GIT_VERSION;
var gitHash = process.env.GIT_COMMIT;
var gitBranch = process.env.GIT_BRANCH;
var autoTranslateCurrencySymbol = false;
var currencySymbol = "$";
var urlparams = getURLParams();
var currentContentSize = { width: window.innerWidth, height: window.innerHeight };


var isFetchingAppConfig = false;



function getAppConfig(ctx) {
    const state = ctx.state;
    if (state._isSubModule) return;
    IGTMediaElements.contentCache.getStateElement("__app_config__").then(value => {
        if (value && value.hasOwnProperty("cacheAppConfig") && value.cacheAppConfig) {
            state.appConfig = value;
        } else {
            var configURL = "./appconfig.json";
            if (window.location.protocol.indexOf("http") != -1) {
                var packagename = window.location.pathname.split("/")
                packagename.splice(-1, 1);
                packagename = packagename.slice(0, 2).join("/");
                configURL = window.location.protocol + "//" + window.location.host + packagename + "/appconfig.json"
            }
            if (isFetchingAppConfig) return;
            isFetchingAppConfig = true;
            fetch(configURL)
                .then(res => {
                    var jsonData = JSON.parse(res);
                    state.appConfig = jsonData;
                    IGTMediaElements.contentCache.setStateElement("__app_config__", jsonData);

                })
                .catch(error => {
                    console.warn(`can't get ${configURL}`, error)
                })
                .finally(() => {
                    isFetchingAppConfig = false;
                });
        }
    });
}

var isFetchingURLMap = false;
function getURLMaps(ctx) {
    const state = ctx.state;
    if (state._isSubModule) return;
    IGTMediaElements.contentCache.getStateElement("__url_maps__").then(value => {
        if (value) {
            state.urlMaps = value;
        } else {
            let isVertical = (parseInt(state.urlparams.contentHeight) || window.innerHeight) > (parseInt(state.urlparams.contentWidth) || window.innerWidth);
            let url = isVertical ? "/shell/urlMapVertical.json" : "/shell/urlMapHorizontal.json";
            if (shellWindow != window && shellWindow.location.protocol.indexOf("http") != -1) {
                var packagename = window.location.pathname.split("/")
                packagename.splice(-2, 2);
                packagename = packagename.slice(0, 2).join("/");
                url = shellWindow.location.protocol + "//" + window.location.host + packagename + url;
            }
            if (isFetchingURLMap) return;
            isFetchingURLMap = true;
            fetch(url)
                .then(res => {
                    let urlmap = JSON.parse(res)
                    state.urlMaps = urlmap;
                    IGTMediaElements.contentCache.setStateElement("__url_maps__", urlmap);

                })
                .catch(error => {
                    console.warn(`can't get ${url}`, error)
                })
                .finally(function () {
                    isFetchingURLMap = false;
                });
        }
    });
}
function absolute(sRelPath) {
    var nUpLn, sDir = "", sPath = location.pathname.replace(/[^\/]*$/, sRelPath.replace(/(\/|^)(?:\.?\/+)+/g, "$1"));
    for (var nEnd, nStart = 0; nEnd = sPath.indexOf("/../", nStart), nEnd > -1; nStart = nEnd + nUpLn) {
        nUpLn = /^\/(?:\.\.\/)*/.exec(sPath.slice(nEnd))[0].length;
        sDir = (sDir + sPath.substring(nStart, nEnd)).replace(new RegExp("(?:\\\/+[^\\\/]*){0," + ((nUpLn - 1) / 3) + "}$"), "/");
    }
    return sDir + sPath.substr(nStart);
}

export default function getDefaultStore(el, isPrimary) {
    var _isSubModule = !el;

    var store = {
        // namespaced: true,
        state: {
            gitBranch, gitHash, gitVersion, currencySymbol, autoTranslateCurrencySymbol, urlparams, currentContentSize, _isSubModule,
            version: modelVersion,
            frameworkVersion: modelVersion,
            contentVersion: process.env.VERSION || "UNKNOWN",
            igtMediaElementsRequirements: { version: null, date: null },
            igtMediaElementsVersion: { version: null, date: null },
            appConfig: {},
            systemValidationErrors: {
                "libExits": null,
                "libVersion": null,
                "libBuildDate": null,
                "branch": null,
                "contentSize": null
            },
            $_global: {
                language: "en"
            },
            $_rootClass:{
            },
            screenId: "",
            displayMessage: "",
            messageTokens: {},
            screenTrigger: {},
            isDemo: shouldDemo(),
            isReady: false,
            language: "en",
            preferredDisplayType: "any",
            isFullScreen: false,
            cardId: null,
            floorLocation: "",
            machineNumber: "",
            isChangingDisplayType: false,
            ext: {}

        },

        mutations: {

        },
        actions: {
            syncDisplayType: debounce(function(ctx, force=false) {
                let timeoutId;
                IGTMediaElements.getFullscreenState().then(function (isFullscreen) {
                    var preferredDisplayType = ctx.state.preferredDisplayType;
                    var shouldShowBannerInFullscreen = ctx.getters.shouldShowBannerInFullscreen;
                    var needsSync = (!isFullscreen && preferredDisplayType == "fullscreen") || (isFullscreen && preferredDisplayType == "normal") || (ctx.getters.displayName == "TFT" && preferredDisplayType == "normal") || force;
                    if (needsSync) {
                        ctx.state.isChangingDisplayType = true;
                        if (preferredDisplayType == "fullscreen") {
                            if(shouldShowBannerInFullscreen && shouldShowBannerInFullscreen.then && shouldShowBannerInFullscreen.catch){
                                shouldShowBannerInFullscreen.then(function(showBanner){
                                    IGTMediaElements.showFullScreen(showBanner);
                                }).catch((e)=>{
                                    console.log(e);
                                    IGTMediaElements.showFullScreen();
                                })
                            }else{
                                IGTMediaElements.showFullScreen(!!shouldShowBannerInFullscreen);

                            }
                        } else {
                            IGTMediaElements.hideFullScreen();
                        }
                        if (timeoutId) {
                            clearTimeout(timeoutId);
                            timeoutId == null;
                        }
                        timeoutId = setTimeout(() => {
                            ctx.state.isChangingDisplayType = false;
                        }, 300);
                    }
                });
            },200),
            getAppConfig, getURLMaps
        }
    }
    if (el) {
        store.state.urlMaps = {};
        store.getters = Object.assign({}, store.getters, {
            apps(state) {
                if (state.appConfig.hasOwnProperty("apps")) {
                    return state.appConfig.apps;
                } else {
                    return [];
                }
            },

            navigationMapping(state, getters) {
                let obj = {}
                getters.apps.forEach(function (item) {

                    obj[item.title] = absolute(item.url);
                })

                var urlMapsObj = {}
                for (var key in state.urlMaps.screenIdMap) {
                    urlMapsObj[key] = state.urlMaps.baseUrl + state.urlMaps.screenIdMap[key];
                }

                obj = Object.assign({}, obj, urlMapsObj || {});
                return obj;
            },
            displayMessage: function (state) {
                if (state.screenId == "") {
                    shellWindow.postMessage({
                        cmd: 'requestSendTriggers',
                        ver: '1.0'
                    }, '*');
                    // window.top.postMessage({cmd: 'requestSendTokens', ver: '1.0'}, '*');
                }
                return state.displayMessage;
            },
            isVertical(state, getters) {
                return getters.contentHeight > getters.contentWidth;
            },
            displayName(state, getters) {
                let name = getDisplayName(getters.contentWidth, getters.contentHeight)
                if(isPrimary){
                    let root = $("html").removeClass();
                    root.addClass(getters.isVertical ? "vertical" : "horizontal").addClass(name);
                    for(var key in state.$_rootClass){
                        let value = state.$_rootClass[key];
                        if(typeName(value)=="Array" && value.includes(name)){
                            root.addClass(key);
    
                        }else if(typeName(value) == "Function"){
                            if(value.apply(this, Array.from(arguments)) === true){
                                root.addClass(key);
                            }
                        }
                    }
                }
                return name || `Unkown(${getters.contentWidth}x${getters.contentHeight})`;
            },
            contentHeight(state) {
                return parseInt(state.currentContentSize.height || window.innerHeight);
            },
            contentWidth(state) {
                return parseInt(state.currentContentSize.width || window.innerWidth);
            }

        });
    }
    return store;
}


export function getLocalDefaultStore(el) {
    var store = {
        // namespaced: true,
        state: {
            version: modelVersion,
            isReady: false,
            language: "en",
            ext: {}
        },

        mutations: {

        }
    }
    if (el) {
        store.getters = Object.assign({}, store.getters,
            {
                isVertical(state, getters) {
                    return getters.contentHeight > getters.contentWidth;
                },
                contentHeight(state) {
                    return parseInt(state.urlparams.contentHeight || window.innerHeight);
                },
                contentWidth(state) {
                    return parseInt(state.urlparams.contentWidth || window.innerWidth);
                }

            });
    }
    return store;
}