import cm from 'm5-content-model'

export default cm.declareModel('com.igt.Bonus', function (el, options, beforeGen) {
  const model = cm.genModel({
    state: {
      WinAmountDollars: null,
      WinAmountCoins: null,
      HandpayAmount: null
    },
    actions: {

    }
  }, options, el, beforeGen);

  model.mapToken("0x1E", "WinAmountCoins");
  model.mapToken("0x25", "HandpayAmount");
  model.mapToken("0x29", "WinAmountDollars", {refresh: true});
  model.start();
  return model;
})
