import promotions from '../api/promotions';

const state = {
  'promotionsAll': [],
  'isAlreadyCheckin': false
};

const getters = {
  checkinDrawings: state => {
    const arr = [];
    state.promotionsAll.forEach(element => {
      element.Drawings.forEach(drawing => {
        const currentTime = new Date().getTime();
        if (drawing.IsCheckInNeeded &&
          !drawing.PlayerAlreadyCheckedIn &&
          (currentTime > drawing.CheckInStartTime &&
            currentTime < drawing.CheckInEndTime)
        ) {
          arr.push(drawing.DrawingId);
        }
      });
    });
    return arr;
  }
};

const actions = {
  getPromotionsAll({
    dispatch,
    state,
    commit,
    rootState,
    rootGetters
  }) {
    rootState.isFetching = true;
    promotions.getPromotionsAll(rootState.CardID, rootState.EgmLocation).then((data) => {
      rootState.isFetching = false;
      if (data.ErrorCode != 0) {
        rootGetters.rootAnalytics.trackException("Error", {Error:data});
        dispatch('addInfo', {
          message: data.ErrorMessage,
          errorCode: data.ErrorCode,
          routertype: "exit"
        }, {
          root: true
        });
        return;
      }
      commit('setPromotions', data.PromoAwards);
    }).catch((err) => {
      rootGetters.rootAnalytics.trackException("Error", {Error:err.message});
      rootState.isFetching = false;
      dispatch('addInfo', {
        message: err.message,
        routertype: "exit"
      }, {
        root: true
      });
    });
  },
  batchTicketCheckIn({
    dispatch,
    state,
    commit,
    rootGetters,
    rootState,
    getters
  }) {
    rootState.isFetching = true;
    const checkinPromotionsLength = getters.checkinDrawings.length;
    let index = 0;
    promotions.batchTicketCheckIn(rootState.CardID, rootState.EgmLocation, getters.checkinDrawings, rootGetters.guid).then((resp) => {
      rootState.isFetching = false;
      resp.forEach(element => {
        if (element.ErrorCode != 0) {
          rootGetters.rootAnalytics.trackException("Error", {Error:element});
          if (element.ErrorCode == '1303') {
            index++;
          }
          dispatch('addInfo', {
            message: element.ErrorMessage,
            errorCode: element.ErrorCode
          }, {
            root: true
          });
        } else {
          index++;
          if (index == checkinPromotionsLength) {
            state.isAlreadyCheckin = true;
          }
        }
      });
    }).catch((err) => {
      rootGetters.rootAnalytics.trackException("Error", {Error:err});
      rootState.isFetching = false;
      dispatch('addInfo', {
        message: err.message
      }, {
        root: true
      });
    });
  }
};

const mutations = {
  setPromotions(state, promotions) {
    state.promotionsAll = promotions;
  }
};

export default {
  'namespaced': true,
  state,
  getters,
  actions,
  mutations
};