import { genModel, declareModel, retry } from "m5-content-model"


function modelConstructor(el, modules, beforeConfig) {
  var store = {
    states: {
      isCashlessAvailable: false,
      isCardlessAvailable: false
    },
    actions: {
      refresh: function () { refresh(model.start) }
    }
  }
  const model = genModel(store, modules, el, beforeConfig);

  const retryGetCashless = retry(function () {
    return IGTMediaElements.cashless.checkCashlessEnabled().then(data => {
      return new Promise(function (resolve, reject) {
        if (data.responseData.haveCardData) {
          resolve(data)
        } else {
          reject(new Error("haveCardData is false"));
        }
      })
    })
  }, 3, 1500);
  retryGetCashless.then(data => {
    model.state.isCashlessAvailable = data.enabled
  }).catch(error => {
    trace(`retry 3 times: ${error}!`);
  })

  const retryGetCardless = retry(function () {
      return IGTMediaElements.getPatronData().then(data => {
        return new Promise(function (resolve, reject) {
        if (data) {
          resolve(data);
        } else {
          reject(new Error("Can't obtain patronData"));
        }
      })
    })
  }, 3, 1500);
  retryGetCardless.then(data => {
    if(typeof data.patronData.patronData.eCard === 'undefined')
      model.state.isCardlessAvailable = false;
    else
      model.state.isCardlessAvailable = data.patronData.patronData.eCard;
  }).catch(error => {
    trace(`retry 3 times: ${error}!`);
  });
  
  model.start();
  return model;
}



export default declareModel('com.igt.Apps', modelConstructor)