import award from '../api/award';

const state = {
  'awardAll': []
};

const actions = {
  getAward({
    dispatch,
    state,
    commit,
    rootState,
    rootGetters
  }) {
    rootState.isFetching = true;
    award.getAward(rootState.currentSelectDrawingId, rootState.CardID, rootState.EgmLocation).then((data) => {
      rootState.isFetching = false;
      if (data.ErrorCode != 0) {
        rootGetters.rootAnalytics.trackException("Error", {Error:data});
        dispatch('addInfo', {
          message: data.ErrorMessage,
          errorCode: data.ErrorCode,
          routertype: "router",
          pathname: "promotion"
        }, {
          root: true
        });
        return;
      }
      if (data.Drawing != null) {
        commit('setAward', data.Drawing);
      }
      dispatch("setAwardToPromotion", data.Drawing);
    }).catch((err) => {
      rootState.isFetching = false;
      rootGetters.rootAnalytics.trackException("Error", {Error:err.message});
      dispatch('addInfo', {
        message: err.message,
        routertype: "router",
        pathname: "promotion"
      }, {
        root: true
      });
    });
  },
  setAwardToPromotion({
    dispatch,
    state,
    commit,
    rootState
  }, data) {
    rootState.promotions.promotionsAll.forEach((element, index) => {
      element.Drawings.forEach((el, id) => {
        if (el.DrawingId == rootState.currentSelectDrawingId) {
          if (data == null) {
            // when the specific returns null, this award is overdue, and this award in promotions of store will be deleted.
            // after click ok, it will back to drawing page.
            rootState.promotions.promotionsAll[index].Drawings.splice(id, 1);
            rootState.isFetching = false;
            dispatch('addInfo', {
              message: "This award is overdue!"
            }, {
              root: true
            });
          } else {
            // modify the data of promotion
            rootState.promotions.promotionsAll[index].AvailableFreeEntryNumber = data.AvailableFreeEntryNumber;
            rootState.promotions.promotionsAll[index].TicketsAvailable = data.AvailableTicketNumber;
            rootState.promotions.promotionsAll[index].TicketsUsed += data.TicketsUsed - rootState.promotions.promotionsAll[index].Drawings[id].TicketsUsed;
            rootState.promotions.promotionsAll[index].Drawings[id] = data;
            rootState.AvailableFreeEntryNumber = data.AvailableFreeEntryNumber;
            rootState.TicketsAvailable = data.AvailableTicketNumber;
            rootState.AwardTicketsUsed = data.TicketsUsed;
          }
        }
      });
    });
  },
  setTicketAutoRedeem({
    dispatch,
    state,
    commit,
    rootGetters,
    rootState
  }, params) {
    award.TicketAutoRedeemSet(rootState.currentSelectDrawingId, rootState.CardID, rootState.EgmLocation, params.autoRedeemSet, params.ticketsToExclude, rootGetters.guid).then((data) => {
      rootState.isFetching = false;
      if (data.ErrorCode != 0) {
        rootGetters.rootAnalytics.trackException("Error", {Error:data});
        dispatch('addInfo', {
          message: data.ErrorMessage,
          errorCode: data.ErrorCode
        }, {
          root: true
        });
        return;
      }
      state.awardAll.IsAutoRedeemSet = !state.awardAll.IsAutoRedeemSet;
    }).catch((err) => {
      rootState.isFetching = false;
      rootGetters.rootAnalytics.trackException("Error", {Error:err});
      dispatch('addInfo', {
        message: err.message
      }, {
        root: true
      });
    });
  },
  setPlayerDrawingCheckin({
    dispatch,
    state,
    commit,
    rootGetters,
    rootState
  }) {
    award.PlayerDrawingCheckin(rootState.currentSelectDrawingId, rootState.CardID, rootState.EgmLocation, rootGetters.guid).then((data) => {
      rootState.isFetching = false;
      if (data.ErrorCode != 0) {
        rootGetters.rootAnalytics.trackException("Error", {Error:data});
        if (data.ErrorCode == '1303') {
          state.awardAll.PlayerAlreadyCheckedIn = true;
        }
        dispatch('addInfo', {
          message: data.ErrorMessage,
          errorCode: data.ErrorCode
        }, {
          root: true
        });
        return;
      }
      state.awardAll.PlayerAlreadyCheckedIn = true;
    }).catch((err) => {
      rootState.isFetching = false;
      rootGetters.rootAnalytics.trackException("Error", {Error:err});
      dispatch('addInfo', {
        message: err.message
      }, {
        root: true
      });
    });
  }
};

const mutations = {
  setAward(state, award) {
    state.awardAll = award;
  }
};

export default {
  'namespaced': true,
  state,
  actions,
  mutations
};